import Warrior from '../class/warrior.js';
import Tactics from '../class/tactics.js';
import Count from './count.js';
import Report from './report.js';
import Statistics from './statistics.js';
import Util from './util.js';
import Query from './query.js';

import weiSArr from '../warrior/wei_s.js';
import weiAArr from '../warrior/wei_a.js';
import qunSArr from '../warrior/qun_s.js';
import shuSArr from '../warrior/shu_s.js';
import wuSArr from '../warrior/wu_s.js';
import qunBArr from '../warrior/qun_b.js';
import shuAArr from '../warrior/shu_a.js';
import wuAArr from '../warrior/wu_a.js';
import hanBArr from '../warrior/han_b.js';
import hanAArr from '../warrior/han_a.js';
import wuBArr from '../warrior/wu_b.js';
import weiBArr from '../warrior/wei_b.js';
import qunAArr from '../warrior/qun_a.js';
import hanSArr from '../warrior/han_s.js';

import AttrBlock from '../block/attr.js';
import shuBArr from '../warrior/shu_b.js';
import activeA from '../tactics/active_a.js';
import commandS from '../tactics/command_s.js';
import activeS from '../tactics/active_s.js';
import commandA from '../tactics/command_a.js'
import commandB from '../tactics/command_b.js'
import Manage from './manage.js';
import comboS from '../tactics/combo_s.js';
import comboA from '../tactics/combo_a.js';
import comboB from '../tactics/combo_b.js'
import jinSArr from '../warrior/jin_s.js';
import jinAArr from '../warrior/jin_a.js';
import jinBArr from '../warrior/jin_b.js';
import activeB from '../tactics/active_b.js';
import passiveA from '../tactics/passive_a.js';
import passiveS from '../tactics/passive_s.js';
import passiveB from '../tactics/passive_b.js';

import trammels from '../../data/trammels.json';
import DamageBlock from '../block/damage.js';
import Judge from './judge.js';
import StatusBlock from '../block/status.js';
import ExecuteBlock from '../block/execute.js';
import RangeBlock from '../block/range.js';
import CounterAttackBlock from '../block/counterAtk.js';
import DodgeBlock from '../block/dodge.js';
import ArmSpec from '../class/spec.js';
import ArtifactProcessor from '../class/artifact.js';
class Driver {
    constructor(global) {
        this.global = global;
        //
        this.version = global.version;
        //
        this.Team = [];
        //
        this.turn = -2;
        //
        this.EXECUTE = [];
        //
        this.ReportHeap = [];
        //
        this.Count = new Count();
        this.attrMap = {
            ad : "",
            de : "",
            tact : "",
            speed : "",
        }
    }
    //
    Main() {
        //
        this.Init();
        //
        this.PrepareBattle();
        //
        this.Execute();
        //
        this.PushReport();

    }
    //
    InitExternalAddition(target,teamIdx) {
        let transforName = {
            jifen:"",
            shangwu:"",
            junji:"",
            tiebi:"",
        }
        let attrMap = {
            jifen : "speed",
            shangwu : "ad",
            junji : "tact",
            tiebi : "de",
        }
        let st = this.global.setting.teamSettingList[teamIdx];
        //
        for(let key in st.fourMajorCampsLevel){
            target.attr[attrMap[key]] += st.fourMajorCampsLevel[key] * 2;
            let report = new Report("");
            report.InitExecuteReport(2, target, `${transforName[key]}${st.fourMajorCampsLevel[key]}${this.attrMap[attrMap[key]]}${st.fourMajorCampsLevel[key] * 2}(${target.attr[attrMap[key]]})`)
        }
        //
        if(st.barracksLevel > 10) target.hp +=  2000 + (st.barracksLevel-10)*300;
        else target.hp += st.barracksLevel*200;
        if(st.isUpHp) target.hp += 200 * target.red;
        target.hpMax = target.hp;
        //
        target.morale = st.defMorale;
        
    }
    //
    InitArmsAddition(target, targetArr) {
        let u = new Util();
        //
        let armType = u.TransferArms(target.upArms[target.choseUpArmsIdx]);
        let mul = 0;
        let number = 0;
        for (let k = 0; k < 3; k++) {
            let targetArmsType = u.TransferArms(targetArr[k].upArms[targetArr[k].choseUpArmsIdx]);
            if (targetArmsType == armType) {
                number++;
            }
        }
        //
        if (number > 1) {
            if (number == 2) mul = 0.055;
            else if (number == 3) mul = 0.1;
            let report = new Report("");
            switch (armType) {
                //
                case "b":
                    let addition_ad = Math.floor(this.Count.GetAttr(target, "") * mul);
                    let addition_de = Math.floor(this.Count.GetAttr(target, "") * mul);
                    target.attr.ad += addition_ad;
                    target.attr.de += addition_de;
                    report.InitExecuteReport(2, target, `${number}+${mul * 100}%(${addition_ad})+${mul * 100}%(${addition_de})`)
                    break;
                case "g":
                    let addition_speed = Math.floor(this.Count.GetAttr(target, "") * mul);
                    let addition_de1 = Math.floor(this.Count.GetAttr(target, "") * mul);
                    target.attr.speed += addition_speed;
                    target.attr.de += addition_de1;
                    report.InitExecuteReport(2, target, `${number}+${mul * 100}%(${addition_speed})+${mul * 100}%(${addition_de1})`)

                    break;
                case "q":
                    let addition_speed1 = Math.floor(this.Count.GetAttr(target, "") * mul);
                    let addition_ad1 = Math.floor(this.Count.GetAttr(target, "") * mul);
                    target.attr.speed += addition_speed1;
                    target.attr.ad += addition_ad1;
                    report.InitExecuteReport(2, target, `${number}+${mul * 100}%(${addition_speed1})+${mul * 100}%(${addition_ad1})`)
                    break;

            }

        }
    }
    //
    InitCampAddition(target, originAd, originDe, originTact, originSpeed) {
        let mul = 0;
        let count = 0;
        for (let i in target.ally) {
            if (target.ally[i].camp == target.camp) count++;
        }
        switch (count) {
            case 2: mul = 0.08; break;
            case 3: mul = 0.1; break;
        }
        target.attr.ad += Math.floor(originAd * mul);
        target.attr.de += Math.floor(originDe * mul);
        target.attr.tact += Math.floor(originTact * mul);
        target.attr.speed += Math.floor(originSpeed * mul);
        if (count > 1) {
            let report = new Report("");
            report.InitExecuteReport(2, target, `${count}${target.camp}${mul * 100}%`);
        }
    }
    //
    InitTrammelsAddition(target) {
        for (let i in trammels) {
            let count = 0;
            for (let k = 0; k < target.ally.length; k++) {
                for (let j = 0; j < trammels[i].member.length; j++) {
                    if (target.ally[k].name == trammels[i].member[j].name && target.ally[k].camp == trammels[i].member[j].camp) {
                        count += 1;
                        break;
                    }
                    if (trammels[i].member.length - 1 == j) {
                        k = target.ally.length;
                    }
                }
            }
            if (count == 3) {
                let str = ""
                for(let key in target.attr){
                    target.attr[key] += trammels[i].plus[key];
                    if(trammels[i].plus[key] > 0) str += `${this.attrMap[key]}+${trammels[i].plus[key]} ` 
                }
                let r = new Report("");
                r.InitExecuteReport(2,target,`${trammels[i].name}${str}`);
            }
        }
    }
    //
    OutputWarriorAttr() {
        let positinMap = {
            0:'',
            1:'',
            2:'',
        }
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                let ad = this.Count.GetAttr(this.Team[i][j], "");
                let de = this.Count.GetAttr(this.Team[i][j], "");
                let tact = this.Count.GetAttr(this.Team[i][j], "");
                let speed = this.Count.GetAttr(this.Team[i][j], "");
                let report1 = new Report();
                report1.InitExecuteReport(1, this.Team[i][j], `${positinMap[j]} (${this.Team[i][j].morale}) `);
                let report2 = new Report();
                report2.InitExecuteReport(2,this.Team[i][j],`(${ad}) (${de}) (${tact}) (${speed}) `)
            }
        }
    }
    //
    LoadTactics(target, data) {
        // -
        let campQualityMap = {
            '': { 'S': weiSArr, 'A': weiAArr, 'B': weiBArr },
            '': { 'S': qunSArr, 'A': qunAArr, 'B': qunBArr },
            '': { 'S': shuSArr, 'A': shuAArr, 'B': shuBArr },
            '': { 'S': wuSArr, 'A': wuAArr, 'B': wuBArr },
            '': { 'S': hanSArr, 'A': hanAArr, 'B': hanBArr },
            '': { 'S': jinSArr, 'A': jinAArr, 'B': jinBArr }
        };
        // -
        let tacticTypeMap = {
            'command': { 'S': commandS, 'A': commandA, 'B': commandB },
            'active': { 'S': activeS, 'A': activeA, 'B': activeB },
            'combo': { 'S': comboS, 'A': comboA, 'B': comboB },
            'passive': { 'S': passiveS, 'A': passiveA, 'B': passiveB }
        };
        let targetMainArr = campQualityMap[data.camp][data.tacticsArr[0].quality];

        //
        for (let i in targetMainArr) {
            if (targetMainArr[i].id == data.tacticsArr[0].id) {
                let t = new Tactics(target, targetMainArr[i]);
                target.TacticsArr.push(t);
                break;
            }
        }
        //
        for (let i = 0; i < data.tacticsArr.length; i++) {
            if (i != 0 && data.tacticsArr[i].id != -1) {
                let targetArr = tacticTypeMap[data.tacticsArr[i].type][data.tacticsArr[i].quality];
                for (let j in targetArr) {
                    if (targetArr[j].name == data.tacticsArr[i].name) {
                        let t = new Tactics(target, targetArr[j]);
                        target.TacticsArr.push(t);
                        break;
                    }
                }
            }
        }
    }
    //
    InitExtraSpecial(targetTeam,teamIdx) {
        let st = this.global.setting.teamSettingList[teamIdx];
        //
        for (let i = 0; i < 4; i++) {
            //
            if (i == 0 && st.fourMajorCampsLevel['jifen'] == 10) {
                for (let j in targetTeam) {
                    let skill = new Tactics(targetTeam[j])
                    skill.type = "";
                    skill.name = '';
                    let block = new AttrBlock(targetTeam[j], skill, '', 10, 1, false);
                    let work = block.Init(targetTeam[j], false, 0);
                    if(work){
                        let r = new Report("");
                        r.InitExecuteReport(1,targetTeam[j],`10`)
                    }
                }
            }
            //
            else if (i == 1 && st.fourMajorCampsLevel['tiebi'] == 10) {
                let rand = Math.floor(Math.random() * 3);
                let tgt = targetTeam[rand];
                let skill = new Tactics(tgt)
                skill.type = "";
                skill.name = '';
    
                let block = new DodgeBlock(tgt, skill, 1,120);
                block.Init(tgt, false);
                let r = new Report("");
                r.InitExecuteReport(1,tgt,`()`)
            }
            //
            else if (i == 2 && st.fourMajorCampsLevel['junji'] == 10) {
                let rand = Math.floor(Math.random() * 3);
                let tgt = targetTeam[rand];
                let skill = new Tactics(tgt)
                skill.type = "";
                skill.name = '';
                
                let block = new StatusBlock(tgt, skill, '', 1, 0);
                block.Init(tgt, false);
                let r = new Report("");
                r.InitExecuteReport(1,tgt,``)
            }
            //
            else if (i == 3 && st.fourMajorCampsLevel['shangwu'] == 10) {
                let rand = Math.floor(Math.random() * 3);
                let tgt = targetTeam[rand];
                let skill = new Tactics(tgt)
                skill.type = "";
                skill.name = "";
                
                let block = new StatusBlock(tgt, skill, '', 1, 0);
                block.Init(tgt, false);
                let r = new Report("");
                r.InitExecuteReport(1,tgt,``)
            }
        }
        //
        if(st.giantWarriorStatueLevel > 0){
            for(let i in targetTeam){
                let tgt = targetTeam[i];
                let skill = new Tactics(tgt);
                skill.type = "";
                skill.name = "";
                let value = st.giantWarriorStatueLevel * 4;
                let block = new DamageBlock(tgt,skill,"",false,-value,false,false,99,0);
                let work = block.Init(tgt,null,false);
                if(work){
                    let r = new Report("");
                    r.InitExecuteReport(1,tgt,`${value}%`)
                }
            }
        }
        if(st.tacticsFormationLevel > 0){
            for(let i in targetTeam){
                let tgt = targetTeam[i];
                let skill = new Tactics(tgt);
                skill.type = "";
                skill.name = "";
                let value = st.tacticsFormationLevel * 4;
                let block = new DamageBlock(tgt,skill,"",false,-value,false,false,99,0);
                let work = block.Init(tgt,null,false);
                if(work){
                    let r = new Report("");
                    r.InitExecuteReport(1,tgt,`${value}%`)
                }
            }
        }
    }
    //
    Init() {
        //
        this.global.ReportHeap = [];
        //
        let s = new Statistics(this.global);
        s.Init();
        let q = new Query();
        //
        for (let i = 0; i < this.global.teams.list.length; i++) {
            let empty_team = []
            for (let j in this.global.teams.list[i]) {
                if (1) {
                    let belong_str = i == 0 ? "blue" : "red";
                    let warrior = new Warrior(this.global.teams.list[i][j], j, belong_str, this.global, this);
                    this.LoadTactics(warrior, this.global.teams.list[i][j])
                    empty_team.push(warrior);
                }
            }
            this.Team.push(empty_team)
        }
        //
        for (let i in this.Team[0]) {
            this.Team[0][i].ally = this.Team[0];
            this.Team[0][i].enemy = this.Team[1];
        }
        for (let i in this.Team[1]) {
            this.Team[1][i].ally = this.Team[1];
            this.Team[1][i].enemy = this.Team[0];
        }
        // 
        let sp = new ArmSpec();
        let artP = new ArtifactProcessor()
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                let report = new Report("");
                report.InitExecuteReport(1, this.Team[i][j], "");
                let ad = q.GetAttr(this.Team[i][j], "");
                let de = q.GetAttr(this.Team[i][j], "");
                let tact = q.GetAttr(this.Team[i][j], "");
                let speed = q.GetAttr(this.Team[i][j], "");
                //
                this.InitArmsAddition(this.Team[i][j], this.Team[i]);
                //
                this.InitCampAddition(this.Team[i][j], ad, de, tact, speed);
                //
                this.InitExternalAddition(this.Team[i][j],i);
                //
                this.InitTrammelsAddition(this.Team[i][j]);
                //
                sp.Init(this.Team[i][j]);
                //
                artP.Init(this.Team[i][j]);
            }
        }
        // 
        for(let i in this.Team){
            this.InitExtraSpecial(this.Team[i],i);
        }
        //
        this.OutputWarriorAttr();
        //
        let passiveTactAvail = false;
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                for (let k in this.Team[i][j].TacticsArr) {
                    if (this.Team[i][j].TacticsArr[k].type == '') passiveTactAvail = true;
                }
            }
        }
        if(passiveTactAvail){
            let report = new Report();
            report.InitSysMsg(this.global.ReportHeap, "", ``);
        }
        // console.log(this.Team);
        //
        this.turn = -1;
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                for (let k in this.Team[i][j].TacticsArr) {
                    if (this.Team[i][j].TacticsArr[k].type == '') {
                        let report = new Report();
                        report.InitExecuteReport(1, this.Team[i][j], `${this.Team[i][j].TacticsArr[k].name}`)
                        this.Team[i][j].TacticsArr[k].execute(this.Team[i][j]);
                    }
                }
            }
        }
    }

    //,
    PrepareBattle() {
        let u = new Util();
        let r = new Report();
        this.turn = 0;
        r.InitSysMsg(this.global.ReportHeap, "", ``);
        let i = 1;
        while (i <= 6) {
            let executor = u.GetExecutor(this.Team);
            if (executor == null) {
                console.log('wrong')
                return;
            }
            //
            for (let j in executor.TacticsArr) {
                if (executor.TacticsArr[j].type == '') {
                    let report = new Report();
                    report.InitExecuteReport(1, executor, `${executor.TacticsArr[j].name}`)
                    executor.TacticsArr[j].execute(executor);
                }
            }
            executor.action = false;
            i++;
        }
        //
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                this.Team[i][j].action = true;
            }
        }
    }
    //8
    Execute() {
        let charMap = {
            1 : "",
            2 : "",
            3 : "",
            4 : "",
            5 : "",
            6 : "",
            7 : "",
            8 : "",
        }
        this.turn = 1;
        for (let i = 0; i < 8; i++) {
            let r = new Report();
            r.InitSysMsg(this.global.ReportHeap, "", `${charMap[i + 1]}`);
            let m = new Manage();
            m.ManageExecuteSysBlock(this);
            this.ExecuteOneTurn();
            if (!this.Team[0][0].alive) {
                let r = new Report();
                r.InitExecuteReport(1, this.Team[0][0], ``);
                break;
            }
            else if (!this.Team[1][0].alive) {
                let r = new Report();
                r.InitExecuteReport(1, this.Team[1][0], ``);
                break;
            }
            //
            this.turn += 1;
            //
            for (let i in this.Team) {
                for (let j in this.Team[i]) {
                    this.Team[i][j].action = true;
                }
            }
        }
        //
        this.global.battleResult = this.Team;
        //
        let s = new Statistics(this.global);
        s.BuildDataMatrices(this.Team, this);

        this.global.isShowBattleResult = true;
        let r = new Report();
        r.InitSysMsg(this.global.ReportHeap, "", ``);

    }
    //
    ExecuteOneTurn() {
        let u = new Util();
        let m = new Manage();
        //
        let j = 1;
        while (j <= 6) {
            let executor = u.GetExecutor(this.Team);
            if (executor == null) {
                break;
            }
            //
            m.ManageExecuteBlock(executor, '');
            executor.action = false;
            j++;
        }
        //
        for (let i in this.Team) {
            for (let j in this.Team[i]) {
                this.Team[i][j].action = true;
            }
        }
        //
        let i = 1;
        while (i <= 6) {
            let executor = u.GetExecutor(this.Team);
            if (executor == null) return;
            if (!this.Team[0][0].alive) {
                return;
            }
            else if (!this.Team[1][0].alive) {
                return;
            }
            //
            executor.Execute();
            i++;
        }
    }
    //
    PushReport() {
        let currentTime = new Date(); // Date
        let year = currentTime.getFullYear(); // 
        let month = (currentTime.getMonth() + 1).toString().padStart(2, '0'); // 1
        let day = currentTime.getDate().toString().padStart(2, '0'); // 
        let hours = currentTime.getHours().toString().padStart(2, '0'); // 
        let minutes = currentTime.getMinutes().toString().padStart(2, '0'); // 
        let seconds = currentTime.getSeconds().toString().padStart(2, '0'); // 

        // 
        let timeString = `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
        let s = new Statistics(this.global);
        let sta = s.CopyDataMatrices(this.global.Statistics);
        let item = {
            battleResult: this.global.battleResult,
            ReportHeap: this.global.ReportHeap,//
            //
            Statistics: sta,
            time: timeString,
        }
        this.global.ReportContainer.unshift(item);
        //50
        if (this.global.ReportContainer.length > 50) {
            this.global.ReportContainer.pop();
        }
    }





}

export default Driver;