        isControlMaker: false,
        isCureMaker: false,
        selectFri: false,
        selectSelf: true,
        selectEnemy: true,
        targetNumber: 1,
        arms: ['', '', ""],
        wait: 0,
        execute(self) {
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = q.GetAdditionValue(self, 170, '', 1.75);
            let cureValue = 150;
            let cureIndex = 1.2;

            let tag = '' + self.belong + self.position;
            let action = function () {
                self[tag] += 1;
                if (self[tag] >= 3) {
                    let r = new Report();
                    r.InitExecuteReport(2, self, ``);
                    let rand = Math.floor(Math.random() * 3);
                    if (rand == 1) {
                        let res = a.SelectTarget(self, 5, false, true, 1, false, true, that);
                        for (let i in res) {
                            let damage = c.CountDamage(self, self, damageValue, '', false, that, null);
                            a.GetHurt(self, res[i], damage, false, '', that, true, null);
                        }
                    }
                    else if (rand == 2) {
                        let r = new Report();
                        r.InitExecuteReport(3, self, ``);
                        for (let j = 0; j < 2; j++) {
                            let block = new DodgeBlock(self, that, 99);
                            block.Init(self, false);
                        }
                    }
                    else {
                        let res = a.SelectTarget(self, 3, true, false, 1, false, false, that);
                        res.push(self);
                        for (let i in res) {
                            let cure = c.CountCure(self, res[i], cureValue, cureIndex, that);
                            a.GetCure(self, res[i], cure, that, true, null);
                        }
                    }
                    self[tag] -= 3;
                }
            }
            let block1 = new ExecuteBlock(self, this, '', action, 99);
            block1.Init(self, false);
            let block2 = new ExecuteBlock(self, this, '', action, 99);
            block2.Init(self, false);
            self[tag] = 0;
            let r = new Report();
            r.InitExecuteReport(2, self, ``);
            this.STATISTICS.times += 1;
        }
