import Attack from "../js/attack";
import Count from "../js/count";
import StatusBlock from "../block/status";
import Query from "../js/query";
import DamageBlock from "../block/damage";
import Report from "../js/report";
import CureStatusBlock from "../block/cureStatus";
import OddBlock from "../block/odd";
import ExecuteBlock from "../block/execute";
import ActiveTacticsBlock from "../block/execute3";
import BeforeMakeDamageBlock from "../block/execute2";
import AttrBlock from "../block/attr";
import GetCureExecuteBlock from "../block/execute5";
import AfterDamageBlock from "../block/execute1";
let jinAArr = [
    //
    {
        id : 124,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let ad = q.GetAdditionValue(self,22,'',0.16);//
            let de = q.GetAdditionValue(self,22,'',0.16);//
            let tact = q.GetAdditionValue(self,22,'',0.16);//
            let maxTime = 4;//
            
            let tag_ad = '' + '' + self.belong + self.position;
            let tag_de = '' + '' + self.belong + self.position;
            let tag_tact = '' + '' + self.belong + self.position;
            let getDamage = function(){
                let target = this.beExecuted;
                if(target[tag_de] < maxTime){
                    let block = new AttrBlock(self,that,'',de,0,true);
                    let work = block.Init(target,false,3);
                    if(work){
                        target[tag_de] += 1;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${de * target[tag_de]}(${q.GetAttr(target,'')})`);
                    }
                }
            }
            let makeDamage = function(){
                let target = this.beExecuted;
                if(this.damageType == '' && target[tag_ad] < maxTime){
                    let block = new AttrBlock(self,that,'',ad,0,true);
                    let work = block.Init(target,false,3);
                    if(work){
                        target[tag_ad] += 1;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${ad * target[tag_ad]}(${q.GetAttr(target,'')})`);
                    }
                }
                else if(this.damageType == '' && target[tag_tact] < maxTime){
                    let block = new AttrBlock(self,that,'',tact,0,true);
                    let work = block.Init(target,false,3);
                    if(work){
                        target[tag_tact] += 1;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${tact * target[tag_tact]}(${q.GetAttr(target,'')})`);
                    }
                }
            }
            let clear = function(){
                let tgt = this.beExecuted;
                tgt[tag_ad] = 0;
                tgt[tag_de] = 0;
                tgt[tag_tact] = 0;
            }
            for(let i in self.ally){
                let target = self.ally[i];
                //
                let block1 = new BeforeMakeDamageBlock(self,this,getDamage,99,false);
                block1.Init(target);
                //
                let block2 = new BeforeMakeDamageBlock(self,this,makeDamage,99,true);
                block2.Init(target);
                let block = new ExecuteBlock(self,this,'',clear,99);
                block.Init(target,false);
                //
                target[tag_ad] = 0;
                target[tag_de] = 0;
                target[tag_tact] = 0;
                let r = new Report();
                r.InitExecuteReport(2,target,``);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 130,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : false,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let odd = 75;//
            let cureValue = 65;
            let cureIndex = 0.6;
            let damageValue = q.GetAdditionValue(self,180,'',1.75);
            if(self.driver.version == ''){
                cureValue = 80;
                cureIndex = 0.7;
            }
            
            let getCure = function(){
                if(this.cureTactics == that) return;
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let res = a.SelectTarget(self,5,true,false,2,false,true,that);
                    for(let i in res){
                        let cure = c.CountCure(self,res[i],cureValue,cureIndex,that);
                        let work = a.GetCure(self,res[i],cure,that,false);
                        if(work){
                            let r = new Report();
                            r.InitTwoTargetReport(3,self,res[i],``,`${cure}(${res[i].hp})`)
                        }
                    }
                }
            }
            let getHurt = function(){
                if(this.damageType == ''){
                    let target = this.damageFromWho;
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let damage = c.CountDamage(self,target,damageValue,'',false,that,null);
                        let report = new Report().InitTwoTargetReport(3,self,target,``,`${damage}(${target.hp - damage})`)
                        a.GetHurt(self,target,damage,false,'',that,false,report,false);
                    }
                }
            }
            let block = new GetCureExecuteBlock(self,this,getCure,99);
            block.Init(self);
            let block1 = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
            block1.Init(self,'')
            this.STATISTICS.times += 1;
        }
    },
]

export default jinAArr;