import AttrBlock from "../block/attr";
import DamageBlock from "../block/damage";
import DamageStatus from "../block/damageStatus";
import ActiveTacticsBlock from "../block/execute3";
import StatusBlock from "../block/status";
import Attack from "../js/attack";
import Count from "../js/count";
import Query from "../js/query";
import Report from "../js/report";
import Util from "../js/util";
import ExecuteBlock from '../block/execute.js';
import DodgeBlock from '../block/dodge.js'
import BeforeMakeDamageBlock from "../block/execute2.js";
import AfterDamageBlock from "../block/execute1.js";

let weiBArr = [
    //
    {
        id : 13,
        name : "",
        type : "",
        odd : 40,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let value = 15 + Math.floor((q.GetAttr(self,"")-80)*0.12);

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,2,true,true,this,"");
            for(let i in res){
                //
                let block1 = new DamageBlock(self,this,"",true,value,false,false,2,0);
                block1.Init(res[i],null,true);
                let block2 = new DamageBlock(self,this,"",true,value,false,false,2,0);
                block2.Init(res[i],null,true);
            }
            //
            for(let i in res){
                let block1 = new StatusBlock(self,this,'',2,0);
                block1.Init(res[i],true,3);
                let block2 = new StatusBlock(self,this,"",2,0);
                block2.Init(res[i],true);
            }


            this.STATISTICS.times += 1;

        }
    },
    //
    {
        id : 18,
        name : "",
        type : "",
        odd : 35,
        range : 3,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageIndex = 125;//
            let cureValue = 200;//
            
            //
            let cure = c.CountCure(self,self,cureValue,0,this);
            a.GetCure(self,self,cure,this);
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageIndex,"",false,this);
                a.GetHurt(self,res[i],damage,false,"",this);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 32,
        name : "",
        type : "",
        odd : 50,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self,target){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageValue = 180;//
            let cureValue = 0.5;//
            if(self.driver.version == ''){
                damageValue = 200;
            }

            //
            let damage = c.CountDamage(self,target,damageValue,"",false,this,null);
            a.GetHurt(self,target,damage,false,"",this,true);
            
            //
            let cure = damage * cureValue;
            //80%
            let cure_max = (q.GetMaxHp(self) - self.hp) * 0.8;
            if(cure > cure_max){
                //
                cure = cure_max;
            }
            cure = Math.floor(cure);
            a.GetCure(self,self,cure,this,true);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 33,
        name : "",
        type : "",
        odd : 120,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let value = 70 + Math.floor((q.GetAttr(self,"")-80)*0.06);

            let block = new DamageBlock(self,this,"",true,value,true,true,0);
            block.Init(self,null,true);
            this.STATISTICS.times += 1;

        }
    },
    //
    {
        id : 34,
        name : "",
        type : "",
        odd : 35,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageValue = 150;//
            let damageValue2 = 80;//
            let value = 10;//

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(2,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true);
            }
            //
            for(let i in res){
                let block = new DamageStatus(self,this,"",damageValue2,"",1,0,true);
                block.Init(res[i],true);
            }
            for(let i in res){
                let realValue = value;
                if(res[i].morale < value) realValue = res[i].morale;
                res[i].morale -= realValue;
                let r = new Report();
                r.InitExecuteReport(3,res[i],`${realValue}(${res[i].morale})`)
            }


            this.STATISTICS.times += 1;
        }
    },
    //&
    {
        id : 54,
        name : "",
        type : "",
        odd : 100,
        range : 6,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let n = 2;//n
            let value = 22 + Math.floor((q.GetAttr(self,"")-80)*0.18);//n
            let adValue = 11;//
            let tactValue = 13;//
            let decline_value = 7 + Math.floor((q.GetAttr(self,"")-80)*0.02);//
            if(self.driver.version == ''){
                n = 3;
            }

            let fri = a.SelectTarget(self,6,true,false,3,true,false,this,"");
            let enemy = a.SelectTarget(self,6,false,true,3,false,false,this,"");
            let res = [self.ally[1],self.ally[2]];
            //
            for(let i in res){
                for(let j=0;j<n;j++){
                    let block = new DamageBlock(self,this,"",false,-value,true,false,4,0);
                    block.Init(res[i],null,false);
                }
                let r = new Report();
                r.InitExecuteReport(2,res[i],`${n}${value}%`);
            }
            //
            let action1 = function(){
                let target = this.beExecuted;
                for(let i in target.ATTR){
                    if(target.ATTR[i].tactics == that && target.ATTR[i].attrType == ''){
                        target.ATTR[i].value += adValue;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${target.ATTR[i].value}(${q.GetAttr(target,'')})`);
                    }
                    else if(target.ATTR[i].tactics == that && target.ATTR[i].attrType == ''){
                        target.ATTR[i].value += tactValue;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${target.ATTR[i].value}(${q.GetAttr(target,'')})`);
                    }
                }
            }
            //
            let action2 = function(){
                let target = this.beExecuted;
                //
                for(let i in target.ATTR){
                    let realValue = decline_value;
                    if(target.ATTR[i].tactics == that && target.ATTR[i].attrType == ''){
                        //
                        let targetDe = q.GetAttr(target,'');
                        if(targetDe < realValue) realValue = targetDe;
                        target.ATTR[i].value -= realValue;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${Math.abs(target.ATTR[i].value)}(${q.GetAttr(target,'')})`);
                    }
                    else if(target.ATTR[i].tactics == that && target.ATTR[i].attrType == ''){
                        //
                        let targetSpeed = q.GetAttr(target,'');
                        if(targetSpeed < realValue) realValue = targetSpeed;
                        target.ATTR[i].value -= realValue;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,``,`${Math.abs(target.ATTR[i].value)}(${q.GetAttr(target,'')})`);
                    }
                }
            }
            for(let i in fri){
                let block = new ActiveTacticsBlock(self,this,action1,99,0,true);
                let work =  block.Init(fri[i],false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,fri[i],``);
                    let block1 = new AttrBlock(self,this,'',0,99);
                    block1.Init(fri[i],false);
                    let block2 = new AttrBlock(self,this,'',0,99);
                    block2.Init(fri[i],false);
                }
            }
            for(let i in enemy){
                let block = new ActiveTacticsBlock(self,this,action2,99,0,true);
                let work =  block.Init(enemy[i],false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,enemy[i],``);
                    let block1 = new AttrBlock(self,this,'',0,99);
                    block1.Init(enemy[i],false);
                    let block2 = new AttrBlock(self,this,'',0,99);
                    block2.Init(enemy[i],false);
                }
            }



            this.STATISTICS.times += 1;

        }
    },
    //
    {
        id : 79,
        name : "",
        type : "",
        odd : 40,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let u = new Util();
            let odd = 50;//

            //
            let most = self;
            let lest = self;
            for(let i in self.ally){
                if(self.ally[i].hp < lest.hp) lest = self.ally[i];
                else if(self.ally.hp == lest.hp){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1) lest = self.ally[i];
                }
                if(self.ally[i].hp > most.hp) most = self.ally[i];
                else if(self.ally[i].hp == most.hp){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1) most = self.ally[i];
                }
            }
            //
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,3,true,true,this,"");
            for(let i in res){
                let count = 0;
                count += u.ClearWhichTypeTacticsInitStatus(self,res[i],'',false,this,true,1,3);
                count += u.ClearWhichTypeTacticsInitStatus(self,res[i],'',false,this,true,1,3);
                if(count == 0){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
            }
            //
            let action = function(){
                let realOdd = q.GetMoraleOdd(most,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let block1 = new StatusBlock(self,that,'',1,0);
                    let work1 =  block1.Init(most,false);
                    if(work1){
                        let r = new Report();
                        r.InitTwoTargetReport(2,self,most,``,``);
                    }
                    let block2 = new StatusBlock(self,that,'',1,0);
                    let work2 =  block2.Init(most,false);
                    if(work2){
                        let r = new Report();
                        r.InitTwoTargetReport(2,self,most,``,``);
                    }
                }
            }
            //
            let work1 = true;
            for(let i in most.EXECUTE){
                if(most.EXECUTE[i].tactics == this){
                    let r = new Report();
                    r.InitExecuteReport(3,most,'');
                    work1 = false;
                    break;
                }
            }
            if(work1){
                let block = new ExecuteBlock(self,this,'',action,2);
                block.Init(most,false);
                let r = new Report();
                r.InitExecuteReport(3,most,``)
            }
            //
            let dodgeFunction = function(){
                let realOdd = q.GetMoraleOdd(lest,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let block = new DodgeBlock(self,this,99);
                    let work =  block.Init(lest,false);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,lest,``,``);
                    }
                }
            }
            let work2 = true;
            for(let i in lest.BEFORE_MAKE_DAMAGE){
                if(lest.BEFORE_MAKE_DAMAGE[i].tactics == this){
                    let r = new Report();
                    r.InitExecuteReport(3,most,'');
                    work2 = false;
                    break;
                }
            }
            if(work2){
                let block = new BeforeMakeDamageBlock(self,this,dodgeFunction,2,false);
                block.Init(lest);
                let r = new Report();
                r.InitExecuteReport(3,lest,``)
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 80,
        name : "",
        type : "",
        odd : 35,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let index = q.GetAdditionValue(self,37,'',0.06);//
            let damageValue = 300;//
            
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,3,false,true,this,"");
            if(res == []) return;
            let target = res[0];
            let maxAd = q.GetAttr(target,'');
            for(let i in res){
                let ad = q.GetAttr(res[i],'');
                if(ad > maxAd){
                    target = res[i];
                    maxAd = ad;
                }
                else if(ad == maxAd){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1){
                        target = res[i];
                        maxAd = ad; 
                    }
                }
            }
            for(let i=0;i<3;i++){
                let attrType = "";
                let value = 0;
                switch(i){
                    case 0 : attrType = '';value = Math.floor(q.GetAttr(target,'') * index/100);break;
                    case 1 : attrType = '';value = Math.floor(q.GetAttr(target,'') * index/100);break;
                    case 2 : attrType = '';value = Math.floor(q.GetAttr(target,'') * index/100);break;
                }
                let block = new AttrBlock(self,this,attrType,-value,2);
                let work =  block.Init(target,false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(3,target,`${attrType}${index}%(${value})(${q.GetAttr(target,attrType)})`)
                }
            }
            let damage = c.CountDamage(self,target,damageValue,'',false,this,null);
            a.GetHurt(self,target,damage,false,'',this,true,null);

            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 101,
        name : "",
        type : "",
        odd : 100,
        range : 4,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let value = q.GetAdditionValue(self,40,'',0.14);
            let odd = 60;

            let action = function(){
                let target = this.beExecuted;
                if(this.damageType == ''){
                    //
                    if(self[`${self.position}`] == 1){
                        self[`${self.position}`] = 0;
                        let damage = Math.floor(0.5*this.damageValue);
                        target.hp += damage;
                        let r = new Report().InitTwoTargetReport(3,self,target,``,`${damage}(${self.hp-damage})`);
                        a.GetHurt(this.damageFromWho,self,damage,this.isCommonAttack,this.damageType,this.damageFromTactics,false,r);
                        
                    }
                    for(let i in target.DAMAGE_VAR){
                        if(target.DAMAGE_VAR[i].tactics == that && target.DAMAGE_VAR[i][`${self.position}`] > 0){
                            target.DAMAGE_VAR[i].value += Math.floor(value/8);
                            let r = new Report();
                            r.InitTwoTargetReport(3,self,target,``,`${Math.abs(target.DAMAGE_VAR[i].value)}%`);
                            target.DAMAGE_VAR[i][`${self.position}`]--;
                            if(target.DAMAGE_VAR[i][`${self.position}`] == 0){
                                let r = new Report();
                                r.InitExecuteReport(3,target,``)
                            }
                        }
                    }
                }       
            }
            for(let i in self.ally){
                let target = self.ally[i];
                let block = new DamageBlock(self,this,'',false,-value,false,false,99,0);
                block[`${self.position}`] = 8;
                block.Init(target,null,true,2);
                let block2 = new AfterDamageBlock(self,this,action,99,true,true,true,true,true,true,true);
                block2.Init(target,'')
            }
            //
            let divide = function(){
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    self[`${self.position}`] = 1;
                    let r = new Report();
                    r.InitTwoTargetReport(2,self,self,``,``)
                }
                else{
                    let r = new Report();
                    r.InitExecuteReport(2,self,`${realOdd}%`);
                }
            }
            let block = new ExecuteBlock(self,this,'',divide,99);
            block.Init(self,false);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 103,
        name : "",
        type : "",
        odd : 100,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let value = q.GetAdditionValue(self,10,'',0.08);//
            let time = 3;//
            
            let tag = '' + self.belong + self.position;
            let action = function(){
                //
                if(self[tag]){
                    let r = new Report();
                    r.InitExecuteReport(3,self,``);
                    let res = a.SelectTarget(self,2,true,false,2,true,true,that,"");
                    for(let i in res){
                        //
                        let work = true;
                        for(let j in res[i].DAMAGE_VAR){
                            if(res[i].DAMAGE_VAR[j].tactics == that && res[i].DAMAGE_VAR[j][tag] < time){
                                res[i].DAMAGE_VAR[j].value += value;
                                res[i].DAMAGE_VAR[j][tag]++;
                                let r = new Report();
                                r.InitExecuteReport(3,res[i],`${res[i].DAMAGE_VAR[j].value}%`);
                                work = false;
                                break;
                            }
                        }
                        if(work){
                            let block = new DamageBlock(self,that,'',true,value,false,false,99,0);
                            block[tag] = 1;
                            block.Init(res[i],null,true,3);
                        }
                    }
                    self[tag] = false;
                }
            }
            let block = new ActiveTacticsBlock(self,this,action,99,0,true,'');
            block.Init(self,false);
            self[tag] = true;
            let action1 = function(){
                self[tag] = true;
            }
            let block1 = new ExecuteBlock(self,this,'',action1,99);
            block1.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);

            this.STATISTICS.times += 1;
        }
    },
    //sp
    {
        id : 147,
        name : "",
        type : "",
        odd : 120,
        range : 3,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            //
            let damageValue = 140;


            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,'',false,this,null);
                a.GetHurt(self,res[i],damage,false,'',this,true,null);
            }
            this.STATISTICS.times += 1;

        }
    },
]

export default weiBArr;