        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let value = q.GetAdditionValue(self,22,'',0.09);
            let damageValue = q.GetAdditionValue(self,140,'',1.375);;

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(2,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            let action = function(){
                let target = this.beExecuted;
                //
                let avail = false;
                for(let i in target.SPECIAL_STATUS){
                    if(target.SPECIAL_STATUS[i].tactics == that){
                        avail = true; break;
                    }
                }
                if(avail){
                    let damage = c.CountDamage(self,target,damageValue,"",false,this.tactics,"");
                    a.GetHurt(self,target,damage,false,"",this.tactics,false);
                    let r = new Report();
                    r.InitTwoTargetReport(2,self,target,``,`${damage}(${target.hp})`);
                }
                else{
                    //
                    let temp = [];
                    for(let i in target.EXECUTE){
                        if(target.EXECUTE[i].tactics != that) temp.push(target.EXECUTE[i]);
                    }
                    target.EXECUTE = temp;
                }
            }
            for(let i in res){
                //
                for(let j=0;j<2;j++){
                    let attrType = "";
                    switch(j){
                        case 0 : attrType = "";break;
                        case 1 : attrType = '';break;
                    }
                    let block = new AttrBlock(self,this,attrType,-value,2);
                    block.Init(res[i]);
                }
                //
                let block = new DamageStatus(self,this,'',damageValue,"",2,0,false);
                let flag = block.Init(res[i],true);
                if(flag){
                    let block = new ExecuteBlock(self,this,"",action,2);
                    block.Init(res[i]);
                }
            }


            this.STATISTICS.times += 1;
        }
