            fri.push(self);
            for(let i in fri){
                for(let j in fri[i].TacticsArr){
                    if(fri[i].TacticsArr[j].type == '' && fri[i].TacticsArr[j].isDamageMaker && fri[i].TacticsArr[j].targetNumber == 1){
                        let block = new DamageBlock(self,this,'',true,value,false,false,2,0);
                        block.Init(fri[i],fri[i].TacticsArr[j],true,3);
                    }
                }
            }
