import CounterAttackBlock from "../block/counterAtk";
import ExecuteBlock from "../block/execute";
import StatusBlock from "../block/status";
import Attack from "../js/attack";
import Count from "../js/count";
import Query from "../js/query";
import Report from "../js/report";
import Util from "../js/util";
import DamageStatus from '../block/damageStatus.js'
import DamageBlock from '../block/damage.js'
import AfterDamageBlock from "../block/execute1.js";
import AttrBlock from "../block/attr.js";
let wuBArr = [
    //
    {
        id : 17,
        name : "",
        type : "",
        odd : 100,
        range : 6,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let c = new Count();
            let a = new Attack();
            let u = new Util();
            let cureValue = 10000;//
            let cureIndex = 0;
            let damageValue = 85;

            let target = self.ally[2];
            if(u.TransferArms(target.up_arms[target.choseArms]) == 'q'){
                let r = new Report();
                r.InitExecuteReport(2,target,``);
                this.STATISTICS.times += 1;
                return false;
            }
            
            
            let r1 = new Report();
            r1.InitExecuteReport(2,target,``);
            //
            let block1 = new CounterAttackBlock(self,that,"",damageValue,100,99,3);
            let avail = block1.Init(target,false);
            let r2 = new Report();
            r2.InitExecuteReport(2,target,`()`);

            let action = function(){
                if(self.driver.turn == 3){
                    let cure = c.CountCure(self,target,cureValue,cureIndex,that);
                    let work = a.GetCure(self,target,cure,that,false);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(2,self,target,'',`${cure}(${target.hp})`);
                    }
                    //
                    let block2 = new StatusBlock(self,that,"",99);
                    let work1 =  block2.Init(target,false);
                    if(work1){
                        let r = new Report();
                        r.InitExecuteReport(2,target,``)
                    }
                    if(avail){
                        let r = new Report();
                        r.InitExecuteReport(2,target,``)
                    }
                }
            }
            let block = new ExecuteBlock(self,this,"",action,3);
            block.Init(target);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 28,
        name : "",
        type : "",
        odd : 40,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let c = new Count();
            let a = new Attack();
            let q = new Query();
            let damageValue = q.GetAdditionValue(self,114,"",0.95);//
            let damageValue2 = q.GetAdditionValue(self,114,"",0.95);//
            let index = q.GetAdditionValue(self,16,"",0.06);//

            let realRang = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRang,false,true,realTargetNumber,false,true,this);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,"");
                a.GetHurt(self,res[i],damage,false,"",this,true);
                let status = new DamageStatus(self,this,"",damageValue2,"",1,0);
                status.Init(res[i]);
                let block = new DamageBlock(self,this,"",false,index,false,false,2);
                block.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 29,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 1,
        execute(self){
            let c = new Count();
            let a = new Attack();
            let u = new Util();
            let q = new Query();
            let damageValue = 160;//
            let damageValue2 = 140;//

            let realRang = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(3,self,this);
            let res = a.SelectTarget(self,realRang,false,true,realTargetNumber,false,true,this);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true);
            }
            let r = new Report();
            r.InitExecuteReport(3,self,``);
            let rand = Math.floor(Math.random()*2);
            let res2 = a.SelectTarget(self,realRang,false,true,rand + 1,false,true,this);
            for(let i in res2){
                let damage = c.CountDamage(self,res2[i],damageValue2,"",false,this,null);
                a.GetHurt(self,res2[i],damage,false,"",this,true);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 53,
        name : "",
        type : "",
        odd : 100,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let value = 60;

            let realRang = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRang,true,false,3,true,false,this);
            let action1 = function(){
                if(this.damageType == ''){
                    let target = this.beExecuted;
                    for(let i in target.DAMAGE_VAR){
                        if(target.DAMAGE_VAR[i].tactics == that && target.DAMAGE_VAR[i].damageType == '' && target.DAMAGE_VAR[i].value < 0){
                            let index = value/5;
                            target.DAMAGE_VAR[i].value += index;
                            if(target.DAMAGE_VAR[i].value == 0){
                                let temp = [];
                                for(let j in target.AFTER_DAMAGE){
                                    if(target.AFTER_DAMAGE[j] != this) temp.push(target.AFTER_DAMAGE[j]);
                                    else{
                                        let r = new Report();
                                        r.InitTwoTargetReport(3,self,target,``,``);
                                    }
                                }
                                target.AFTER_DAMAGE = temp;
                            }
                            else{
                                let r = new Report();
                                r.InitTwoTargetReport(3,self,target,``,`${Math.abs(target.DAMAGE_VAR[i].value)}%`);
                            }
                            break;
                        }
                    }
                }
            }
            let action2 = function(){
                if(this.damageType == ''){
                    let target = this.beExecuted;
                    for(let i in target.DAMAGE_VAR){
                        if(target.DAMAGE_VAR[i].tactics == that && target.DAMAGE_VAR[i].damageType == '' && target.DAMAGE_VAR[i].value < 0){
                            let index = value/5;
                            target.DAMAGE_VAR[i].value += index;
                            if(target.DAMAGE_VAR[i].value == 0){
                                let temp = [];
                                for(let j in target.AFTER_DAMAGE){
                                    if(target.AFTER_DAMAGE[j] != this) temp.push(target.AFTER_DAMAGE[j]);
                                    else{
                                        let r = new Report();
                                        r.InitTwoTargetReport(3,self,target,``,``);
                                    }
                                }
                                target.AFTER_DAMAGE = temp;
                            }
                            else{
                                let r = new Report();
                                r.InitTwoTargetReport(3,self,target,``,`${Math.abs(target.DAMAGE_VAR[i].value)}%`);
                            }
                            break;
                        }
                    }
                }
            }
            for(let i in res){
                let block1 = new DamageBlock(self,this,'',false,-value,false,false,99,0);
                let work1 =  block1.Init(res[i],null,false);
                if(work1){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${value}%`);
                    let block = new AfterDamageBlock(self,this,action1,99,true,true,true,true,true,true,true);
                    block.Init(res[i],"1");
                }
                let block2 = new DamageBlock(self,this,'',false,-value,false,false,99,0);
                let work2 =  block2.Init(res[i],null,false);
                if(work2){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${value}%`);
                    let block = new AfterDamageBlock(self,this,action2,99,true,true,true,true,true,true,true);
                    block.Init(res[i],"2");
                }
            }
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 108,
        name : "",
        type : "",
        odd : 40,
        range : 4,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let c = new Count();
            let a = new Attack();
            let q = new Query();
            let value1 = q.GetAdditionValue(self,20,'',0.1);
            let value2 = q.GetAdditionValue(self,30,'',0.22);
            
            let realRang = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRang,true,false,2,true,true,this);
            //
            let minAd = 100000;
            let minAdTarget = null;
            for(let i in self.ally){
                if(self.ally[i] != self && self.ally[i].alive){
                    let ad = q.GetAttr(self.ally[i],'');
                    if(ad < minAd){
                        minAd = ad;
                        minAdTarget = self.ally[i];
                    }
                    else if(ad == minAd){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1){
                            minAd = ad;
                            minAdTarget = self.ally[i];
                        }
                    }
                }
            }
            let fri = [self];
            if(minAdTarget != null) fri.push(minAdTarget);
            for(let i in fri){
                let block = new StatusBlock(this.execute,this,'',1,0);
                block.Init(fri[i],true,3);
            }
            for(let i in res){
                let block = new DamageBlock(self,this,'',false,-value1,false,false,2,0);
                block.Init(res[i],null,)
                for(let j=0;j<2;j++){
                    let block = new DamageBlock(self,this,'',true,value2,true,false,99,0);
                    block.Init(res[i],null,false,3);
                }
                let r = new Report();
                r.InitExecuteReport(3,res[i],`${value2}%`);
            }
            this.STATISTICS.times += 1;
        }
    },
    //sp
    {
        id : 149,
        name : "",
        type : "",
        odd : 35,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self,target){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = 210 + Math.floor(Math.random()*100);//
            let value = q.GetAttr(self,'') * 0.5;
            //
            let damage = c.CountDamage(self,target,damageValue,"",false,this,null);
            a.GetHurt(self,target,damage,false,"",this,true);
            
            let block = new StatusBlock(self,this,"",2,0);
            block.Init(target,true);
            //50%
            let block1 = new AttrBlock(self,this,"",-value,0);
            block1.Init(self,true);
            this.STATISTICS.times += 1;
        }
    },
]

export default wuBArr;