import { createApp } from 'vue'
import App from './App.vue'
import router from './router'  // 
import { createPinia } from 'pinia' //  pinia
import piniaPlugin from './store/pinia.js'


const pinia = createPinia()
const app = createApp(App)
    
app.use(pinia)  //  Pinia
app.use(piniaPlugin)  // 
app.use(router)  // 
app.mount('#app')
