import RangeBlock from "../block/range";
import Util from '../js/util.js';
import BeforeMakeDamageBlock from '../block/execute2.js';
import CureStatusBlock from '../block/cureStatus.js';
let passiveB = [
    //
    {
        id : 10,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let odd = 90;//

            let action = function(){
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let block = new StatusBlock(self,that,'',0,0);
                    block.Init(self,false);
                    let r = new Report();
                    r.InitTwoTargetReport(2,self,self,``,``);
                    let block1 = new StatusBlock(self,that,'',0,0);
                    block1.Init(self,false,2);
                }
                else{
                    let r = new Report();
                    r.InitTwoTargetReport(2,self,self,``,`${realOdd}%`);
                }
            }
            let block = new ExecuteBlock(self,this,'',action,4);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            //
            if(self.name == ''){
                for(let i=0;i<4;i++){
                    let attrType = "";
                    let tgtAttr = 0;
                    switch(i){
                        case 0 : attrType = ''; tgtAttr = self.attr.ad; break;
                        case 1 : attrType = ''; tgtAttr = self.attr.de; break;
                        case 2 : attrType = ''; tgtAttr = self.attr.tact; break;
                        case 3 : attrType = ''; tgtAttr = self.attr.speed; break;
                    }
                    let value = Math.floor(0.1 * tgtAttr);
                    let block = new AttrBlock(self,this,attrType,value,99,false);
                    block.Init(self,true,2);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 12,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let odd = 100;//
            let value = 55;

            let action  = function(){
                let r = new Report();
                r.InitExecuteReport(3,self,``);
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let res = a.SelectTarget(self,2,true,false,2,true,true,that);
                    for(let j in res){
                        for(let i=0;i<3;i++){
                            let attrType = "";
                            switch(i){
                                case 0 : attrType = ''; break;
                                case 1 : attrType = ''; break;
                                case 2 : attrType = ''; break;
                            }
                            let block = new AttrBlock(self,that,attrType,value,2,false);
                            block.Init(res[j],true,3);
                        }
                    }
                }
                if(this.triggerTactics.wait > 0){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let block = new StatusBlock(self,that,'',2,0);
                        block.Init(self,true,3);
                    }
                }
            }
            let block = new ActiveTacticsBlock(self,this,action,99,0,true,'');
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 13,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let c = new Count();
            let a = new Attack();
            let cureValue = 260;
            let cureIndex = 0;

            let action = function(){
                if(self.driver.turn %2 == 0){
                    let cure = c.CountCure(self,self,cureValue,cureIndex,that);
                    let r = new Report().InitTwoTargetReport(2,self,self,``,`${cure}(${self.hp})`,false);
                    a.GetCure(self,self,cure,that,true,r);
                }
            }
            let block = new StatusBlock(self,this,'',99,0);
            block.Init(self,true,2);
            let block1 = new ExecuteBlock(self,this,'',action,99);
            block1.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 14,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let value = 32;
            let odd = 50;//
            let value1 = 1;//

            let block = new RangeBlock(self,this,true,value1,99,0);
            block.Init(self,true,2);
            let getHurt = function(){
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let tgt = this.attacker;
                    let block = new DamageBlock(self,that,'',true,-value,true,false,0,0);
                    let work = block.Init(tgt,null,false);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,tgt,``,`${value}%`);
                    }
                }
            }
            let block1 = new BeforeMakeDamageBlock(self,this,getHurt,99,false);
            block1.Init(self);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 15,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let value = 32;

            for(let i=0;i<4;i++){
                let attrType = "";
                switch(i){
                    case 0 : attrType = '';break;
                    case 1 : attrType = '';break;
                    case 2 : attrType = '';break;
                    case 3 : attrType = '';break;
                }
                let block = new AttrBlock(self,this,attrType,value,99,false);
                block.Init(self,true,2);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 16,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let damageValue = 60;//

            let block1 = new CounterAttackBlock(self,this,'',damageValue,100,99,0);
            block1.Init(self,true,2);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 17,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let cureValue = 150;
            let cureIndex = 0;

            let action  = function(){
                //
                let r = new Report();
                r.InitExecuteReport(2,self,``);
                let cure = c.CountCure(self,self,cureValue,cureIndex,that);
                a.GetCure(self,self,cure,that,true,null);
            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
]

export default passiveB;