import DamageBlock from "../block/damage";
import ExecuteBlock from "../block/execute";
import AfterDamageBlock from "../block/execute1";
import AttrBlock from '../block/attr.js'
import StatusBlock from "../block/status";
import Attack from "../js/attack";
import Count from "../js/count";
import Judge from "../js/judge";
import Query from "../js/query";
import Report from "../js/report";
import AttrAppliedExecuteBlock from "../block/execute_attr_applied";
import MakeDamageBlock from "../block/execute6";

let hanSArr = [
    //
    {
        id: 24,
        name: "",
        type: "",
        odd: 35,
        range: 5,
        isDamageMaker: true,
        damageMakeType: [""],
        isControlMaker: true,
        isCureMaker: false,
        selectFri: false,
        selectSelf: false,
        selectEnemy: true,
        targetNumber: 1,
        arms: [''],
        wait: 0,
        execute(self) {
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let j = new Judge();
            let damageValue = 275;

            let realRange = q.GetTacticsRange(self, this);
            let allTargets = a.SelectTarget(self, realRange, false, true, 3, false, true, this,"");
            let res = [];
            for (let i in allTargets) {
                if (j.IsGetStatus(allTargets[i], "") || j.IsGetStatus(allTargets[i], "")) {
                    res.push(allTargets[i]);
                }
            }
            //
            if (res.length > 0) {
                let target = res[Math.floor(Math.random() * res.length)];
                let r = new Report();
                r.InitExecuteReport(3, self, ``)
                let damage = c.CountDamage(self, target, damageValue, "", false, this);
                a.GetHurt(self, target, damage, false, "", this, true);
            }

            let target = a.SelectTarget(self, realRange, false, true, 1, false, true, this,"");
            for (let i in target) {
                let damage = c.CountDamage(self, target[i], damageValue, "", false, this);
                a.GetHurt(self, target[i], damage, false, "", this, true);
                let block = new StatusBlock(self, this, "", 2, 0);
                block.Init(target[i]);
            }




            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id: 44,
        name: "",
        type: "",
        odd: 100,
        range: 5,
        isDamageMaker: false,
        damageMakeType: [],
        isControlMaker: false,
        isCureMaker: true,
        selectFri: true,
        selectSelf: true,
        selectEnemy: false,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count()
            let value = q.GetAdditionValue(self, 32, '', 0.228);
            let value2 = 999;
            let cureIndex = 35;

            let res = a.SelectTarget(self, 4, true, false, 3, true, true, this,"");
            let enemy = a.SelectTarget(self, 5, false, true, 3, false, false, that);
            for (let i in res) {
                let block1 = new DamageBlock(self, this, "", false, -value, false, false, 2, 0);
                let work1 = block1.Init(res[i], null, false);
                if (work1) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `${value}%`);
                }
                let block2 = new DamageBlock(self, this, "", false, -value, false, false, 2, 0);
                let work2 = block2.Init(res[i], null, false);
                if (work2) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `${value}%`);
                }
                //
                let block3 = new DamageBlock(self, this, "", true, -value2, false, false, 3, 3);
                let work3 = block3.Init(res[i], null, false);
                if (work3) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `()`);
                }
                let block4 = new DamageBlock(self, this, "", true, -value2, false, false, 3, 3);
                let work4 = block4.Init(res[i], null, false);
                if (work4) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `()`);
                }
            }
            let blood = function () {
                if (this.damageFromWho == self) {
                    let cure = Math.floor(this.damageValue * cureIndex / 100);
                    cure = c.CountCure(self, self, 0, 0, that, cure);
                    a.GetCure(self, self, cure, this.tactics, true);
                }
            }
            let action = function () {
                if (self.driver.turn == 4) {
                    let r = new Report();
                    r.InitExecuteReport(2, self, ``);
                    for (let i in enemy) {
                        let block = new AfterDamageBlock(self, that, blood, 99, true, true, true, true, true, true, true);
                        block.Init(enemy[i], "");
                    }
                }
            }
            let block = new ExecuteBlock(self, this, "", action, 5);
            block.Init(self);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id: 60,
        name: "",
        type: "",
        odd: 100,
        range: 3,
        isDamageMaker: false,
        damageMakeType: [],
        isControlMaker: false,
        isCureMaker: true,
        selectFri: true,
        selectSelf: true,
        selectEnemy: false,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {
            let that = this;
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let odd = 50;//
            let value = q.GetAdditionValue(self, 20.4, '', 0.13);//
            let cureValue = 80;//
            let cureIndex = 0.75;//
            if (self.driver.version == '') {
                cureValue = 60;
            }

            let res = self.ally;
            for (let i in res) {
                let action = function () {
                    let realOdd = q.GetMoraleOdd(self, odd);
                    let rand = Math.floor(Math.random() * 100);
                    if (rand < realOdd) {
                        let cure = c.CountCure(self, res[i], cureValue, cureIndex, that);
                        let report = new Report();
                        // report.InitExecuteReport(3,res[i],`${cure}(${res[i].hp})`,false)
                        report.InitTwoTargetReport(3, self, res[i], ``, `${cure}(${res[i].hp + cure})`, false)
                        a.GetCure(self, res[i], cure, that, true, report);
                    }
                }
                let block1 = new DamageBlock(self, this, "", false, -value, false, false, 3, 0);
                let work1 = block1.Init(res[i], null, false);
                if (work1) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `${value}%`);
                }
                let block2 = new DamageBlock(self, this, "", false, -value, false, false, 3, 0);
                let work2 = block2.Init(res[i], null, false);
                if (work2) {
                    let r = new Report();
                    r.InitExecuteReport(2, res[i], `${value}%`);
                }
                let block = new AfterDamageBlock(self, this, action, 99, true, true, true, true, true, true, true);
                let work = block.Init(res[i], "");
                if (work) {
                    let report = new Report();
                    report.InitExecuteReport(2, res[i], ``);
                }
            }
            let clearCure = function () {
                if (self.driver.turn == 4) {
                    for (let i in res) {
                        let temp = [];
                        for (let j in res[i].AFTER_DAMAGE) {
                            if (res[i].AFTER_DAMAGE[j].tactics != that) temp.push(res[i].AFTER_DAMAGE[j]);
                            else {
                                console.log(123);
                            }
                        }
                        res[i].AFTER_DAMAGE = temp;
                    }
                }
            }
            let block = new ExecuteBlock(self, this, '', clearCure, 4);
            block.Init(self);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id: 91,
        name: "",
        type: "",
        odd: 100,
        range: 1,
        isDamageMaker: true,
        damageMakeType: ["", ""],
        isControlMaker: false,
        isCureMaker: false,
        selectFri: true,
        selectSelf: true,
        selectEnemy: true,
        targetNumber: 1,
        arms: [''],
        wait: 0,
        execute(self) {
            let that = this;
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let odd = 70;//
            let damageValue1 = 200;
            let damageValue2 = q.GetAdditionValue(self, 200, '', 1);
            if (self.driver.version == '') {
                odd = 65;
            }

            let action = function () {
                let target = this.beExecuted;
                //
                if (this.damageFromWho == self && this.damageFromTactics != that) {
                    let realOdd = q.GetMoraleOdd(self, odd);
                    let rand = Math.floor(Math.random() * 100);
                    if (rand < realOdd) {
                        let rand1 = Math.floor(Math.random() * 2);
                        if (rand1 == 1) {
                            let damage = c.CountDamage(self, target, damageValue1, '', false, that, null);
                            let report = new Report().InitTwoTargetReport(3, self, target, ``, `${damage}(${target.hp - damage})`);
                            a.GetHurt(self, target, damage, false, '', that, false, report);
                        }
                        else {
                            let damage = c.CountDamage(self, target, damageValue2, '', false, that, null);
                            let report = new Report().InitTwoTargetReport(3, self, target, ``, `${damage}(${target.hp - damage})`);
                            a.GetHurt(self, target, damage, false, '', that, false, report);
                        }
                    }
                }
            }
            for (let i in self.enemy) {
                let target = self.enemy[i];
                let block = new AfterDamageBlock(self, this, action, 99, true, true, true, true, true, true, true);
                block.Init(target, '');
            }
            let r = new Report();
            r.InitExecuteReport(2, self, ``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id: 105,
        name: "",
        type: "",
        odd: 100,
        range: 3,
        isDamageMaker: false,
        damageMakeType: [],
        isControlMaker: false,
        isCureMaker: false,
        selectFri: true,
        selectSelf: true,
        selectEnemy: false,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {
            let that = this;
            let q = new Query();
            //
            let value = q.GetAdditionValue(self, 80, '', 0.325);
            //
            let decValue = q.GetAdditionValue(self, 20, '', 0.16);
            //
            let declineIndex = 4
            let attrDeclineValue = Math.floor(value / declineIndex);

            let tag = '' + self.belong + self.position;
            for (let i in self.ally) {
                let target = self.ally[i];
                let block1 = new AttrBlock(self, this, '', 0, 99);
                block1.isBenefit = true;
                block1.Init(target, false);
                block1[tag] = 0;
                let r1 = new Report();
                r1.InitExecuteReport(2, target, `()`)
                let block2 = new AttrBlock(self, this, '', 0, 99);
                block2.isBenefit = true;
                block2.Init(target, false);
                let r2 = new Report();
                r2.InitExecuteReport(2, target, `()`)
                block2[tag] = 0;
                let block3 = new DamageBlock(self, this, '', false, 0, false, false, 99, 0);
                block3.isBenefit = true;
                block3.Init(target, null, false);
                block3[tag] = 0;
                let r3 = new Report();
                r3.InitExecuteReport(2, target, `()`)
            }
            let action = function () {
                if (self.driver.turn == 2 || self.driver.turn == 4 || self.driver.turn == 6) {
                    let r = new Report();
                    r.InitExecuteReport(1, self, ``);
                    for (let i in self.ally) {
                        let target = self.ally[i];
                        //
                        let temp = [];
                        for (let j in target.ATTR) {
                            if (target.ATTR[j].tactics != that) {
                                temp.push(target.ATTR[j])
                            }
                        }
                        target.ATTR = temp;
                        let dmgTemp = [];
                        for (let j in target.DAMAGE_VAR) {
                            if (target.DAMAGE_VAR[j].tactics != that) dmgTemp.push(target.DAMAGE_VAR[j]);
                        }
                        target.DAMAGE_VAR = dmgTemp;
                        //
                        let block1 = new AttrBlock(self, that, '', value, 99);
                        block1.isBenefit = true;
                        block1[tag] = declineIndex;
                        block1.Init(target, false);
                        let r1 = new Report();
                        r1.InitExecuteReport(3, target, `${value}(${q.GetAttr(target,"")})`)
                        let block2 = new AttrBlock(self, that, '', value, 99);
                        block2.isBenefit = true;
                        block2[tag] = declineIndex;
                        block2.Init(target, false);
                        let r2 = new Report();
                        r2.InitExecuteReport(3, target, `${value}(${q.GetAttr(target,"")})`)
                        let block3 = new DamageBlock(self, that, '', false, -decValue, false, false, 99, 0);
                        block3.Init(target, null, false);
                        block3.isBenefit = true;
                        block3[tag] = declineIndex
                        let r3 = new Report();
                        r3.InitExecuteReport(3, target, `${decValue}%`);
                    }
                }
            }
            let block = new ExecuteBlock(self, this, '', action, 99);
            block.Init(self, false);
            let makeDamage = function(){
                //
                let target = this.beExecuted;
                for (let j in target.ATTR) {
                    if (target.ATTR[j].tactics == that && target.ATTR[j][tag] > 0) {
                        target.ATTR[j].value -= attrDeclineValue;
                        target.ATTR[j][tag]--;
                        let r = new Report();
                        let txt = `${target.ATTR[j].attrType}${target.ATTR[j].value}(${q.GetAttr(target, target.ATTR[j].attrType)})()`
                        r.InitTwoTargetReport(3, self, target, ``, txt)
                    }
                }
                for (let j in target.DAMAGE_VAR) {
                    if (target.DAMAGE_VAR[j].tactics == that && target.DAMAGE_VAR[j][tag] > 0) {
                        target.DAMAGE_VAR[j].value += Math.floor(decValue / declineIndex);
                        target.DAMAGE_VAR[j][tag]--;
                        let r = new Report();
                        r.InitTwoTargetReport(3, self, target, ``, `${Math.abs(target.DAMAGE_VAR[j].value)}%()`)
                    }
                }
            
            }
            for(let i in self.ally){
                let target = self.ally[i];
                let block = new MakeDamageBlock(self,this,makeDamage,99);
                block.Init(target,this.name);
            }
            let r = new Report();
            r.InitExecuteReport(2, self, ``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id: 154,
        name: "",
        type: "",
        odd: 100,
        range: 5,
        isDamageMaker: false,
        damageMakeType: [""],
        isControlMaker: false,
        isCureMaker: false,
        selectFri: true,
        selectSelf: true,
        selectEnemy: true,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {

            let q = new Query();
            let c = new Count();
            let a = new Attack()
            //
            let odd = 40;
            //
            let damageValue = q.GetAdditionValue(self, 100, "", 0.8);
            //
            let cureValue = 60;
            let cureIndex = 0.45;

            let that = this;
            let allyAction = function () {
                if (this.attrBlock.isBenefit) {
                    let realOdd = q.GetMoraleOdd(this.beExecuted, odd);
                    let rand = Math.floor(Math.random() * 100)
                    if (rand < realOdd) {
                        let cure = c.CountCure(self, this.beExecuted, cureValue, cureIndex, that);
                        let r = new Report();
                        r.InitTwoTargetReport(3, self, this.beExecuted, ``, `${cure}(${this.beExecuted.hp + cure})`, false);
                        a.GetCure(self, this.beExecuted, cure, that, true, r)
                    }
                    else {
                        let r = new Report();
                        r.InitTwoTargetReport(3, self, this.beExecuted, ``, `${realOdd}%`);
                    }
                }
            }
            let enemyAction = function () {
                if (!this.attrBlock.isBenefit) {
                    let realOdd = q.GetMoraleOdd(this.beExecuted, odd);
                    let rand = Math.floor(Math.random() * 100)
                    if (rand < realOdd) {
                        let damage = c.CountDamage(self, this.beExecuted, damageValue, "", false, that, null);
                        a.GetHurt(self, this.beExecuted, damage, false, "", that, false, null, false);
                        let r = new Report();
                        r.InitTwoTargetReport(3, self, this.beExecuted, ``, `${damage}(${this.beExecuted.hp})`);
                    }
                    else {
                        let r = new Report();
                        r.InitTwoTargetReport(3, self, this.beExecuted, ``, `${realOdd}%`);
                    }
                }
            }
            for (let i in self.ally) {
                let block = new AttrAppliedExecuteBlock(self, this, true, true, true, true, true, allyAction, 99);
                block.Init(self.ally[i]);
                let r = new Report();
                r.InitExecuteReport(2, self.ally[i], ``);
            }
            for (let i in self.enemy) {
                let block = new AttrAppliedExecuteBlock(self, this, true, true, true, true, true, enemyAction, 99);
                block.Init(self.enemy[i]);
                let r = new Report();
                r.InitExecuteReport(2, self.enemy[i], ``);
            }


            this.STATISTICS.times += 1;
        }
    }
]

export default hanSArr;