import Count from "./count";
import Judge from "./judge";
import Report from "./report";
import Query from "./query";
import Manage from "./manage";

//
class Attack {
    constructor(attacker) {
        this.attacker = attacker;
    }
    /**
     * 
     * @param {Object} self 
     * @param {number} range 
     * @param {boolean} isFri 
     * @param {boolean} isEn  
     * @param {number} tgtNumber 
     * @param {boolean} includeSelf 
     * @param {boolean} [considerMad=true] 
     * @param {Object|null} [tacticsItem=null] 
     * @param {string} forWhat "","","","","".... ""
     * @returns []
     */
    //,self,rangeisFri,isEn,number,includeSelfconsiderMad
    SelectTarget(self, range, isFri, isEn, tgtNumber, includeSelf, considerMad = true, tacticsItem = null,forWhat = "") {
        let res = [];//
        let arr = [];//
        let j = new Judge();
        let dis = range;
        let number = tgtNumber;
        //
        for(let i in self.BEFORE_TARGET_SELECT_EXECUTE){
            self.BEFORE_TARGET_SELECT_EXECUTE[i].Execute(self,tacticsItem,range,number,res,forWhat);
            number = self.BEFORE_TARGET_SELECT_EXECUTE[i].tgtNumber;
        }
        //
        for (let i = Number(self.position) + 1; i < 3; i++) {
            if (self.ally[i].alive) {
                dis -= 1;
            }
        }
        dis > 3 ? dis = 3 : null;
        let isMad = j.IsGetStatus(self, "");
        if (j.IsGetStatus(self, "")) isMad = false;
        //
        if ((considerMad && isMad) || isEn) {
            for (let i = 0; i < 3 && dis > 0; i++) {
                if (self.enemy[Math.abs(i - 2)].alive) {
                    //
                    if (tacticsItem != null
                        && tacticsItem.type == ''
                        && (j.IsGetStatus(self.enemy[Math.abs(i - 2)], '') || j.IsGetStatus(self.enemy[Math.abs(i - 2)], ''))) {
                        let r = new Report();
                        r.InitExecuteReport(3, self.enemy[Math.abs(i - 2)], ``);
                    }
                    else {
                        arr.push(self.enemy[Math.abs(i - 2)]);
                    }
                    dis--;
                }
            }
        }
        //
        if ((considerMad && isMad) || isFri) {
            if (range > 1 || range == 1 && self.position == 1) {
                for (let i in self.ally) {
                    if (self.ally[i].alive) {
                        if (tacticsItem != null && tacticsItem.type == '' && j.IsGetStatus(self.ally[i], '')) {
                            let r = new Report();
                            r.InitExecuteReport(3, self.ally[i], ``);
                        }
                        else {
                            if (self.ally[i] == self) {
                                includeSelf ? arr.push(self) : null;
                            }
                            else
                                arr.push(self.ally[i]);
                        }
                    }
                }
            }
            else if (range == 1) {
                if (self.ally[1].alive) arr.push(self.ally[1]);
                else if (self.ally[Math.abs(self.position - 2)].alive) arr.push(self.ally[Math.abs(self.position - 2)]);
                includeSelf ? arr.push(self) : null;
            }
            else if (range == 0) includeSelf ? arr.push(self) : null;
        }
        //
        if (tacticsItem == null && j.IsGetStatus(self, '') && !j.IsGetStatus(self, '')) {
            //arr
            let target = null;
            for (let i in self.STATUS) {
                if (self.STATUS[i].type == '') {
                    target = self.STATUS[i].applicator;
                    break;
                }
            }
            for (let i in arr) {
                if (arr[i] == target) {
                    res.push(arr[i]);
                    arr = [];
                    let r = new Report();
                    r.InitTwoTargetReport(2, self, target, ``, ``);
                    break;
                }
            }
        }

        //numberres
        for (let i = 0; i < number && arr.length > 0; i++) {
            let rand = Math.floor(Math.random() * arr.length);
            res.push(arr[rand]);
            arr.splice(rand, 1);
        }
        //,
        if (tacticsItem == null) {
            let temp = [];
            for (let i in res) {
                //
                let target = null;
                for (let j in res[i].STATUS) {
                    if (res[i].STATUS[j].type == '') {
                        target = res[i].STATUS[j].applicator;
                        break;
                    }
                }
                if (target != null) {
                    let r = new Report();
                    r.InitTwoTargetReport(2, res[i], target, ``, ``);
                    temp.push(target);
                }
                else temp.push(res[i]);
            }
            res = temp;
        }
        //
        for (let i = 0; i < res.length; i++) {
            for (let j = i + 1; j < res.length; j++) {
                if (Number(res[i].position) > Number(res[j].position)) {
                    let temp = res[i];
                    res[i] = res[j];
                    res[j] = temp;
                }
            }
        }
        //
        for(let i in self.AFTER_TARGET_SELECT_EXECUTE){
            self.AFTER_TARGET_SELECT_EXECUTE[i].Execute(self,tacticsItem,range,number,res,forWhat);
        }

        return res;
    }
    /**
     * aoe
     * @param {Object} target 
     * @returns 
     */
    GetDivideForceTarget(target) {
        let res = [];
        //
        for (let i = Number(target.position) - 1; i >= 0; i--) {
            if (target.ally[i].alive) {
                res.push(target.ally[i])
                break;
            }
        }
        //
        for (let i = Number(target.position) + 1; i < 3; i++) {
            if (target.ally[i].alive) {
                res.push(target.ally[i]);
                break;
            }
        }
        return res;
    }
    /**
     * 
     * @param {Object} attacker 
     */
    LaunchAttack(attacker) {
        let c = new Count();
        let q = new Query();
        let j = new Judge();
        let adRange = q.GetRealAdRange(attacker);
        let receiver = this.SelectTarget(attacker, adRange, false, true, 1, false, true, null,"");
        //
        if (receiver.length > 0) {
            //
            let report = new Report();
            report.InitTwoTargetReport(2, attacker, receiver[0], "", "");
            //
            for (let i in attacker.LAUNCH_ATTACK_EXECUTE) {
                if (attacker.LAUNCH_ATTACK_EXECUTE[i].duration > 0 && !attacker.LAUNCH_ATTACK_EXECUTE[i].isAfterExecute) {
                    attacker.LAUNCH_ATTACK_EXECUTE[i].Execute(receiver[0]);
                }
            }
            let damage = c.CountDamage(attacker, receiver[0], 100, "",true,null,null);
            this.GetHurt(attacker, receiver[0], damage, true, "", null);
            //
            for (let i in attacker.LAUNCH_ATTACK_EXECUTE) {
                if (attacker.LAUNCH_ATTACK_EXECUTE[i].duration > 0 && attacker.LAUNCH_ATTACK_EXECUTE[i].isAfterExecute && attacker.LAUNCH_ATTACK_EXECUTE[i].detailExecuteTime == '') {
                    attacker.LAUNCH_ATTACK_EXECUTE[i].Execute(receiver[0]);
                }
            }
            //
            if (receiver[0].alive && !j.IsGetStatus(attacker,'')) this.LaunchCounterAttack(attacker, receiver[0], "");
            //
            this.LaunchCounterAttack(attacker, receiver[0], "");
            //
            for (let i in attacker.LAUNCH_ATTACK_EXECUTE) {
                if (attacker.LAUNCH_ATTACK_EXECUTE[i].duration > 0 && attacker.LAUNCH_ATTACK_EXECUTE[i].isAfterExecute && attacker.LAUNCH_ATTACK_EXECUTE[i].detailExecuteTime == '') {
                    attacker.LAUNCH_ATTACK_EXECUTE[i].Execute(receiver[0]);
                }
            }
            //
            if (receiver[0].alive) this.LaunchComboAttack(attacker, receiver[0]);
            //
            for (let i in attacker.LAUNCH_ATTACK_EXECUTE) {
                if (attacker.LAUNCH_ATTACK_EXECUTE[i].duration > 0 && attacker.LAUNCH_ATTACK_EXECUTE[i].isAfterExecute && attacker.LAUNCH_ATTACK_EXECUTE[i].detailExecuteTime == '') {
                    attacker.LAUNCH_ATTACK_EXECUTE[i].Execute(receiver[0]);
                }
            }
        }
        else {
            //
            let report = new Report();
            report.InitExecuteReport(2, attacker, "")
        }

    }
    /**
     * 
     * @param {Object} target  
     */
    LaunchActiveTactics(target) {
        let q = new Query();
        let m = new Manage();
        for (let i in target.TacticsArr) {
            if (target.TacticsArr[i].type == '') {
                //
                for (let j in target.EXECUTE) {
                    if (target.EXECUTE[j].executeStage == "") target.EXECUTE[j].execute();
                }
                //,launchTactics
                let launchTactics = m.ManageWaitTactics(target, target.TacticsArr[i]);
                let isLaunched = launchTactics.launch == null ? false : true;
                let odd = q.GetRealTacticsOdd(target, target.TacticsArr[i]);
                let rand = Math.floor(Math.random() * 100);
                if (rand < odd) {
                    //
                    if (q.GetRealTacticsPrepareTime(target, target.TacticsArr[i]) <= 0) {
                        let report = new Report();
                        report.InitExecuteReport(2, target, `${target.TacticsArr[i].name}!`)
                        target.TacticsArr[i].execute(target);
                        isLaunched = true;
                    }
                    //
                    else if (launchTactics.wait == null && launchTactics.launch == null) {
                        let r = new Report();
                        r.InitExecuteReport(2, target, `${target.TacticsArr[i].name}`);
                        let block = {
                            tactics: target.TacticsArr[i],//
                            waitTime: q.GetRealTacticsPrepareTime(target, target.TacticsArr[i])//
                        }
                        target.WaitTacticsArr.push(block);
                    }
                }
                else if (launchTactics.wait == null && launchTactics.launch == null) {
                    let r = new Report();
                    r.InitExecuteReport(2, target, `${target.TacticsArr[i].name}${odd}%`)
                }
                //
                if (target.TacticsArr[i].isDamageMaker && isLaunched) m.ManageOnceTacticsDamageBlock(target, true);
                //
                for (let j in target.ACTIVE_TACTICS_EXECUTE) {
                    //
                    if (target.ACTIVE_TACTICS_EXECUTE[j].isLaunchedExecute == isLaunched
                        && target.ACTIVE_TACTICS_EXECUTE[j].waitTime <= 0 
                        && target.ACTIVE_TACTICS_EXECUTE[j].isAfterExecute
                        && target.ACTIVE_TACTICS_EXECUTE[j].tacticsType == '') {
                        target.ACTIVE_TACTICS_EXECUTE[j].Execute(target.TacticsArr[i]);
                    }
                }
            }

        }
    }
    //
    LaunchCounterAttack(attacker, receiver, type = '') {
        let c = new Count();
        let j = new Judge();
        if (type == "") {
            for (let i in receiver.COUNTER_ATTACK) {
                if (receiver.COUNTER_ATTACK[i].waitTime <= 0 && receiver.COUNTER_ATTACK[i].type == '') {
                    let avail = j.IsCanAttackSomeone(receiver, attacker);
                    if (avail) {
                        let odd = receiver.COUNTER_ATTACK[i].damageOdd;
                        let rand = Math.floor(Math.random() * 100);
                        //
                        if (rand < odd) {
                            let r = new Report();
                            r.InitExecuteReport(3, receiver, ``);
                            let damage = c.CountDamage(receiver, attacker, receiver.COUNTER_ATTACK[i].damageValue, "", true, null, null);
                            this.GetHurt(receiver, attacker, damage, true, "", null);
                        }
                    }
                    else {
                        let r = new Report();
                        r.InitExecuteReport(3, receiver, ``);
                    }

                }
            }
        }
        else if (type == "") {
            for (let i in attacker.COUNTER_ATTACK) {
                if (attacker.COUNTER_ATTACK[i].waitTime <= 0 && attacker.COUNTER_ATTACK[i].type == '') {
                    let odd = attacker.COUNTER_ATTACK[i].damageOdd;
                    let rand = Math.floor(Math.random() * 100);
                    if (rand < odd) {
                        let targetArr = this.GetDivideForceTarget(receiver);
                        for (let j in targetArr) {
                            let r = new Report();
                            r.InitExecuteReport(3, attacker, ``);
                            let damage = c.CountDamage(attacker, targetArr[j], attacker.COUNTER_ATTACK[i].damageValue, "", true, null, null);
                            this.GetHurt(attacker, targetArr[j], damage, true, "", null);
                        }
                    }
                }
            }
        }
    }
    /**
     * 
     * @param {Object} attacker -
     * @param {Object} receiver -
     */
    LaunchComboAttack(attacker, receiver) {
        let q = new Query();
        for (let i in attacker.TacticsArr) {
            if (attacker.TacticsArr[i].type == '') {
                //
                for (let j in attacker.EXECUTE) {
                    if (attacker.EXECUTE[j].executeStage == "") attacker.EXECUTE[j].execute();
                }
                let realOdd = q.GetRealTacticsOdd(attacker, attacker.TacticsArr[i]);
                let rand = Math.floor(Math.random() * 100);
                if (rand < realOdd) {
                    let r = new Report();
                    r.InitExecuteReport(3, attacker, `${attacker.TacticsArr[i].name}`);
                    attacker.TacticsArr[i].execute(attacker, receiver);
                    //
                    for (let k in attacker.ACTIVE_TACTICS_EXECUTE) {
                        if (attacker.ACTIVE_TACTICS_EXECUTE[k].waitTime <= 0 && attacker.ACTIVE_TACTICS_EXECUTE[k].isAfterExecute
                            && attacker.ACTIVE_TACTICS_EXECUTE[k].tacticsType == '') {
                            attacker.ACTIVE_TACTICS_EXECUTE[k].Execute(attacker.TacticsArr[i]);
                        }
                    }
                }
                else {
                    let r = new Report();
                    r.InitExecuteReport(3, attacker, `${attacker.TacticsArr[i].name}${realOdd}%`);
                }
            }
        }
    }

    /**
     * 
     * @param {Object} attacker - 
     * @param {Object} receiver - 
     * @param {number} damage - 
     * @param {boolean} isCommonAttack - 
     * @param {string} damageType - 
     * @param {Object|null} tacticsItem - 
     * @param {boolean} [needReport=true] - 
     * @param {Object|null} [reportParma=null] - 
     * @param {boolean} [isContinueDamageType=false] - 
     * @returns {Object} 
    */
    GetHurt(attacker, receiver, originDamageValue, isCommonAttack, damageType, tacticsItem, needReport = true, reportParma = null, isContinueDamageType = false) {
        let damage = originDamageValue;
        //()
        for(let i in receiver.BEFORE_GET_HURT){
            receiver.BEFORE_GET_HURT[i].Execute(attacker, receiver, originDamageValue,isCommonAttack,damageType,tacticsItem,isContinueDamageType);
            damage = receiver.BEFORE_GET_HURT[i].damageValue;
        }


        //
        receiver.hp -= damage;
        //
        receiver.wound += Math.floor(damage * 0.95);
        if (receiver.hp <= 0) {
            receiver.hp = 0;
            receiver.wound = Math.floor(receiver.wound * 0.6)
            receiver.alive = false;
        }
        //
        let reportLevel = 3;
        if (needReport) {
            let report = new Report();
            report.InitExecuteReport(reportLevel, receiver, `${damage}(${receiver.hp})`);
        }
        //
        else if (reportParma != null) {
            let r = new Report();
            r.PutReportToHeap(receiver.global.ReportHeap, reportParma);
        }
        //
        let m = new Manage();
        m.ManageOnceDamageBlock(attacker, damage, isCommonAttack, damageType, tacticsItem, true);
        m.ManageOnceDamageBlock(receiver, damage, isCommonAttack, damageType, tacticsItem, false);
        //
        if (isCommonAttack || tacticsItem == null) attacker.STATISTICS.atk += damage;
        else {
            tacticsItem.STATISTICS.damage += damage;
        }
        //
        if (attacker.alive && damage != 0) {
            let len = attacker.MAKE_DAMAGE.length;
            for (let i = 0; i < len; i++) {
                if (!attacker.MAKE_DAMAGE[i]['action']) {
                    attacker.MAKE_DAMAGE[i].Execute(damage, receiver, tacticsItem, isCommonAttack, damageType);
                    attacker.MAKE_DAMAGE[i]['action'] = true;
                }
                if (len != attacker.MAKE_DAMAGE.length) {
                    i = 0;
                    len = attacker.MAKE_DAMAGE.length;
                }
            }
            //action
            for (let i in attacker.MAKE_DAMAGE) {
                attacker.MAKE_DAMAGE[i]['action'] = false;
            }
        }
        //,
        if (receiver.alive && damage != 0) {
            let length = receiver.AFTER_DAMAGE.length;
            for (let i = 0; i < length; i++) {
                if (isCommonAttack) {
                    if (receiver.AFTER_DAMAGE[i].getAdDmgExecute || receiver.AFTER_DAMAGE[i].getCAttackExecute) {
                        receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isCommonAttack, damageType)
                        receiver.AFTER_DAMAGE[i].Execute();
                    }
                }
                else {
                    if (tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.command
                        || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.active
                        || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.combo
                        || tacticsItem.type == '' && receiver.AFTER_DAMAGE[i].getTacticsDmgExecute.passive) {

                        if(receiver.AFTER_DAMAGE[i].getAdDmgExecute && !receiver.AFTER_DAMAGE[i].getTactDmgExecute){
                            if(damageType == ''){
                                receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isCommonAttack, damageType)
                                receiver.AFTER_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
                            }
                        }
                        else{
                            receiver.AFTER_DAMAGE[i].GetDamageData(damage, attacker, tacticsItem, isCommonAttack, damageType)
                            receiver.AFTER_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
                        }
                        
                    }
                }
                if (receiver.AFTER_DAMAGE.length != length) {
                    i--;
                    length = receiver.AFTER_DAMAGE.length;
                }
            }
        }
        //xp
        if (receiver.alive && damage != 0 && isContinueDamageType) {
            let length = receiver.AFTER_CONTINUE_DAMAGE.length;
            for (let i = 0; i < length; i++) {
                if (tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.command
                    || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.active
                    || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.combo
                    || tacticsItem.type == '' && receiver.AFTER_CONTINUE_DAMAGE[i].getTacticsDmgExecute.passive) {
                    receiver.AFTER_CONTINUE_DAMAGE[i].GetData(damage, attacker, tacticsItem, damageType);
                    receiver.AFTER_CONTINUE_DAMAGE[i].Execute();//receiver.AFTER_DAMAGE
                }
                if (receiver.AFTER_CONTINUE_DAMAGE.length != length) {
                    i--;
                    length = receiver.AFTER_CONTINUE_DAMAGE.length;
                }
            }
        }
        return true;
    }
    /**
     * 
     * @param {Object} initiator - 
     * @param {Object} receiver - 
     * @param {number} cureNumber - 
     * @param {Object} [cureOriginTactics=null] - 
     * @param {boolean} [needReport=true] - 
     * @param {Object|null} [reportParma=null] - 
     * @returns {Object} 
    */
    //
    GetCure(initiator, receiver, cureNumber, cureOriginTactics = null, needReport = true, reportParma = null) {
        //
        let cure = cureNumber > 0 ? cureNumber : -cureNumber;
        let j = new Judge();
        let q = new Query();
        if (j.IsGetStatus(receiver, "")) {
            let report = new Report();
            report.InitExecuteReport(3, receiver, ``);
            return false;
        }
        //


        
        //
        receiver.hp += cure;
        receiver.wound -= cure;
        //
        cureOriginTactics.STATISTICS.cure += cure;
        if (needReport) {
            if (reportParma == null) {
                let report = new Report();
                report.InitExecuteReport(3, receiver, `${cure}(${receiver.hp})`);
            }
            else if (reportParma != null) {
                let r = new Report();
                r.PutReportToHeap(receiver.global.ReportHeap, reportParma);
            }
        }
        //
        for (let i in receiver.GET_CURE) {
            receiver.GET_CURE[i].Execute(cureOriginTactics, initiator, cureNumber);
        }
        return true;
    }
}




export default Attack;