import Attack from "../js/attack";
import Count from "../js/count";
import StatusBlock from "../block/status";
import Query from "../js/query";
import DamageBlock from "../block/damage";
import Report from "../js/report";
import CureStatusBlock from "../block/cureStatus";
import OddBlock from "../block/odd";
import ExecuteBlock from "../block/execute";
import ActiveTacticsBlock from "../block/execute3";
import AttackExecuteBlock from "../block/execute4";
import AttrBlock from "../block/attr";
import Judge from "../js/judge";
import AfterDamageBlock from "../block/execute1";
import ExecuteSysBlock from '../block/executeSys.js';
let jinSArr = [
    //
    {
        id : 122,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : ["",""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue1 = 180;//
            let damageValue2 = q.GetAdditionValue(self,100,'',1);//
            let addition = 10;//
            let decline = 5;//

            let action = function(){
                let rand = Math.floor(Math.random()*2);
                let r = new Report();
                r.InitExecuteReport(3,self,``);
                //
                if(rand == 1){
                    let res = a.SelectTarget(self,5,false,true,1,false,true,that,"");
                    for(let i in res){
                        let damage = c.CountDamage(self,res[i],damageValue1,'',false,that,null);
                        a.GetHurt(self,res[i],damage,false,'',that,true,null);
                    }
                    let block = new AttrBlock(self,that,'',addition,99,true);
                    block.Init(self,true,3);
                }
                else{
                    let res = a.SelectTarget(self,5,false,true,2,false,true,that,"");
                    for(let i in res){
                        let damage = c.CountDamage(self,res[i],damageValue2,'',false,that,null);
                        a.GetHurt(self,res[i],damage,false,'',that,true,null);
                    }
                    for(let i in res){
                        let block = new AttrBlock(self,that,'',-decline,99,true);
                        block.Init(res[i],true,3);
                    }
                }
                
            }
            let block1 = new AttackExecuteBlock(self,this,action,99,0,true);
            block1.Init(self,false);
            let block2 = new ActiveTacticsBlock(self,this,action,99,0,true,'');
            block2.Init(self,false);
            let block3 = new ActiveTacticsBlock(self,this,action,99,0,true,'');
            block3.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 123,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            //
            let value = q.GetAdditionValue(self,30,'',0.2);
            let decline = Math.floor(value/8);//
            let moraleAddition = 8;//
            
            for(let i in self.ally){
                let block1 = new DamageBlock(self,this,'',false,-value,false,false,99,0);
                block1.Init(self.ally[i],null,true,2);
                let block2 = new DamageBlock(self,this,'',false,-value,false,false,99,0);
                block2.Init(self.ally[i],null,true,2);
            }
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            let action = function(){
                let r = new Report();
                r.InitExecuteReport(1,self,``);
                let res = a.SelectTarget(self,5,true,false,3,true,false,that,"");
                for(let i in res){
                    res[i].morale += moraleAddition;
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${moraleAddition}(${res[i].morale})`)
                }
                for(let i in res){
                    let target = res[i];
                    for(let j in target.DAMAGE_VAR){
                        if(target.DAMAGE_VAR[j].tactics == that){
                            target.DAMAGE_VAR[j].value += decline;
                            let r = new Report();
                            r.InitExecuteReport(2,target,`${target.DAMAGE_VAR[j].damageType}${Math.abs(target.DAMAGE_VAR[j].value)}%`)
                        }
                    }
                }
            }
            let block = new ExecuteSysBlock(self,this,'',action,99);
            block.Init(self,false);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 125,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let j = new Judge();
            let odd1 = 30;//1
            let odd2 = 50;//2
            let damageValue = q.GetAdditionValue(self,220,'',1.25);
            
            let action = function(){
                let realOdd1 = q.GetMoraleOdd(self,odd1);
                let rand1 = Math.floor(Math.random()*100);
                let realOdd2 = q.GetMoraleOdd(self,odd2);
                let rand2 = Math.floor(Math.random()*100);
                if(rand1 < realOdd1){
                    let res = a.SelectTarget(self,5,false,true,1,false,true,that,"");
                    for(let i in res){
                        let block = new StatusBlock(self,that,'',1,0);
                        let work =  block.Init(res[i],false,2);
                        if(work){
                            let r = new Report();
                            r.InitTwoTargetReport(2,self,res[i],``,``);
                        }
                    }
                }
                if(rand2 < realOdd2){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``)
                    let res = a.SelectTarget(self,5,false,true,3,false,true,that,"");
                    for(let i in res){
                        if(j.IsGetStatus(res[i],'') || j.IsGetStatus(res[i],'')){
                            let damage = c.CountDamage(self,res[i],damageValue,'',false,that,null);
                            a.GetHurt(self,res[i],damage,false,'',that,true,null);
                        }
                    }
                }
            }
            let block = new ExecuteBlock(self,this,"",action,99);
            block.Init(self,false);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 126,
        name : "",
        type : "",
        odd : 40,
        range : 5,
        isDamageMaker : true,
        damageMakeType : ["",""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue1 = 160;
            let damageValue2 = q.GetAdditionValue(self,160,'',1.95);
            
            let realRange = q.GetTacticsRange(self,this);
            //
            let min = 10000;
            let minWarrior = null;
            let fri = a.SelectTarget(self,realRange,true,false,2,false,true,this,"");
            let res = a.SelectTarget(self,realRange,false,true,2,false,true,this,"");
            for(let i in fri){
                let targetAd = q.GetAttr(fri[i],'');
                if(targetAd < min){
                    min = targetAd;
                    minWarrior = fri[i];
                }
                else if(targetAd == min){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1){
                        min = targetAd;
                        minWarrior = fri[i];
                    }
                }
            }
            let target = null;
            if(minWarrior != null){
                let r = new Report();
                r.InitExecuteReport(3,minWarrior,``)
                let en = a.SelectTarget(minWarrior,realRange,false,true,1,false,true,this,"");
                for(let i in en){
                    let damage = c.CountDamage(minWarrior,en[i],damageValue1,'',false,this,null);
                    a.GetHurt(minWarrior,en[i],damage,false,'',this,true,null);
                    target = en[i];
                }
            }
            //
            let r = new Report();
            r.InitExecuteReport(3,self,``)
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue2,'',false,this,null);
                a.GetHurt(self,res[i],damage,false,'',this,true,null);
                if(target == res[i]){
                    let block = new StatusBlock(self,this,'',1,0);
                    block.Init(res[i],true,3);
                }
            }

            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 127,
        name : "",
        type : "",
        odd : 50,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self,target){
            let a = new Attack();
            let c = new Count();
            let damageValue1 = 260;//
            let damageValue2 = 80 + Math.floor(Math.random()*80);//2
            if(self.driver.version == ''){
                damageValue1 = 220;
            }

            //
            let damage = c.CountDamage(self,target,damageValue1,"",false,this,null);
            a.GetHurt(self,target,damage,false,"",this,true);
            
            //
            if(target.morale > 100){
                let block = new StatusBlock(self,this,"",1,0);
                block.Init(target,true);
            }
            else{
                let damage = c.CountDamage(self,target,damageValue2,"",false,this,null);
                a.GetHurt(self,target,damage,false,"",this,true);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 129,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : false,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue = q.GetAdditionValue(self,140,'',1.65);
            let odd = 50;//
            let attrValue = 15;//
            let cureValue = 100;//
            let cureIndex = 0.55;//
            if(self.driver.version == ''){
                damageValue = q.GetAdditionValue(self,125,'',1.5);
                odd = 80;
            }

            let action = function(){
                if(self.driver.turn < 5){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let cure = c.CountCure(self,self,cureValue,cureIndex,that);
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,self,``,`${cure}(${self.hp + cure})`,false)
                        a.GetCure(self,self,cure,that,true,r);
                        //
                        let block1 = new AttrBlock(self,that,'',attrValue,99,true);
                        block1.Init(self,true,3);
                        let block2 = new AttrBlock(self,that,'',attrValue,99,true);
                        block2.Init(self,true,3);
                    }
                }
            }
            let makeDamage = function(){
                if(self.driver.turn > 4 && self.position != 0){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``);
                    let res = a.SelectTarget(self,5,false,true,3,false,false,that,"");
                    for(let i in res){
                        let target = res[i];
                        if(q.GetAttr(target,'') < q.GetAttr(self,'')){
                            let realOdd = q.GetMoraleOdd(self,odd);
                            let rand = Math.floor(Math.random()*100);
                            if(rand < realOdd){
                                let damage = c.CountDamage(self,target,damageValue,'',false,that,null);
                                a.GetHurt(self,target,damage,false,'',that,true,null);
                            }
                            else{
                                let r = new Report();
                                r.InitTwoTargetReport(3,self,target,``,`${realOdd}%`);
                            }
                        }
                    }
                }
            }
            let block = new AfterDamageBlock(self,this,action,4,true,true,true,true,true,true,true);
            block.Init(self,'');
            let block1 = new ExecuteBlock(self,this,'',makeDamage,99);
            block1.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
]

export default jinSArr;