import Query from '../js/query.js'
import Count from '../js/count.js';
import Attack from '../js/attack.js';
import Report from '../js/report.js';
let comboS = [
    //
    {
        id : 10,
        name : "",
        type : "",
        odd : 30,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : ['',"",""],
        wait : 0,
        execute(self,target){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageValue = 150;//
            let damageValue2 = 100;//
            let odd = 60;//
            let odd_decline = 20;//
            //
            let damage = c.CountDamage(self,target,damageValue,"",false,this,null);
            a.GetHurt(self,target,damage,false,"",this,true);

            let time = 1;
            for(let i=0;i<time;i++){
                let realOdd = q.GetMoraleOdd(self,odd) - (time-1)*odd_decline;
                if(realOdd <= 0) return;
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let damage = c.CountDamage(self,target,damageValue2,"",false,this,null);
                    a.GetHurt(self,target,damage,false,"",this,true);
                    time += 1;
                }
            }
            
            
            this.STATISTICS.times += 1;
        }
    },
]

export default comboS;