import Report from "../js/report";
import Attack from "../js/attack";
import Count from "../js/count";
import Query from "../js/query";
import StatusBlock from "../block/status";
import DamageBlock from "../block/damage";
import DamageStatus from "../block/damageStatus";
import AfterDamageBlock from "../block/execute1";
import ExecuteBlock from "../block/execute";
import AttrBlock from '../block/attr.js';
import Util from "../js/util";
import AttackExecuteBlock from "../block/execute4.js";
import ActiveTacticsBlock from "../block/execute3.js";
let weiAArr = [
    //
    {
        id : 14,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let index = q.GetAdditionValue(self,32,'',0.11); //

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            //
            for(let i in res){
                let block = new DamageBlock(self,this,"",true,-index,false,false,2);
                block.Init(res[i]);
            }
            for(let i in res){
                let block = new StatusBlock(self,this,"",2);
                block.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 36,
        name : "",
        type : "",
        odd : 45,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue1 = q.GetAdditionValue(self,155,'',1.75);//
            let damageValue2 = q.GetAdditionValue(self,130,'',1.5);


            let tag = "" + self.position + self.belong;
            let action = function(){
                if(that == this.damageFromTactics) return;
                let target = this.beExecuted;
                //tag
                let refresh = true;
                for(let i in target.SPECIAL_STATUS){
                    if(target.SPECIAL_STATUS[i].tactics == that) refresh = false;
                }
                if(refresh){
                    target[tag] = 0;
                    let temp = [];
                    for(let i in target.AFTER_DAMAGE){
                        if(target.AFTER_DAMAGE[i].tactics != that) temp.push(target.AFTER_DAMAGE[i]);
                    }
                    target.AFTER_DAMAGE = temp;
                    return;
                }
                if(target[tag] > 0){
                    target[tag] -=1;
                    let damage = c.CountDamage(self,target,damageValue2,"",false,that,"");
                    let work = a.GetHurt(self,target,damage,false,"",that,false,null);
                    if(work){
                        let r = new Report();
                        r.InitExecuteReport(3,target,`${damage}(${target.hp})${target[tag]}`);
                    }
                    //,
                    if(target[tag] == 0){
                        let temp = [];
                        for(let k in target.SPECIAL_STATUS){
                            if(target.SPECIAL_STATUS[k].tactics != that) temp.push(target.SPECIAL_STATUS[k]);
                            else{
                                let r = new Report();
                                r.InitExecuteReport(3,target,``);
                                let temp = [];
                                for(let i in target.AFTER_DAMAGE){
                                    if(target.AFTER_DAMAGE[i].tactics != that) temp.push(target.AFTER_DAMAGE[i]);
                                }
                                target.AFTER_DAMAGE = temp;
                            }
                        }
                        target.SPECIAL_STATUS = temp;
                    }
                }
            }
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
            for(let i in res){
                //
                for(let j in res[i].ally){
                    //
                    let refresh = true;
                    for(let k in res[i].ally[j].SPECIAL_STATUS){
                        if(res[i].ally[j].SPECIAL_STATUS[k].tactics == this) refresh = false;
                    }
                    if(refresh){
                        res[i].ally[j][tag] = 0;
                        //
                        let arr = [];
                        for(let m in res[i].ally[j].AFTER_DAMAGE){
                            if(res[i].ally[j].AFTER_DAMAGE[m].tactics != this) arr.push(res[i].ally[j].AFTER_DAMAGE[m])
                        }
                        res[i].ally[j].AFTER_DAMAGE = arr;
                    }
                    else if(res[i].ally[j][tag]>0){
                        for(let k=0;k<res[i].ally[j][tag];k++){
                            let damage = c.CountDamage(self,res[i].ally[j],damageValue2,"",false,this,"");
                            let work = a.GetHurt(self,res[i].ally[j],damage,false,"",that,false,null);
                            if(work){
                                let r = new Report();
                                r.InitExecuteReport(3,res[i].ally[j],`${damage}(${res[i].ally[j].hp})${res[i].ally[j][tag] - k - 1}`);
                            }
                        }
                        res[i].ally[j][tag] = 0;
                        //
                        let temp = [];
                        for(let k in res[i].ally[j].SPECIAL_STATUS){
                            if(res[i].ally[j].SPECIAL_STATUS[k].tactics != this) temp.push(res[i].ally[j].SPECIAL_STATUS[k]);
                            else{
                                let r = new Report();
                                r.InitExecuteReport(3,res[i].ally[j],``);
                                let arr = [];
                                for(let m in res[i].ally[j].AFTER_DAMAGE){
                                    if(res[i].ally[j].AFTER_DAMAGE[m].tactics != this) arr.push(res[i].ally[j].AFTER_DAMAGE[m])
                                }
                                res[i].ally[j].AFTER_DAMAGE = arr;
                            }
                        }
                        res[i].ally[j].SPECIAL_STATUS = temp;
                    }
                }
                let damage = c.CountDamage(self,res[i],damageValue1,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true,null);
                let block = new DamageStatus(self,this,"",damageValue2,"",3,0,false);
                let work = block.Init(res[i],true);
                if(work){
                    res[i][tag] = 3;//
                    let block1 = new AfterDamageBlock(self,this,action,3,true,true,true,true,true,true,true);
                    block1.Init(res[i],"");
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 51,
        name : "",
        type : "",
        odd : 100,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let index = q.GetAdditionValue(self,50,'',0.2);//
            let tgtNumber = Math.floor(Math.random()*2) + 2;//

            
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,tgtNumber,true,false,this,"");
            let action = function(){
                if(self.driver.turn < 5){
                    let target = this.beExecuted;
                    for(let i in target.DAMAGE_VAR){
                        if(target.DAMAGE_VAR[i].tactics == that){
                            let value = Math.floor(index - (self.driver.turn - 1) * 0.25 * index);
                            target.DAMAGE_VAR[i].value = value;
                            let r = new Report();
                            r.InitTwoTargetReport(2,self,target,``,`${value}%`)
                            break;
                        }
                    }
                }
            }
            for(let i in res){
                let block = new DamageBlock(self,this,"",true,index,false,false,4,0);
                let work = block.Init(res[i],null,false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`${index}%`);
                    let execute = new ExecuteBlock(self,this,"",action,4);
                    execute.Init(res[i]);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 59,
        name : "",
        type : "",
        odd : 40,
        range : 5,
        isDamageMaker : true,
        damageMakeType : ["",""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 1,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue1 = q.GetAdditionValue(self,240,'',1.5);
            let damageValue2 = 240;

            let realRange = q.GetTacticsRange(self,this);
            
            for(let j=0;j<2;j++){
                //
                if(j == 0){
                    let res = a.SelectTarget(self,realRange,false,true,2,false,true,this,"");
                    for(let i in res){
                        let damage = c.CountDamage(self,res[i],damageValue1,'',false,this,null);
                        a.GetHurt(self,res[i],damage,false,"",this,true,null);
                    }
                }
                //
                else{
                    let res = a.SelectTarget(self,realRange,false,true,2,false,true,this,"");
                    for(let i in res){
                        let damage = c.CountDamage(self,res[i],damageValue2,'',false,this,null);
                        a.GetHurt(self,res[i],damage,false,"",this,true,null);
                    }
                }
            }
            //
            for(let i in self.ally){
                let block = new StatusBlock(self,this,'',2,0);
                block.Init(self.ally[i],true);
            }
            for(let i in self.enemy){
                let block = new StatusBlock(self,this,'',2,0);
                block.Init(self.enemy[i],true);
            }


            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 63,
        name : "",
        type : "",
        odd : 100,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let odd = 40;//
            let damageValue = 60;//

            //
            let status = new StatusBlock(self,this,'',99,0);
            let work =  status.Init(self,false);
            if(work){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
            }
            let action = function(){
                //
                if(this.damageFromTactics && this.damageFromTactics.name == "") return
                if(this.damageFromTactics != that){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let r = new Report();
                        r.InitExecuteReport(3,self,``);
                        let res = a.SelectTarget(self,4,false,true,2,false,false,that,"");
                        for(let i in res){
                            let damage = c.CountDamage(self,res[i],damageValue,'',false,that,null);
                            a.GetHurt(self,res[i],damage,false,'',that,true,null);
                        }
                    }
                }
            }
            let block = new AfterDamageBlock(self,this,action,99,true,true,true,true,true,true,true);
            block.Init(self,'');
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 68,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let u = new Util();
            let value1 = 100;//
            let value2 = 25;//

            //
            let block1 = new AttrBlock(self,this,'',value1,99);
            let work1 = block1.Init(self,false);
            if(work1){
                let r = new Report();
                r.InitExecuteReport(2,self,`${value1}(${q.GetAttr(self,'')})`);
            }
            let block2 = new AttrBlock(self,this,'',value2,99);
            let work2 = block2.Init(self,false);
            if(work2){
                let r = new Report();
                r.InitExecuteReport(2,self,`${value2}(${q.GetAttr(self,'')})`);
            }
            
            let action = function(){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
                u.ClearWhichTypeTacticsInitStatus(self,self,'',false,that,true,1,3);
                //
                for(let i in self.EXECUTE){
                    if(self.EXECUTE[i].tactics.name == '' && self.EXECUTE[i].duration > 0){
                        let r = new Report();
                        r.InitExecuteReport(3,self,``);
                        self.EXECUTE[i].duration-=99;
                    }
                }
            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self,true);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 83,
        name : "",
        type : "",
        odd : 60,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self,target){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageValue = q.GetAdditionValue(self,233,'',2.1);//
            if(self.driver.version == ''){
                damageValue = q.GetAdditionValue(self,210,'',2);
            }
            
            //
            let damage = c.CountDamage(self,target,damageValue,"",false,this,null);
            a.GetHurt(self,target,damage,false,"",this,true);
            //
            let block = new StatusBlock(self,this,'',2,0);
            block.Init(target,true);
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 93,
        name : "",
        type : "",
        odd : 35,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = 210;

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            //
            let all = a.SelectTarget(self,realRange,false,true,3,false,true,this,"");
            if(all != []){
                let lowDe = all[0];
                let lowHp = all[0];
                for(let i in all){
                    if(q.GetAttr(all[i],'') < q.GetAttr(lowDe,'')) lowDe = all[i];
                    else if(q.GetAttr(all[i],'') == q.GetAttr(lowDe,'')){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1) lowDe = all[i];
                    }
                    if(all[i].hp < lowHp.hp) lowHp = all[i];
                    else if(all[i].hp == lowHp.hp){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1) lowHp = all[i];
                    }
                }
                let res = [lowDe,lowHp];
                for(let i in res){
                    let damage = c.CountDamage(self,res[i],damageValue,'',false,this,null);
                    a.GetHurt(self,res[i],damage,false,'',this,true,null);
                }
            }

            //
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            for(let i in res){
                let block = new StatusBlock(self,this,"",2);
                block.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 100,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let value = 100;
            let odd = 50;

            let highestAd = self.enemy[0];
            let highestTact = self.enemy[0];
            for(let i in self.enemy){
                if(q.GetAttr(self.enemy[i],'') > q.GetAttr(highestAd,'')) highestAd = self.enemy[i];
                else if(q.GetAttr(self.enemy[i],'') == q.GetAttr(highestAd,'')){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1) highestAd = self.enemy[i];
                }
                if(q.GetAttr(self.enemy[i],'') > q.GetAttr(highestTact,'')) highestTact = self.enemy[i];
                else if(q.GetAttr(self.enemy[i],'') == q.GetAttr(highestTact,'')){
                    let rand = Math.floor(Math.random()*2);
                    if(rand == 1) highestTact = self.enemy[i];
                }
            }
            let block1 = new AttrBlock(self,this,'',-value,3);
            block1.Init(highestAd,true,2);
            let block2 = new AttrBlock(self,this,'',-value,3);
            block2.Init(highestTact,true,2);
            let res = a.SelectTarget(self,5,false,true,2,false,false,this,"");
            for(let i in res){
                let action = function(){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let type = "";
                        let randNumber = Math.floor(Math.random()*2);
                        if(randNumber == 1) type = ''
                        let block = new StatusBlock(self,that,type,1,0);
                        let work = block.Init(res[i],false);
                        if(work){
                            let r = new Report();
                            r.InitTwoTargetReport(3,self,res[i],``,`${type}`);
                        }
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(3,self,`${realOdd}%`);
                    }
                }
                let block1 = new AttackExecuteBlock(self,this,action,4,0,true);
                block1.Init(res[i],false);
                let block2 = new ActiveTacticsBlock(self,this,action,99,0,true,'');
                block2.Init(res[i],false);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 109,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let cureValue = 196;
            let cureIndex = 2;
            let value = q.GetAdditionValue(self,10,'',0.06);
            
            let realRange = q.GetTacticsRange(self,this);
            let tag = "" + self.belong + self.position;
            let action = function() {
                if(self[tag] < 6){
                    self[tag] += 1;
                    let r = new Report();
                    r.InitExecuteReport(3,self,`${self[tag]}`)
                }
                else{
                    self[tag] = 0;
                    let res = a.SelectTarget(self,realRange,true,false,2,true,true,that,"");
                    for(let j in res){
                        let cure = c.CountCure(self,res[j],cureValue,cureIndex,that);
                        a.GetCure(self,res[j],cure,that,false);
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,res[j],``,`${cure}(${res[j].hp})`);
                    }
                }
                //
                let res = a.SelectTarget(self,realRange,false,true,1,false,true,that,"");
                for(let i in res){
                    let avail = true;
                    for(let j in res[i].DAMAGE_VAR){
                        if(res[i].DAMAGE_VAR[j].tactics == that){
                            res[i].DAMAGE_VAR[j].value -= value;
                            let r = new Report();
                            r.InitTwoTargetReport(3,self,res[i],``,`${Math.abs(res[i].DAMAGE_VAR[j].value)}%`);
                            avail = false;
                        }
                    }
                    if(avail){
                        let block = new DamageBlock(self,that,'',true,-value,true,false,99,0);
                        let work = block.Init(res[i],null,false);
                        if(work){
                            let r = new Report();
                            r.InitTwoTargetReport(3,self,res[i],``,`${value}%`);
                        }
                    }
                }
            }
            for(let i in self.ally){
                let target = self.ally[i];
                let block1 = new AttackExecuteBlock(self,this,action,99,0,true);
                block1.Init(target,false);
                let block2 = new ActiveTacticsBlock(self,this,action,99,0,true,'');
                block2.Init(target,false);
                let r = new Report();
                r.InitExecuteReport(2,target,``);
            }
            self[tag] = 0;
            this.STATISTICS.times += 1;
        }
    },
]

export default weiAArr;