//
class MakeDamageBlock {
    constructor(applicator, tactics, execute, duration) {
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.execute = execute;
        //
        this.duration = duration;
        //
        this.beExecuted = null;
        //
        this.damageValue = 0;
        //
        this.damageToWho = null;
        //
        this.damageFromTactics = null;
        //
        this.isCommonAttack = false;
        //
        this.damageType = "";
        //
        this.type = "";
        //
        this.isAbnormal = false;
    }
    Init(target, type) {
        //
        this.type = type;
        this.beExecuted = target;
        target.MAKE_DAMAGE.push(this);
        return true;
    }
    Execute(damageValue, damageToWho, damageFromTactics, isCommonAttack, damageType) {
        this.damageValue = damageValue
        this.damageToWho = damageToWho
        this.damageFromTactics = damageFromTactics
        this.isCommonAttack = isCommonAttack
        this.damageType = damageType;
        this.execute();
    }
}
export default MakeDamageBlock;