import Report from "../js/report";
import Attack from "../js/attack";
import Count from "../js/count";
import Query from "../js/query";
import StatusBlock from "../block/status";
import DamageBlock from "../block/damage";
import DamageStatus from "../block/damageStatus";
import AfterDamageBlock from "../block/execute1";
let shuBArr = [
    //
    {
        id : 35,
        name : "",
        type : "",
        odd : 35,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let value1 = q.GetAdditionValue(self,35,'',0.22);//
            let value2 = q.GetAdditionValue(self,14,'',0.1); //

            
            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(3,self,this);
            let res = a.SelectTarget(self,realRange,true,false,realTargetNumber,true,true,this,"");
            //
            for(let i in res){
                let block = new DamageBlock(self,this,"",false,-value1,false,false,2);
                block.Init(res[i]);
                let block1 = new DamageBlock(self,this,"",true,value2,false,false,2);
                block1.Init(res[i]);
                let block2 = new DamageBlock(self,this,"",true,value2,false,false,2);
                block2.Init(res[i]);
            }
            //
            let block = new StatusBlock(self,this,"",2);
            block.Init(self);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 38,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = 190;
            let damageValue2 = 35;
            let odd = 75;//

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(2,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            let action = function(){
                if(that == this.damageFromTactics) return;
                let target = this.beExecuted;
                //
                let refresh = true;
                for(let i in target.SPECIAL_STATUS){
                    if(target.SPECIAL_STATUS[i].tactics == that) refresh = false;
                }
                if(!refresh){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let damage = c.CountDamage(self,target,damageValue2,"",false,that,"");
                        let work = a.GetHurt(self,target,damage,false,"",that,false);
                        if(work){
                            let r = new Report();
                            r.InitExecuteReport(3,target,`${damage}(${target.hp})`)
                        }
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(3,target,`${realOdd}%`)
                    }
                }
                else{
                    //
                    let temp = [];
                    for(let i in target.AFTER_DAMAGE){
                        if(target.AFTER_DAMAGE[i].tactics != that) temp.push(target.AFTER_DAMAGE[i]);
                    }
                    target.AFTER_DAMAGE = temp;
                }
            }
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true,null);
            }
            for(let i in res){
                //
                let block = new DamageStatus(self,this,"",damageValue2,"",1,0,false);
                let work = block.Init(res[i],true);
                if(work){
                    let block1 = new AfterDamageBlock(self,this,action,1,true,true,true,true,true,true,true);
                    block1.Init(res[i],"");
                }
            }
            this.STATISTICS.times += 1;
        }
    }
]

export default shuBArr;