import Attack from '../js/attack.js';
import Report from '../js/report.js'
import DamageStatus from "../block/damageStatus.js";
import Query from '../js/query.js';
import Count from '../js/count.js';
import AttrBlock from '../block/attr.js';
import DodgeBlock from '../block/dodge.js';
import StatusBlock from '../block/status.js';
import DamageBlock from '../block/damage.js';
import AttackExecuteBlock from '../block/execute4.js';
import ActiveTacticsBlock from '../block/execute3.js';
import ExecuteBlock from '../block/execute.js';
import CounterAttackBlock from '../block/counterAtk.js';
import AfterDamageBlock from '../block/execute1.js';
import Judge from '../js/judge.js';
import DamageCureDiscountBlock from '../block/discount.js';
import OddBlock from '../block/odd.js';
import RangeBlock from "../block/range";
import Util from '../js/util.js';
import BeforeMakeDamageBlock from '../block/execute2.js';
import CureStatusBlock from '../block/cureStatus.js';
import IgnoreBlock from '../block/ignore.js';
import MakeDamageBlock from '../block/execute6.js';
let passiveS = [
    //
    {
        id : 0,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let odd = 50;//
            let cureValue = 200;
            let cureIndex = 0;

            let begin = function(){
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let r = new Report();
                    r.InitExecuteReport(2,self,``);
                    let res = a.SelectTarget(self,3,true,false,2,false,false,that);
                    for(let i in res){
                        let block = new StatusBlock(self,that,'',1,0);
                        block.Init(res[i],true,3);
                    }
                }
                else{
                    let r = new Report();
                    r.InitExecuteReport(2,self,`${realOdd}%`);
                }
            }
            let getHurt = function(){
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let cure = c.CountCure(self,self,cureValue,cureIndex,that);
                    let r = new Report().InitTwoTargetReport(3,self,self,``,`${cure}(${self.hp + cure})`,false)
                    a.GetCure(self,self,cure,that,true,r);
                }
                let block = new DodgeBlock(self,that,99,50);
                block.Init(self,false);
                let r = new Report();
                r.InitTwoTargetReport(3,self,self,``,`()`);
                let realOdd2 = q.GetMoraleOdd(self,odd);
                let rand2 = Math.floor(Math.random()*100);
                if(rand2 < realOdd2){
                    let r = new Report();
                    r.InitExecuteReport(3,self,``);
                    let u = new Util();
                    u.ClearWhichTypeTacticsInitStatus(self,self,'',false,that,true,1,3);
                    u.ClearWhichTypeTacticsInitStatus(self,self,'',false,that,true,1,3);
                    // u.ClearStatus(self,that,false,3);
                }
                else{
                    let r = new Report();
                    r.InitExecuteReport(3,self,`${realOdd2}%`);
                }
            }
            let block = new ExecuteBlock(self,this,'',begin,99);
            block.Init(self,false);
            let block1 = new AfterDamageBlock(self,this,getHurt,99,true,false,true,false,false,false,false);
            block1.Init(self,'')
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 1,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let odd = 65;//
            let value1 = 50;//
            let value2 = 60;//

            let begin = function(){
                let realOdd1 = q.GetMoraleOdd(self,odd);
                let rand1 = Math.floor(Math.random()*100);
                if(rand1 < realOdd1){
                    let block = new DamageBlock(self,that,'',true,value1,false,false,0,0);
                    let work = block.Init(self,null,false);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(2,self,self,``,`${value1}%`);
                    }
                }
                let realOdd2 = q.GetMoraleOdd(self,odd);
                let rand2 = Math.floor(Math.random()*100);
                if(rand2 < realOdd2){
                    let block = new IgnoreBlock(self,that,"",value2,0,0,null);
                    let work = block.Init(self,false);
                    if(work){
                        let r = new Report();
                        r.InitTwoTargetReport(2,self,self,``,`${value2}%`)
                    }
                }
            }
            
            let block = new ExecuteBlock(self,this,'',begin,99);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 19,
        name : "",
        type : "",
        odd : 100,
        range : 1,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : ['','',""],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let c = new Count();
            let value = 20;//
            let cureValue = 200;
            let cureIndex = 0;
            let maxTime = 3;//


            if(self.position == '0'){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
            }
            else{
                let tag = '' + self.belong + self.position;
                let block1 = new DamageBlock(self,this,'',false,-value*maxTime,false,false,99,0);
                block1.Init(self,null,true,2);
                let block2 = new DamageBlock(self,this,'',false,-value*maxTime,false,false,99,0);
                block2.Init(self,null,true,2);
                self[tag] = maxTime;
                let getHurt = function(){
                    if(self[tag] > 0){
                        self[tag]-=1;
                        //
                        let cure = c.CountCure(self,self,cureValue,cureIndex,that);
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,self,``,`${cure}(${self.hp + cure})`,false);
                        a.GetCure(self,self,cure,that,true,r);
                        for(let i in self.DAMAGE_VAR){
                            if(self.DAMAGE_VAR[i].tactics == that){
                                self.DAMAGE_VAR[i].value = -value*self[tag];
                                let r = new Report();
                                let txt = `${self.DAMAGE_VAR[i].damageType}${Math.abs(self.DAMAGE_VAR[i].value)}%`;
                                r.InitTwoTargetReport(3,self,self,``,txt);
                            }
                        }
                    }
                }
                let block = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
                block.Init(self,'');
                let makeDamage = function(){
                    if(self[tag] < maxTime){
                        self[tag]+=1;
                        for(let i in self.DAMAGE_VAR){
                            if(self.DAMAGE_VAR[i].tactics == that){
                                self.DAMAGE_VAR[i].value = -value*self[tag];
                                let r = new Report();
                                let txt = `${self.DAMAGE_VAR[i].damageType}${Math.abs(self.DAMAGE_VAR[i].value)}%`;
                                r.InitTwoTargetReport(3,self,self,``,txt);
                            }
                        }
                    }
                }
                let make = new MakeDamageBlock(self,this,makeDamage,99);
                make.Init(self,'');
                let r = new Report();
                r.InitExecuteReport(2,self,``);
            }
            this.STATISTICS.times += 1;
        }
    },
]

export default passiveS;