import AttrBlock from "../block/attr";
import CounterAttackBlock from "../block/counterAtk";
import DamageBlock from "../block/damage";
import ExecuteBlock from "../block/execute";
import BeforeMakeDamageBlock from "../block/execute2";
import AttackExecuteBlock from "../block/execute4";
import ExecuteSysBlock from "../block/executeSys";
import RangeBlock from "../block/range";
import StatusBlock from "../block/status";
import Judge from "../js/judge";
import Query from "../js/query";
import Report from "../js/report";
import Util from "../js/util";
import Tactics from "./tactics";

//
class ArmSpec {
    constructor() {
        this.specType = ""
        this.armTypeMap = {
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": "",
            "": ""
        };
    }
    //
    ProcessEliteUnit(target, specName) {
        let spec = new Tactics(target);
        spec.type = '' + this.specType + specName;
        spec.name = specName;
        if (specName == "") {
            let value = 8;
            let block = new DamageBlock(target, spec, '', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("")
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == "") {
            let value = 8;
            if (self.position == '2') {
                value = 12;
            }
            let block = new DamageBlock(target, spec, '', false, -value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("")
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == '') {
            let value = 6;
            let block = new DamageBlock(target, spec, '', false, -value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("")
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == '') {
            let value = 15;
            let block = new AttrBlock(target, spec, '', -value, 99, false);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("")
                r.InitExecuteReport(2, target, `${value}`);
            }
        }
        else if (specName == '') {
            let value = 12;
            let block1 = new DamageBlock(target, spec, '', false, -value, false, false, 99, 0);
            let work1 = block1.Init(target, null, false, 2);
            if (work1) {
                let r = new Report("")
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == '') {
            let block = new DamageBlock(target, spec, '', false, -30, false, false, 99, 0);
            let work = block.Init(target, null, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
            let action = function () {
                if(target.driver.turn < 1) return;
                let tgt = this.beExecuted;
                for (let i in tgt.SPECIAL_STATUS) {
                    if (tgt.SPECIAL_STATUS[i].type == '' || tgt.SPECIAL_STATUS[i].type == '') {
                        let block = new StatusBlock(target, spec, '', 0, 0);
                        let work = block.Init(target, false);
                        if (work) {
                            let r = new Report("");
                            r.InitTwoTargetReport(2, target, target, ``, ``);
                            break;
                        }
                    }
                }
            }
            let block1 = new ExecuteBlock(target, spec, '', action, 99);
            block1.Init(target, false);
            let r = new Report("");
            r.InitExecuteReport(2, target, ``);
        }
        else if (specName == '') {
            let value = 30;
            let action = function () {
                if(target.driver.turn < 1) return;
                if(this.statusDamageType == null || (this.statusDamageType != "" && this.statusDamageType != "")){
                    let block = new DamageBlock(target,spec,'',false,-value,true,false,0,0);
                    let work =  block.Init(target,null,false);
                    if(work){
                        let r = new Report("");
                        r.InitTwoTargetReport(3,target,target,``,`${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }

        }
        else if (specName == '') {
            let value = 18;
            let block = new DamageBlock(target, spec, '', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }

        else if (specName == '') {
            let block = new StatusBlock(target, spec, '', 2, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let block = new RangeBlock(target, spec, true, 1, 99, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `+1`);
            }
        }
        else if (specName == "") {
            let value = 8;
            let block = new DamageBlock(target, spec, '', true, value, false, false, 99, 0);
            let work = block.Init(target, null, false, 2);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == "") {
            let block = new RangeBlock(target, spec, true, -1, 99, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `-1`);
            }
        }
        else if (specName == "") {
            let value = 18;
            for (let i = 0; i < 3; i++) {
                let attrType = ''
                switch (i) {
                    case 0: attrType = ''; break;
                    case 1: attrType = ''; break;
                    case 2: attrType = ''; break;
                }
                let block = new AttrBlock(target, spec, attrType, value, 99, false);
                block.Init(target, false);
            }
            let r = new Report("");
            r.InitExecuteReport(2, target, `${value}`);
        }

        else if (specName == '') {
            let value = 18;
            let block = new AttrBlock(target, spec, '', value , 99, false);
            block.Init(target, false);
            let r = new Report("");
            r.InitExecuteReport(2, target, `${value}%`);
        }
        else if (specName == '') {
            let value = 18;
            for (let i = 0; i < 4; i++) {
                let block = new DamageBlock(target, spec, '', true, value, true, false, 99, 0);
                block.Init(target, null, false);
            }
            let r = new Report("");
            r.InitExecuteReport(2, target, ``);
        }
        else if (specName == '') {
            let value = 25;
            let action = function () {
                if(target.driver.turn < 1) return;
                let j = new Judge();
                if(j.IsGetStatus(target,"") || j.IsGetStatus(target,"") || j.IsGetStatus(target,"") || j.IsGetStatus(target,"")){
                    let block = new DamageBlock(target,spec,'',false,-value,true,false,0,0);
                    let work =  block.Init(target,null,false);
                    if(work){
                        let r = new Report("");
                        r.InitTwoTargetReport(3,target,target,``,`${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == '') {
            let block = new StatusBlock(target, spec, '', 99, 0);
            block.Init(target, false);
            let block1 = new DamageBlock(target, spec, '', false, -10, false, false, 99, 0);
            let work1 = block1.Init(target, null, false, 2);
            if (work1) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let value = 75;
            let block = new CounterAttackBlock(target, spec, '', value, 100, 2, 0);
            block.Init(target, false);
            let r = new Report("");
            r.InitExecuteReport(2, target, ``);
        }
        else if (specName == "") {
            let block = new RangeBlock(target, spec, false, 1, 99, 0);
            block.Init(target, false);
            let r = new Report("");
            r.InitExecuteReport(2, target, ``);
        }
    }
    //
    ProcessOptionalUnit(target, specName) {
        let spec = new Tactics(target);
        spec.type = this.specType + specName;
        spec.name = specName;
        if (specName == "") {
            let value = 15;
            let action = function () {
                if(target.driver.turn < 1) return;
                if (target.driver.turn >= 3) {
                    let block = new DamageBlock(target, spec, '', true, value, true, false, 1, 0);
                    let work = block.Init(target, null, false);
                    if (work) {
                        let r = new Report("");
                        r.InitTwoTargetReport(0, target, target, '', `${value}%`);
                    }
                }
            }
            let block = new ExecuteSysBlock(target, spec, "", action, 99);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let value = 15;
            let action = function () {
                if(target.driver.turn < 1) return;
                if ((this.attacker.unitType == '' || this.attacker.unitType == '') && this.damageType == '') {
                    let block = new DamageBlock(target, spec, '', false, -value, true, false, 0, 0);
                    let work = block.Init(target, null, false);
                    if(work){
                        let r = new Report("");
                        r.InitTwoTargetReport(3,target,target,``,`${value}%`)
                    }
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let count = 0;
            for (let i in target.TacticsArr) {
                if (target.TacticsArr[i].type != 'active') {
                    count++;
                }
            }
            let value = 22 * count;
            let block = new AttrBlock(target, spec, "", value, 99, true);
            block.Init(target, false);

            if (value > 0) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `${value}`);
            }

        }
        else if (specName == "") {
            let value = 10;
            let block = new DamageBlock(target, spec, '', true, value, false, false, 99, 0);
            let work = block.Init(target, spec, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `${value}%`);
            }
        }
        else if (specName == "") {
            let value = 16;
            let block = new DamageBlock(target, spec, '', false, -value, false, false, 99, 0);
            let work = block.Init(target, spec, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, `16%`);
            }
        }

        else if (specName == "") {
            let j = new Judge();
            let value = 18;
            let action = function () {
                if(target.driver.turn < 1) return;
                if (j.IsMaxTroopsInTeam(this.receiver)) {
                    let block = new DamageBlock(target, spec, '', true, value, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("");
                    r.InitTwoTargetReport(3, target, target, ``, `${value}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let value = 10;
            let action = function () {
                if(target.driver.turn < 1) return;
                if (this.receiver.position != 2) {
                    let block = new DamageBlock(target, spec, '', true, value, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("");
                    r.InitTwoTargetReport(3, target, target, ``, `${value}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let block = new StatusBlock(target, spec, '', 3, 0);
            let work = block.Init(target, false);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let value = 85;
            let action = function(){
                let block = new DamageBlock(target, spec, '', false, -value, false, false, 1, 0);
                let work = block.Init(target, null, false);
                if (work) {
                    let r = new Report("");
                    r.InitTwoTargetReport(1,target,target,``,`${value}%`);
                }
            }
            let exeBlock = new ExecuteBlock(target,spec,'',action,1);
            exeBlock.Init(target,true);
        }
        else if (specName == "") {
            let value = 15;
            let action = function () {
                if(target.driver.turn < 1) return;
                if (this.isCommonAttack) {
                    let block = new DamageBlock(target, spec, '', false, -value, true, false, 0, 0);
                    block.Init(target, null, false);
                }
                else if (this.originTactics.type == '') {
                    let block = new DamageBlock(target, spec, '', false, -value, true, false, 0, 0);
                    block.Init(target, null, false);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, false);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }

        else if (specName == "") {
            let value = 15;
            let block = new DamageBlock(target, spec, '', true, value, false, false, 3, 0);
            let work = block.Init(target, null, false);
            if (work) {
                let r = new Report("")
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let value = 24;
            let str = ""
            if (target.position == 2) {
                let block = new AttrBlock(target, spec, "", value, 99, true, true);
                block.Init(target, false);
            }
            else if (target.position == 1) {
                value = 10;
                let block1 = new AttrBlock(target, spec, "", value, 99, true, true);
                block1.Init(target, false);
                let block2 = new AttrBlock(target, spec, "", value, 99, true, true);
                block2.Init(target, false);
                str += ""
            }
            else {
                value = 6;
                let strList = ["", "", ""];
                for (let i in strList) {
                    let block = new AttrBlock(target, spec, strList[i], value, 99, true, true);
                    block.Init(target, false);
                }
                str += ''
            }
            let r = new Report("")
            r.InitExecuteReport(2, target, `${str}`);
        }
        else if (specName == "") {
            let value = 10;
            let block = new DamageBlock(target,spec,'',false,-value,false,false,99,0);
            let work =  block.Init(target,null,false);
            if(work){
                let r = new Report("");
                r.InitExecuteReport(2,target,`${value}%`);
            }
        }
        else if (specName == "") {
            let q = new Query();
            let value = 10;
            let action = function () {
                if(target.driver.turn < 1) return;
                let distance = q.GetDistance(this.attacker,this.receiver);
                let finalValue = value - 2 * distance;
                if(finalValue > 0){
                    let block = new DamageBlock(target, spec, '', true, finalValue, true, false, 0, 0);
                    block.Init(target, null, false);
                    let r = new Report("");
                    r.InitTwoTargetReport(3, target, target, ``, `${finalValue}%`);
                }
            }
            let block = new BeforeMakeDamageBlock(target, spec, action, 99, true);
            let work = block.Init(target);
            if (work) {
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
        }
        else if (specName == "") {
            let damageValue = 40;//
            let block = new CounterAttackBlock(target,spec,"",damageValue,100,99,0);
            let work = block.Init(target,false);
            if(work){
                let r = new Report("");
                r.InitExecuteReport(2, target, ``);
            }
            let action = function(){
                block.SelfDestroy();
                let r = new Report("");
                r.InitExecuteReport(3,target,``)
                this.SelfDestroy();
            }
            let exeBlock = new AttackExecuteBlock(target,spec,action,99,0,true,"");
            exeBlock.Init(target,true);
            
        }

    }
    Init(warrior) {
        //
        warrior.unitType = this.armTypeMap[warrior.upArms[warrior.choseUpArmsIdx]];
        //
        for (let i in warrior.armsSpec[warrior.choseUpArmsIdx]) {
            this.ProcessEliteUnit(warrior, warrior.armsSpec[warrior.choseUpArmsIdx][i].name);
        }
        //
        for (let i in warrior.armsCommonSpec[warrior.choseUpArmsIdx]) {
            if (i == warrior.choseArmsCharacterIdx[0] || i == warrior.choseArmsCharacterIdx[1])
                this.ProcessOptionalUnit(warrior, warrior.armsCommonSpec[warrior.choseUpArmsIdx][i].name)
        }
    }


}

export default ArmSpec;