                self[tag] += 1;
                if(self[tag] >= time){
                    let r = new Report();
                    r.InitExecuteReport(3,self,``);
                    self[tag1] = true;
                    self[tag] -= time;
                }
                else{
                    let r = new Report();
                    r.InitExecuteReport(3,self,`${self[tag]}`)
                }
