import CureStatusBlock from "../block/cureStatus";
import Attack from "../js/attack";
import Count from "../js/count";
import Query from "../js/query";
import DodgeBlock from '../block/dodge.js'
import Report from "../js/report";
import Judge from "../js/judge.js";
import ExecuteBlock from "../block/execute.js";
import DamageBlock from "../block/damage.js";
import BeforeMakeDamageBlock from "../block/execute2.js";
import StatusBlock from "../block/status.js";
import Util from "../js/util.js";

let hanBArr = [
    //
    {
        id : 8,
        name : "",
        type : "",
        odd : 35,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : false,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let cureValue1 = 157;//
            let cureValue2 = 206;//
            let cureIndex1 = 1;//
            let cureIndex2 = 1.875;//
            if(self.driver.version == ''){
                cureValue2 = 230;//
            }

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,true,false,realTargetNumber,false,true,this,"");
            for(let i in res){
                let cure = c.CountCure(self,res[i],cureValue1,cureIndex1,this);
                a.GetCure(self,res[i],cure,this,true);
                let cureBlock = new CureStatusBlock(self,this,"",cureValue2,cureIndex2,1,0,100);
                cureBlock.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 11,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let j = new Judge();
            let odd = 50;//
            let index = q.GetAdditionValue(self,30,'',0.15);//
            if(self.driver.version == ''){
                index = q.GetAdditionValue(self,40,'',0.18);
            }

            let realRange = q.GetTacticsRange(self,this);
            let friRes = a.SelectTarget(self,realRange,true,false,3,true,true,this,"");
            let res = a.SelectTarget(self,realRange,false,true,3,false,true,this,"");
            //
            for(let i in friRes){
                let block = new DodgeBlock(self,this,99);
                block.Init(friRes[i],false);
                let r = new Report();
                r.InitExecuteReport(2,friRes[i],`()`);
            }
            let action1 = function(){
                if(this.damageType == ''){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let target = this.beExecuted;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,'',`${index}%`)
                        let block = new DamageBlock(self,this.tactics,"",true,-index,true,false,0);
                        block.Init(target,null,false);
                    }
                }
            }
            let action2 = function(){
                if(this.damageType == ''){
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let target = this.beExecuted;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,'',`${index}%`)
                        let block = new DamageBlock(self,this.tactics,"",true,-index,true,false,0);
                        block.Init(target,null,false);
                    }
                }
            }
            //
            for(let i in res){
                //
                if(j.IsExistDamageBlock(res[i],"","",true,false)){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
                else{
                    let block = new BeforeMakeDamageBlock(self,this,action1,4);
                    block.Init(res[i]);
                }
                if(j.IsExistDamageBlock(res[i],"","",true,false)){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
                else{
                    let block = new BeforeMakeDamageBlock(self,this,action2,4);
                    block.Init(res[i]);
                }
            }


            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 8,
        name : "",
        type : "",
        odd : 30,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageIndex = 150;//
            if(self.driver.version == ''){
                damageIndex = 175;
            }

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,true,false,3,true,true,this,"");
            for(let i in res){
                let r = new Report();
                r.InitExecuteReport(3,res[i],``);
                let targetArr = a.SelectTarget(res[i],5,false,true,realTargetNumber,false,true,this,"");
                if(targetArr.length > 0){
                    let damage = c.CountDamage(res[i],targetArr[0],damageIndex,"",false,this,null);
                    a.GetHurt(res[i],targetArr[0],damage,false,"",this,true,null);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 22,
        name : "",
        type : "",
        odd : 30,
        range : 3,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = q.GetAdditionValue(self,85,'',1.2);//
            let index = q.GetAdditionValue(self,49,'',0.12);//
            if(self.driver.version == ''){
                damageValue = q.GetAdditionValue(self,40,'',1);//
                index = q.GetAdditionValue(self,60,'',0.18);//
            }

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true);
            }
            //
            let block1 = new DamageBlock(self,this,"",false,-index,false,false,2);
            block1.Init(self,null);
            let block2 = new DamageBlock(self,this,"",false,-index,false,false,2);
            block2.Init(self,null);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 41,
        name : "",
        type : "",
        odd : 80,
        range : 3,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageValue = 355;//
            if(self.driver.version == ''){
                damageValue = 500 + (self.driver.turn-1) * 50;
            }

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(1,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");

            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true,null);
            }
            //
            let block = new StatusBlock(self,this,"",1,0);
            let work = block.Init(self,false);
            if(work){
                let r = new Report();
                r.InitExecuteReport(3,self,``);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 43,
        name : "",
        type : "",
        odd : 120,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : false,
        selectEnemy : false,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let cureValue = 63;
            let cureIndex = 0.6;
            let maxCure = 1200;
            let maxCureIndex = 0;
            let odd = 35;

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(2,self,this);
            let res = a.SelectTarget(self,realRange,true,false,realTargetNumber,false,true,this,"");
            for(let i in res){
                let cure = c.CountCure(self,res[i],cureValue,cureIndex,this);
                a.GetCure(self,res[i],cure,this,true);
                let realOdd = q.GetMoraleOdd(self,odd);
                let rand = Math.floor(Math.random()*100);
                if(rand < realOdd){
                    let r = new Report();
                    r.InitExecuteReport(3,self,``);
                    let cure = c.CountCure(self,res[i],maxCure,maxCureIndex,this);
                    a.GetCure(self,res[i],cure,this,true);
                }
            }


            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 72,
        name : "",
        type : "",
        odd : 35,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : [1,2],
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue = q.GetAdditionValue(self,150,'',1.5);
            let targetNumber = 1 + Math.floor(Math.random()*2);

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,targetNumber,false,true,this,"");
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,'',false,this,null);
                a.GetHurt(self,res[i],damage,false,'',this,true,null);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 135,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let j = new Judge();
            let odd = 40;//
            let add_plus = 3;//
            let index = q.GetAdditionValue(self,30,'',0.15);
            let duration = 99;//

            let tag = '' + self.belong + self.position;
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,3,false,true,this,"");
            let action1 = function(){
                if(this.damageType == ''){
                    let realOdd = q.GetMoraleOdd(self,odd + this.beExecuted[tag]*add_plus);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let target = this.beExecuted;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,'',`${index}%`)
                        let block = new DamageBlock(self,this.tactics,"",true,-index,true,false,0);
                        block.Init(target,null,false);
                    }
                }
            }
            let action2 = function(){
                if(this.damageType == ''){
                    let realOdd = q.GetMoraleOdd(self,odd + this.beExecuted[tag]*add_plus);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let target = this.beExecuted;
                        let r = new Report();
                        r.InitTwoTargetReport(3,self,target,'',`${index}%`)
                        let block = new DamageBlock(self,this.tactics,"",true,-index,true,false,0);
                        block.Init(target,null,false);
                    }
                }
            }
            //
            for(let i in res){
                res[i][tag] = 0;
            }
            //
            for(let i in res){
                //
                if(j.IsExistDamageBlock(res[i],"","",true,false)){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
                else{
                    let block = new BeforeMakeDamageBlock(self,this,action1,duration,true);
                    block.Init(res[i]);
                }
                if(j.IsExistDamageBlock(res[i],"","",true,false)){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
                else{
                    let block = new BeforeMakeDamageBlock(self,this,action2,duration,true);
                    block.Init(res[i]);
                }
            }
            let addOdd  = function(){
                let r = new Report();
                r.InitExecuteReport(2,self,``);
                for(let i in res){
                    res[i].camp != '' ? res[i][tag] += 2 : res[i][tag] += 1;
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],`${res[i][tag] * add_plus}%`);
                }
            }
            let block = new ExecuteBlock(self,this,'',addOdd,duration);
            block.Init(self,false);

            this.STATISTICS.times += 1;
        }
    },
    
]

export default hanBArr;