class Report{
    /**
     * 
     * @param {string} fromType "","","",""
     */
    constructor(fromType){
        this.fromType = "";
        if(fromType && fromType.includes('')) this.fromType = ""
        else if(fromType && fromType.includes('')) this.fromType = ""
        else if(fromType == "" || fromType == "" || fromType == "" || fromType == "" || fromType == ""
            || fromType == "" || fromType == ""
        ) this.fromType = "";
        else if(fromType) this.fromType = fromType;
        else this.fromType = ""
        this.type =  '';//
        this.initBelong = "";//
        this.recBelong = "";//
        this.initImg = "";//
        this.recImg = "";//

        this.level = 0,//

        this.text = "";
        this.text1 = '';//1
        this.text2 = '';//2
        this.text3 = "";
        this.text4 = "";
        this.text5 = "";
    }
    //
    InitSysMsg(reportHeap,type,text,isPutReportHeap = true){
        this.type = type;
        this.text = text;
        this.level = 0;
        if(isPutReportHeap) reportHeap.push(this);
        else return this;
    }
    //
    InitExecuteReport(level,target,text,isPutReportHeap = true,type=''){
        this.level = level;
        this.type = type;
        this.initBelong = target.belong;
        this.initImg = target.img.head;
        this.text = text;
        if(isPutReportHeap) target.global.ReportHeap.push(this);
        else return this;
    }
    //
    InitTwoTargetReport(level,target1,target2,text1,text2,isPutReportHeap = true){
        this.type = "";
        this.level = level;
        this.initBelong = target1.belong;
        this.recBelong = target2.belong;
        this.initImg = target1.img.head;
        this.recImg = target2.img.head;
        this.text = text1;
        this.text1 = text2;
        if(isPutReportHeap) target1.global.ReportHeap.push(this);
        else return this;
    }
    //
    PutReportToHeap(reportHeap,target){
        reportHeap.push(target);
    }


    //,typeattackerreceiverdamage
    InitAttackReport(type,attacker,receiver,damage){
        
        this.level = 1;
        if(type == ''){
            //
            //AB{damageBBcurrent_attr.hp}{}
            this.type = '';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = ``
            this.text2 = `${damage}(${receiver.current_attr.hp})`
        }else if(type == 'ballistic_attack'){
            this.type = 'ballistic_attack';
            this.attacker_img = attacker.img.head;
            this.receiver_img = receiver.img.head;
            this.text1 = `${damage}`
            this.text2 = `(${receiver.current_attr.hp})`
        }
    }

    
    
    
    
    
    
    
    
    
    
    
    //
    InitTeamInfo(target){
        this.type = 'info',
        this.level = 0;
        this.initBelong = target.initBelong;
        this.attacker_img = target.img.head;
        this.text1 = `${target.camp}-${target.name}-${target.arms}:${target.origin_attr.hp}`
    }

    //
    InitTacticsPrepareExecute(target,index){
        this.level = 0;
        this.type = 'info',
        this.initBelong = target.initBelong;
        this.attacker_img = target.img.head;
        this.text1 = `${target.TACTICS_STACK[index].info}`;
    }


    

}

export default Report;