import Report from "./report";
class Manage {
    constructor() {

    }
    ManageResistBlock(target) {
        let temp = [];
        for (let i in target.STATUS_RESIST) {
            if (target.STATUS_RESIST[i].duration > 0) {
                temp.push(target.STATUS_RESIST[i]);
                target.STATUS_RESIST[i].duration--;
            }
            else {
                let r = new Report('');
                // let r = new Report(target.STATUS_RESIST[i].tactics.type);
                r.InitExecuteReport(2, target, `${target.STATUS_RESIST[i].tactics.name}`)
            }
        }

        target.STATUS_RESIST = temp;
    }
    ManageAttrAppliedBlock(target) {
        let temp1 = [];
        let temp2 = [];
        for (let i in target.BEFORE_APPLIED_ATTR_EXECUTE) {
            if (target.BEFORE_APPLIED_ATTR_EXECUTE[i].duration > 0) {
                temp1.push(target.BEFORE_APPLIED_ATTR_EXECUTE[i]);
                target.BEFORE_APPLIED_ATTR_EXECUTE[i].duration--;
            }
        }
        for (let i in target.AFTER_APPLIED_ATTR_EXECUTE) {
            if (target.AFTER_APPLIED_ATTR_EXECUTE[i].duration > 0) {
                temp2.push(target.AFTER_APPLIED_ATTR_EXECUTE[i]);
                target.AFTER_APPLIED_ATTR_EXECUTE[i].duration--;
            }
        }
        target.BEFORE_APPLIED_ATTR_EXECUTE = temp1;
        target.AFTER_APPLIED_ATTR_EXECUTE = temp2;
    }
    ManageDamageStatusVarBlock(target) {
        let temp = [];
        for (let i in target.STATUS_DAMAGE_VAR) {
            if (target.STATUS_DAMAGE_VAR[i].duration > 0) {
                temp.push(target.STATUS_DAMAGE_VAR[i]);
                target.STATUS_DAMAGE_VAR[i].duration--;
            }
            else {
                let tgt = target.STATUS_DAMAGE_VAR[i];
                let r = new Report('');
                // let r = new Report(target.STATUS_DAMAGE_VAR[i].tactics.type);
                let txt = `${tgt.tactics.name}${tgt.isMakeDamage ? "" : ""}${tgt.statusType}${tgt.value > 0 ? "" : ""}${Math.abs(tgt.value)}%`
                r.InitExecuteReport(2, target, txt);
            }
        }
        target.STATUS_DAMAGE_VAR = temp;
    }
    ManageCureVarBlock(target) {
        let temp = [];
        for (let i in target.CURE_VAR) {
            if (target.CURE_VAR[i].duration > 0) {
                temp.push(target.CURE_VAR[i]);
                target.CURE_VAR[i].duration--;
            }
            else {
                let r = new Report('');
                // let r = new Report(target.CURE_VAR[i].tactics.type);
                let text = `${target.CURE_VAR[i].tactics.name}${target.CURE_VAR[i].isOutput ? "" : ""}${Math.abs(target.CURE_VAR[i].value)}%`
                r.InitExecuteReport(2, target, text);
            }
        }
        target.CURE_VAR = temp;
    }
    //
    ManageFallIntoDamageStatusExecuteBlock(target) {
        let arr = [];
        let len = target.FALL_INTO_CONTINUE_DAMAGE.length;
        for (let i = 0; i < len; i++) {
            if (!target.FALL_INTO_CONTINUE_DAMAGE[i]['action']) {
                target.FALL_INTO_CONTINUE_DAMAGE[i]['action'] = true;
                if (target.FALL_INTO_CONTINUE_DAMAGE[i].duration > 0) {
                    arr.push(target.FALL_INTO_CONTINUE_DAMAGE[i]);
                }
                target.FALL_INTO_CONTINUE_DAMAGE[i].duration--;
            }
            else arr.push(target.FALL_INTO_CONTINUE_DAMAGE[i]);
            if (target.FALL_INTO_CONTINUE_DAMAGE.length != len) {
                i = 0;
                len = target.FALL_INTO_CONTINUE_DAMAGE.length;
            }
        }
        //action
        for (let i in target.FALL_INTO_CONTINUE_DAMAGE) {
            target.FALL_INTO_CONTINUE_DAMAGE[i]['action'] = false;
        }
        target.FALL_INTO_CONTINUE_DAMAGE = arr;
    }
    //
    ManageFallIntoStatusExecuteBlock(target) {
        let arr = [];
        let len = target.FALL_INTO_STATUS.length;
        for (let i = 0; i < len; i++) {
            if (!target.FALL_INTO_STATUS[i]['action']) {
                target.FALL_INTO_STATUS[i]['action'] = true;
                if (target.FALL_INTO_STATUS[i].duration > 0) {
                    arr.push(target.FALL_INTO_STATUS[i]);
                }
                target.FALL_INTO_STATUS[i].duration--;
            }
            else arr.push(target.FALL_INTO_STATUS[i]);
            if (target.FALL_INTO_STATUS.length != len) {
                i = 0;
                len = target.FALL_INTO_STATUS.length;
            }
        }
        //action
        for (let i in target.FALL_INTO_STATUS) {
            target.FALL_INTO_STATUS[i]['action'] = false;
        }
        target.FALL_INTO_STATUS = arr;
    }
    //
    ManageGetCureExecuteBlock(target) {
        let arr = [];
        let len = target.GET_CURE.length;
        for (let i = 0; i < len; i++) {
            if (!target.GET_CURE[i]['action']) {
                target.GET_CURE[i]['action'] = true;
                if (target.GET_CURE[i].duration > 0) {
                    arr.push(target.GET_CURE[i]);
                }
                target.GET_CURE[i].duration--;
            }
            else arr.push(target.GET_CURE[i]);
            if (target.GET_CURE.length != len) {
                i = 0;
                len = target.GET_CURE.length;
            }
        }
        //action
        for (let i in target.GET_CURE) {
            target.GET_CURE[i]['action'] = false;
        }
        target.GET_CURE = arr;
    }
    //
    ManageTacticsExecutedBlock(target) {
        let temp = [];
        for (let i in target.ACTIVE_TACTICS_EXECUTE) {
            if (target.ACTIVE_TACTICS_EXECUTE[i].duration > 0) temp.push(target.ACTIVE_TACTICS_EXECUTE[i]);
            target.ACTIVE_TACTICS_EXECUTE[i].duration--;
        }
        target.ACTIVE_TACTICS_EXECUTE = temp;
    }
    //
    ManageAttackExecuteBlock(target) {
        let temp = [];
        for (let i in target.LAUNCH_ATTACK_EXECUTE) {
            if (target.LAUNCH_ATTACK_EXECUTE[i].duration > 0) temp.push(target.LAUNCH_ATTACK_EXECUTE[i]);
            target.LAUNCH_ATTACK_EXECUTE[i].duration--;
        }
        target.LAUNCH_ATTACK_EXECUTE = temp;
    }
    //
    ManageIgnoreBlock(target) {
        let temp = [];
        for (let i in target.IGNORE) {
            if (target.IGNORE[i].duration > 0) {
                if (target.IGNORE[i].waitTime > 0) target.IGNORE[i].waitTime--;
                temp.push(target.IGNORE[i]);
            }
            else {
                let r = new Report('');
                // let r = new Report(target.IGNORE[i].tactics.type);
                r.InitExecuteReport(2, target, `${target.IGNORE[i].tactics.name}${target.IGNORE[i].value}%`);
            }
            target.IGNORE[i].duration--;
        }
        target.IGNORE = temp;
    }
    //
    ManageOddBlock(target) {
        let temp = [];
        for (let i in target.ODD_VAR) {
            if (target.ODD_VAR[i].duration > 0) {
                temp.push(target.ODD_VAR[i]);
            }
            else if (target.ODD_VAR[i].value != 0) {
                let r = new Report('');
                // let r = new Report(target.ODD_VAR[i].tactics.type);
                let text = `${target.ODD_VAR[i].workTactics.name}${target.ODD_VAR[i].value > 0 ? "" : ""}${Math.abs(target.ODD_VAR[i].value)}%`;
                r.InitExecuteReport(2, target, text);
            }
            target.ODD_VAR[i].duration--;
        }
        target.ODD_VAR = temp;
    }
    //
    ManageRangBlock(target) {
        let temp = [];
        for (let i in target.RANGE) {
            if (target.RANGE[i].duration > 0) {
                if (target.RANGE[i].waitTime > 0) {
                    target.RANGE[i].waitTime--;
                }
                temp.push(target.RANGE[i]);
            }
            else {
                let text = target.RANGE[i].isCommonAttack ? "" : "";
                if (target.RANGE[i].isBenefit) text += "";
                else text += "";
                text += `(${Math.abs(target.RANGE[i].value)})`
                let r = new Report('');
                // let r = new Report(target.RANGE[i].tactics.type);
                r.InitExecuteReport(2, target, text);
            }
            target.RANGE[i].duration--;
        }
        target.RANGE = temp;
    }
    //
    ManageCounterAttackBlock(target) {
        let temp = [];
        for (let i in target.COUNTER_ATTACK) {
            if (target.COUNTER_ATTACK[i].duration > 0) {
                if (target.COUNTER_ATTACK[i].waitTime > 0) {
                    target.COUNTER_ATTACK[i].waitTime--;
                }
                temp.push(target.COUNTER_ATTACK[i]);
            }
            else {
                let r = new Report('');
                // let r = new Report(target.COUNTER_ATTACK[i].tactics.type);
                r.InitExecuteReport(2, target, `${target.COUNTER_ATTACK[i].tactics.name}${target.COUNTER_ATTACK[i].type}`);
            }
            target.COUNTER_ATTACK[i].duration--;
        }
        target.COUNTER_ATTACK = temp;
    }
    //
    ManageDodgeBlock(target, heighestOddDodge) {
        let arr = [];
        for (let i in target.DODGE) {
            if (target.DODGE[i] != heighestOddDodge) arr.push(target.DODGE[i]);
        }
        target.DODGE = arr;

    }
    //
    ManageWaitTactics(target, tacticsItem) {
        let temp = [];
        let res = {
            launch: null,
            wait: null,
        };
        for (let i in target.WaitTacticsArr) {
            if (tacticsItem == target.WaitTacticsArr[i].tactics) {
                target.WaitTacticsArr[i].waitTime--;
                //
                if (target.WaitTacticsArr[i].waitTime <= 0) {
                    let r = new Report();
                    r.InitExecuteReport(2, target, `${target.WaitTacticsArr[i].tactics.name}!`);
                    target.WaitTacticsArr[i].tactics.execute(target);
                    //res
                    res.launch = target.WaitTacticsArr[i].tactics;
                }
                else {
                    res.wait = target.WaitTacticsArr[i].tactics;
                    temp.push(target.WaitTacticsArr[i]);
                }
            }
            else temp.push(target.WaitTacticsArr[i]);
        }
        target.WaitTacticsArr = temp;
        return res;
    }
    //
    ManageCureStatusBlock(target) {
        let temp = [];
        const CURE_STATUS = [...target.CURE_STATUS];
        for (let i in CURE_STATUS) {
            if (CURE_STATUS[i].duration > 0) {
                if (CURE_STATUS[i].waitTime <= 0) CURE_STATUS[i].Execute(target);
                else CURE_STATUS[i].waitTime--;
                temp.push(CURE_STATUS[i]);
            }
            else {
                let r = new Report('');
                // let r = new Report(CURE_STATUS[i].tactics.type);
                let text = `${CURE_STATUS[i].tactics.name}${CURE_STATUS[i].type}`;
                r.InitExecuteReport(2, target, text);
            }
            CURE_STATUS[i].duration--;
        }
        target.CURE_STATUS = temp;
    }
    //
    ManageDamageStatusBlock(target) {
        let arr = [];
        const SPECIAL_STATUS = [...target.SPECIAL_STATUS];
        for (let i in SPECIAL_STATUS) {
            if (SPECIAL_STATUS[i].duration > 0) {
                if (SPECIAL_STATUS[i].waitTime <= 0) {
                    if (SPECIAL_STATUS[i].isExecute) {
                        //target.SPECIAL_STATUS
                        SPECIAL_STATUS[i].Execute(target);
                    }
                }
                else SPECIAL_STATUS[i].waitTime -= 1;
                arr.push(SPECIAL_STATUS[i]);
            }
            else {
                let r = new Report('');
                // let r = new Report(SPECIAL_STATUS[i].tactics.type);
                let str = `${SPECIAL_STATUS[i].tactics.name}${SPECIAL_STATUS[i].type}`;
                r.InitExecuteReport(2, target, str);
            }
            SPECIAL_STATUS[i].duration -= 1;
        }
        target.SPECIAL_STATUS = arr;
    }
    //
    ManageBeforeMakeDamageBlock(target) {
        let temp = [];
        for (let i in target.BEFORE_MAKE_DAMAGE) {
            if (target.BEFORE_MAKE_DAMAGE[i].duration > 0) temp.push(target.BEFORE_MAKE_DAMAGE[i]);
            target.BEFORE_MAKE_DAMAGE[i].duration--;
        }
        target.BEFORE_MAKE_DAMAGE = temp;
    }
    //
    ManageAfterGetDamagedBlock(target) {
        //
        let temp = [];
        for (let i in target.AFTER_DAMAGE) {
            if (target.AFTER_DAMAGE[i].duration > 0) {
                temp.push(target.AFTER_DAMAGE[i]);
            }
            target.AFTER_DAMAGE[i].duration--;
        }
        target.AFTER_DAMAGE = temp;
        //
        let arr = [];
        for (let i in target.MAKE_DAMAGE) {
            if (target.MAKE_DAMAGE[i].duration > 0) {
                arr.push(target.MAKE_DAMAGE[i]);
            }
            target.MAKE_DAMAGE[i].duration--;
        }
        target.MAKE_DAMAGE = arr;
    }
    //
    ManageExecuteSysBlock(target) {
        let arr = [];
        for (let i in target.EXECUTE) {
            if (target.EXECUTE[i].duration > 0) {
                target.EXECUTE[i].Executed();
                arr.push(target.EXECUTE[i]);
            }
            target.EXECUTE[i].duration--;
        }
        target.EXECUTE = arr;
    }
    //
    ManageExecuteBlock(target, stage) {
        let arr = [];
        let len = target.EXECUTE.length;
        for (let i = 0; i < len; i++) {
            if (target.EXECUTE[i].executeStage == stage && !target.EXECUTE[i]['action']) {
                target.EXECUTE[i]['action'] = true;
                if (target.EXECUTE[i].duration > 0) {
                    target.EXECUTE[i].execute(target.EXECUTE[i]);
                    arr.push(target.EXECUTE[i]);
                }
                target.EXECUTE[i].duration--;
            }
            else arr.push(target.EXECUTE[i]);
            if (target.EXECUTE.length != len) {
                i = 0;
                len = target.EXECUTE.length;
            }
        }
        //action
        for (let i in target.EXECUTE) {
            target.EXECUTE[i]['action'] = false;
        }
        target.EXECUTE = arr;
    }
    //
    ManageAttrBlock(target) {
        let arr = [];
        for (let i in target.ATTR) {
            if (target.ATTR[i].duration > 0) arr.push(target.ATTR[i]);
            else {
                let r = null;
                if (target.ATTR[i].tactics.type == '') r = new Report("");
                else if (target.ATTR[i].tactics.type == '') r = new Report("");
                else r = new Report('');
                // let r = new Report('');
                let text = `${target.ATTR[i].attrType}`
                if (target.ATTR[i].isBenefit) text += ""
                else text += ""
                text += `(${Math.abs(target.ATTR[i].value)})`
                r.InitExecuteReport(2, target, text);
            }
            target.ATTR[i].duration -= 1;
        }
        target.ATTR = arr;
    }
    //
    ManageStatusBlock(target) {
        let arr = [];
        for (let i in target.STATUS) {
            if (target.STATUS[i].duration > 0) {
                if (target.STATUS[i].waitTime > 0) target.STATUS[i].waitTime--;
                arr.push(target.STATUS[i]);
            }
            else {
                if (target.STATUS[i].type != "" && target.STATUS[i].type != "") {
                    let r = new Report('');
                    // let r = new Report(target.STATUS[i].tactics.type);
                    r.InitExecuteReport(2, target, `${target.STATUS[i].tactics.name}${target.STATUS[i].type}`)
                }
            }
            target.STATUS[i].duration -= 1;
        }
        target.STATUS = arr;
    }
    //
    ManageOnceTacticsDamageBlock(target, isMakeDamage) {
        let temp = [];
        for (let i in target.DAMAGE_VAR) {
            if (target.DAMAGE_VAR[i].isOnceTactics && target.DAMAGE_VAR[i].isMakeDamage == isMakeDamage) {
                // let r = new Report(target.DAMAGE_VAR[i].tactics.type);
                let r = new Report('');
                let str1 = target.DAMAGE_VAR[i].isMakeDamage ? "" : "";
                let str2 = "";
                if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                let str3 = "";
                if (target.DAMAGE_VAR[i].value >= 666) str3 += "";
                else if (target.DAMAGE_VAR[i].value >= 0 && target.DAMAGE_VAR[i].value < 666) str3 += '';
                else if (target.DAMAGE_VAR[i].value < -666) str3 += ""
                else str3 += '';
                let str4 = "";
                if (target.DAMAGE_VAR[i].value >= -90) str4 = `${Math.abs(target.DAMAGE_VAR[i].value)}%`
                r.InitExecuteReport(3, target, `${target.DAMAGE_VAR[i].tactics.name}${str1 + str2 + str3 + str4}`);
            }
            else temp.push(target.DAMAGE_VAR[i]);
        }
        target.DAMAGE_VAR = temp;
    }
    //
    ManageOnceDamageBlock(target, damage, isCommonAttack, damageType, tacticsItem, isMakeDamage) {
        let count = [];
        let temp = [];
        // && !(tacticsItem && target.DAMAGE_VAR[i].isOnceTactics)
        for (let i in target.DAMAGE_VAR) {
            let typeControl = false;
            if (damageType == target.DAMAGE_VAR[i].damageType || target.DAMAGE_VAR[i].damageType == '') {
                typeControl = true;
            }
            //
            else if (tacticsItem != null && tacticsItem.type == '' && target.DAMAGE_VAR[i].damageType == '') typeControl = true;
            //
            else if (tacticsItem != null && tacticsItem.type == '' && target.DAMAGE_VAR[i].damageType == '') typeControl = true;
            //
            else if (tacticsItem == null && isCommonAttack && target.DAMAGE_VAR[i].damageType == '') typeControl = true;
            if (target.DAMAGE_VAR[i].isOnceAttack && typeControl
                && target.DAMAGE_VAR[i].isMakeDamage == isMakeDamage
                && !(tacticsItem && target.DAMAGE_VAR[i].isOnceTactics)) {
                //
                let flag = false;
                for (let j in count) {
                    if (count[j].tactics == target.DAMAGE_VAR[i].tactics) {
                        flag = true; break;
                    }
                }
                if (flag) temp.push(target.DAMAGE_VAR[i]);
                //
                else {
                    // let r = new Report(target.DAMAGE_VAR[i].tactics.type);
                    let r = new Report('');
                    let str1 = target.DAMAGE_VAR[i].isMakeDamage ? "" : "";
                    let str2 = "";
                    if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                    let str3 = "";
                    if (target.DAMAGE_VAR[i].value < -666) str3 = "";
                    else if (target.DAMAGE_VAR[i].value > 0) str3 = "";
                    else str3 = "";
                    let str4 = "";
                    if (target.DAMAGE_VAR[i].value >= -666) str4 = `${Math.abs(target.DAMAGE_VAR[i].value)}%`
                    r.InitExecuteReport(3, target, `${target.DAMAGE_VAR[i].tactics.name}${str1 + str2 + str3 + str4}`);
                    if (target.DAMAGE_VAR[i].isOnceAttack || target.DAMAGE_VAR[i].isOnceTactics) count.push(target.DAMAGE_VAR[i]);
                }
            }
            //
            else temp.push(target.DAMAGE_VAR[i]);
        }
        target.DAMAGE_VAR = temp;
    }
    //
    ManageDamageVarBlock(target) {
        let arr = [];
        for (let i in target.DAMAGE_VAR) {
            if (target.DAMAGE_VAR[i].duration > 0) {
                if (target.DAMAGE_VAR[i].waitTime > 0) target.DAMAGE_VAR[i].waitTime--;
                arr.push(target.DAMAGE_VAR[i]);
            }
            else {
                // let r = new Report(target.DAMAGE_VAR[i].tactics.type);
                let r = new Report('');
                let str1 = target.DAMAGE_VAR[i].isMakeDamage ? "" : "";
                let str2 = "";
                if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                let str3 = "";
                if (target.DAMAGE_VAR[i].value < -666) str3 = "";
                else if (target.DAMAGE_VAR[i].value > 0) str3 = "";
                else str3 = "";
                let str4 = ''
                if (target.DAMAGE_VAR[i].value >= -666) str4 = `${Math.abs(target.DAMAGE_VAR[i].value)}%`
                r.InitExecuteReport(2, target, `${target.DAMAGE_VAR[i].tactics.name}${str1 + str2 + str3 + str4}`);
            }
            target.DAMAGE_VAR[i].duration -= 1;
        }
        target.DAMAGE_VAR = arr;
        //
        let temp = [];
        for (let i in target.DISCOUNT) {
            if (target.DISCOUNT[i].duration > 0) {
                if (target.DISCOUNT[i].waitTime > 0) target.DISCOUNT[i].waitTime--;
                temp.push(target.DISCOUNT[i]);
            }
            target.DISCOUNT[i].duration--;
        }
        target.DISCOUNT = temp;
    }
}
export default Manage;