import Report from "../js/report";

class CounterAttackBlock{
    constructor(applicator,tactics,type,damageValue,damageOdd,duration,waitTime){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.type = type;
        //
        this.damageValue = damageValue;
        //
        this.damageOdd = damageOdd;
        //
        this.duration = duration;
        //
        this.waitTime = waitTime;
        this.beExecuted = null;
    }
    Init(target,needReport = true,reportLevel = 3){
        this.beExecuted = target;
        //
        for(let i in target.COUNTER_ATTACK){
            if(target.COUNTER_ATTACK[i].type == this.type){
                let r = new Report();
                r.InitExecuteReport(3,target,`${this.type}`);
                return false;
            }
        }
        target.COUNTER_ATTACK.push(this);
        if(needReport){
            let r = new Report();
            r.InitExecuteReport(reportLevel,target,`${this.type}`);
        }
        return true;
    }
    SelfDestroy(){
        for(let i in this.beExecuted.COUNTER_ATTACK){
            if(this.beExecuted.COUNTER_ATTACK[i] == this){
                this.beExecuted.COUNTER_ATTACK.splice(i,1);
            }
        }
    }
}

export default CounterAttackBlock;