import Report from "../js/report";


export class ResistStatusBlock{
    constructor(applicator,tactics,resistType,odd,duration,waitTime){
        //
        this.applicator = applicator;
        //
        this.tactics = tactics;
        //
        this.resistType = resistType;
        //
        this.odd = odd || 120;
        //
        this.duration = duration;
        this.waitTime = waitTime
        //
        this.beExecuted = null;
    }
    Init(target,isNeedReport=true,reportLevel=3){
        this.beExecuted = target;
        target.STATUS_RESIST.push(this)
        if(isNeedReport){
            let r = new Report();
            r.InitExecuteReport(reportLevel,target,``);
        }
    }   
}