import AttrBlock from "../block/attr";
import StatusBlock from "../block/status";
import Attack from "../js/attack";
import Query from "../js/query";
import ExecuteBlock from '../block/execute.js';
import DamageStatus from "../block/damageStatus";
import Report from "../js/report.js";
import RangeBlock from "../block/range";
import CounterAttackBlock from "../block/counterAtk.js";
import Judge from "../js/judge.js";
import Count from "../js/count.js";
import DamageBlock from "../block/damage.js";
import Util from "../js/util.js";
import AfterDamageBlock from "../block/execute1.js";
let hanAArr = [
    //
    {
        
        id : 10,
        name : "",
        type : "",
        odd : 35,
        range : 4,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 1,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let value = q.GetAdditionValue(self,29,'',0.15);
            if(self.driver.version == ''){
                value = q.GetAdditionValue(self,35,'',0.15);
            }

            let realRange = q.GetTacticsRange(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this,"");
            for(let i in res){
                let block = new StatusBlock(self,this,"",2,0);
                block.Init(res[i]);
            }
            for(let i in res){
                let block = new AttrBlock(self,this,"",-value,2);
                block.Init(res[i]);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 20,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let j = new Judge();
            let damageValue = q.GetAdditionValue(self,59,'',0.5);
            let damageValue1 = 70;//
            let targetNumber = 2;//
            if(self.driver.version == ''){
                damageValue = q.GetAdditionValue(self,75,'',0.5);
                damageValue1 = 100;//
            }

            //
            let res = a.SelectTarget(self,5,false,true,targetNumber,false,true,this,"");
            let block3 = new StatusBlock(self,this,'',1,0);
            block3.Init(self,false);
            for(let i in res){
                let block = new DamageStatus(self,this,"",damageValue,"",99,2,false);
                let work = block.Init(res[i],false);
                if(work){
                    let damage = c.CountDamage(self,res[i],damageValue,'',false,that,'');
                    let damageMake = function(){
                        let tgt = this.beExecuted;
                        if(self.driver.turn > 2 && tgt.alive){
                            let avail = false;
                            for(let j in tgt.SPECIAL_STATUS){
                                if(tgt.SPECIAL_STATUS[j].tactics == that) avail = true;
                            }
                            if(avail){
                                let r = new Report();
                                r.InitExecuteReport(2,res[i],`${damage}(${res[i].hp - damage})`,false);
                                a.GetHurt(self,tgt,damage,false,'',that,false,r,true);
                            }
                        }
                    }
                    let block = new ExecuteBlock(self,that,'',damageMake,99);
                    block.Init(res[i],false);
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`()`)
                }
                
            }
            let block1 = new CounterAttackBlock(self,that,'',damageValue1,100,99,3);
            let work = block1.Init(self,false);
            if(work){
                let r = new Report();
                r.InitExecuteReport(2,self,`()`)
            }
            let block2 = new RangeBlock(self,that,true,1,99,3);
            let work1 =  block2.Init(self,false);
            if(work1){
                let r = new Report();
                r.InitExecuteReport(2,self,`()`)
            }
            let action = function(){
                if(self.driver.turn == 3){
                    if(work){
                        let r = new Report();
                        r.InitExecuteReport(3,self,``);
                    }
                    if(work1){
                        let r = new Report();
                        r.InitExecuteReport(3,self,`1(${q.GetRealAdRange(self)})`);
                    }
                }
            }
            let arr = [];
            for(let i in self.STATUS){
                if(self.STATUS[i].tactics != this.tactics && self.STATUS[i].type == '') arr.push(self.STATUS[i]);
            }
            self.STATUS = arr;
            let block = new ExecuteBlock(self,this,"",action,3);
            block.Init(self);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 49,
        name : "",
        type : "",
        odd : 120,
        range : 4,
        isDamageMaker : true,
        damageMakeType : ["",""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let value1 = q.GetAdditionValue(self,75,'',0.95);//,
            let value2 = q.GetAdditionValue(self,50,'',0.75);//
            let value3 = 100;//

            let realRange = q.GetTacticsRange(self,this);
            for(let j=0;j<3;j++){
                let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
                for(let i in res){
                    let seed = Math.floor(Math.random()*10);
                    let statusType = "";
                    if(seed == 0 || seed == 1){
                        switch(seed){
                            case 0 : statusType = "";break
                            case 1 : statusType = "";break
                        }
                        let block = new DamageStatus(self,this,statusType,value1,"",2,0,true);
                        block.Init(res[i],true);
                    }
                    else if(seed == 2){
                        let block = new DamageStatus(self,this,"",value2,"",2,0,true);
                        block.Init(res[i],true);
                    }
                    else if(seed == 3){
                        let block = new DamageStatus(self,this,"",value3,"",2,0,true);
                        block.Init(res[i],true);
                    }
                    if(seed == 4 || seed == 5 || seed == 6 || seed == 7 || seed == 8 || seed == 9){
                        switch(seed){
                            case 4 : statusType = '';break;
                            case 5 : statusType = '';break;
                            case 6 : statusType = '';break;
                            case 7 : statusType = '';break;
                            case 8 : statusType = '';break;
                            case 9 : statusType = '';break;
                        }
                        let block = new StatusBlock(self,this,statusType,2,0);
                        block.Init(res[i],true);
                    }
                }
            }

            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 73,
        name : "",
        type : "",
        odd : 100,
        range : 2,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue = 191;//
            let index = 2.075;//();
            let targetNumber = 2;//

            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,true,false,targetNumber,true,true,this,"");
            for(let i in res){
                let action = function(){
                    if(self.driver.turn == 2){
                        let targetArr = a.SelectTarget(res[i],4,false,true,2,false,true,that,"");
                        let realDamageValue = q.GetAdditionValue(res[i],damageValue,'',index);
                        let r = new Report();
                        r.InitExecuteReport(2,res[i],``)
                        for(let j in targetArr){
                            let damage = c.CountDamage(res[i],targetArr[j],realDamageValue,'',false,that,null);
                            a.GetHurt(res[i],targetArr[j],damage,false,'',that,true,null);
                        }
                        //
                        let block = new DamageBlock(self,that,'',false,-999,true,false,99,0);
                        block.Init(res[i],null,true);
                    }
                }
                let block = new ExecuteBlock(self,this,'',action,2);
                let work1 =  block.Init(res[i],true);
                if(work1){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],``)
                }
                //
                let status = new StatusBlock(self,this,'',2,0);
                let work2 = status.Init(res[i],false);
                if(work2){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],``)
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 120,
        name : "",
        type : "",
        odd : 30,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let j = new Judge();
            //
            let damageValue = q.GetAdditionValue(self,209,'',2.175);
            let value = q.GetAdditionValue(self,45,'',0.25);
            //
            let damageValue1 = q.GetAdditionValue(self,148,'',1.35);
            //
            let damageValue2 = q.GetAdditionValue(self,155,'',1.5);
            
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
            //
            let r = new Report();
            r.InitExecuteReport(3,self,``);
            let combo = false;
            let combo1 = false;
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true,null);
                if(q.GetAttr(res[i],'') < q.GetAttr(self,'')){
                    combo = true;
                }
                if(j.IsGetStatus(res[i],'')){
                    combo1 = true;
                }
            }
            for(let i in res){
                let block1 = new AttrBlock(self,this,'',-value,2,false);
                block1.Init(res[i],true,3);
                let block2 = new AttrBlock(self,this,'',-value,2,false);
                block2.Init(res[i],true,3);
            }
            //
            if(combo){
                let r = new Report();
                r.InitExecuteReport(3,self,``);
                let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
                for(let i in res){
                    let damage = c.CountDamage(self,res[i],damageValue2,"",false,this,null);
                    a.GetHurt(self,res[i],damage,false,"",this,true,null);
                }
                for(let i in res){
                    let block = new StatusBlock(self,this,'',1,0);
                    block.Init(res[i],true,3);
                }
            }
            //
            if(combo1){
                let r = new Report();
                r.InitExecuteReport(3,self,``);
                let res = a.SelectTarget(self,realRange,false,true,1,false,true,this,"");
                for(let i in res){
                    let damage = c.CountDamage(self,res[i],damageValue1,"",false,this,null);
                    a.GetHurt(self,res[i],damage,false,"",this,true,null);
                }
                for(let i in res){
                    let block = new StatusBlock(self,this,'',1,0);
                    block.Init(res[i],true,3);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 132,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let value = q.GetAdditionValue(self,20,'',0.18);//
            let odd = 60;//
            if(self.driver.version == ''){
                value = q.GetAdditionValue(self,35,'',0.22);
                odd = 75;
            }
            
            let action = function(){
                let r = new Report();
                r.InitExecuteReport(2,self,``)
                let attrType = '';
                let rand = Math.floor(Math.random()*3);
                if(rand == 0) attrType = '';
                if(rand == 1) attrType = '';
                if(rand == 2) attrType = '';

                let low = 10000;
                let high = -10000;
                let lowest = null;
                let highest = null;
                let fri = a.SelectTarget(self,5,true,false,3,true,false,that,"");
                let en = a.SelectTarget(self,5,false,true,3,false,false,that,"");
                for(let i in fri){
                    let num = q.GetAttr(fri[i],attrType);
                    if(num > high){
                        high = num;
                        highest = fri[i];
                    }
                    else if(num == high){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1){
                            high = num;
                            highest = fri[i];
                        }
                    }

                }
                for(let i in en){
                    let num = q.GetAttr(en[i],attrType);
                    if(num < low){
                        low = num;
                        lowest = en[i];
                    }
                    else if(num == low){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1){
                            low = num;
                            lowest = en[i];
                        }
                    }
                }
                //
                if(lowest != null){
                    let block = new AttrBlock(self,that,attrType,-value,1,false);
                    block.Init(lowest,true,3);
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let type = "";
                        let typeRand = Math.floor(Math.random()*3);
                        switch(typeRand){
                            case 0 : type = '';break;
                            case 1 : type = '';break;
                            case 2 : type = '';break;
                        }
                        let block = new StatusBlock(self,that,type,1,0);
                        block.Init(lowest,true,3);
                    }
                }
                //
                if(highest != null){
                    let block = new AttrBlock(self,that,attrType,value,1,false);
                    block.Init(highest,true,3);
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let type = "";
                        let typeRand = Math.floor(Math.random()*3);
                        switch(typeRand){
                            case 0 : type = '';break;
                            case 1 : type = '';break;
                            case 2 : type = '';break;
                        }
                        let block = new StatusBlock(self,that,type,1,0);
                        block.Init(highest,true,3);
                    }
                }

            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self,false);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 136,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : ['',''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let u = new Util();
            let duration = 4;
            let odd = 90;//
            let value = q.GetAdditionValue(self,40,'',0.14);//

            let res = a.SelectTarget(self,5,true,false,3,true,false,this,"");
            for(let i in res){
                if(res[i].gender == ''){
                    let block = new AttrBlock(self,this,'',0,99,false,true);
                    let work = block.Init(res[i],false,2);
                    if(work){
                        let r = new Report();
                        r.InitExecuteReport(2,res[i],`()`);
                    }
                }
                else{
                    let block = new AttrBlock(self,this,'',0,99,false,true);
                    let work = block.Init(res[i],false,2);
                    if(work){
                        let r = new Report();
                        r.InitExecuteReport(2,res[i],`()`);
                    }
                }
                let block = new AttrBlock(self,this,'',0,99,false,true);
                let work = block.Init(res[i],false,2);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(2,res[i],`()`);
                }
            }
            let action = function(){
                if(self.driver.turn == 4){
                    for(let i in res){
                        for(let j in res[i].ATTR){
                            let tgt = res[i].ATTR[j];
                            if(tgt.tactics == that){
                                tgt.value += value;
                                let r = new Report();
                                r.InitTwoTargetReport(2,self,res[i],``,`${tgt.attrType}${value}(${q.GetAttr(res[i],`${tgt.attrType}`)})`);
                            }
                        }
                    }
                }
                else{
                    let realOdd = q.GetMoraleOdd(self,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let r = new Report();
                        r.InitExecuteReport(2,self,``);
                        u.ClearStatus(self,that,true,3);
                        u.ClearStatus(self,that,false,3);
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(2,self,`${realOdd}%`);
                    }
                }
                
            }
            let block = new ExecuteBlock(self,this,'',action,duration);
            block.Init(self,false);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 137,
        name : "",
        type : "",
        odd : 100,
        range : 2,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let cureValue = 200;
            let cureIndex = 1.75;
            let time = 3;//
            let attrValue = 30;//

            let tag = '' + self.belong + self.position;
            let res = a.SelectTarget(self,2,true,false,2,false,false,this,"");
            let getHurt = function(){
                let tgt = this.beExecuted;
                if(tgt.hp < q.GetMaxHp(tgt)*0.5 && self[tag] < time){
                    let cure = c.CountCure(self,tgt,cureValue,cureIndex,that);
                    let r = new Report();
                    r.InitTwoTargetReport(3,self,tgt,``,`${cure}(${tgt.hp + cure})`,false);
                    a.GetCure(self,tgt,cure,that,true,r);
                    let block1 = new AttrBlock(self,that,'',attrValue,99,true,true);
                    let work1 = block1.Init(tgt,false,3);
                    if(work1){
                        block1.InitTwoTargetReport(that,'',self,tgt,true,3);
                    }
                    let block2 = new AttrBlock(self,that,'',attrValue,99,true,true);
                    let work2 = block2.Init(tgt,false,3);
                    if(work2){
                        block2.InitTwoTargetReport(that,'',self,tgt,true,3);
                    }
                    if(self.alive){
                        let block1 = new AttrBlock(self,that,'',-attrValue,99,true,true);
                        let work1 = block1.Init(self,false,3);
                        if(work1){
                            block1.InitTwoTargetReport(that,'',self,self,false,3);
                        }
                        let block2 = new AttrBlock(self,that,'',-attrValue,99,true,true);
                        let work2 = block2.Init(self,false,3);
                        if(work2){
                            block2.InitTwoTargetReport(that,'',self,self,false,3);
                        }
                    }
                    self[tag] += 1;
                }
            }
            for(let i in res){
                let block = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
                block.Init(res[i],'');
            }
            self[tag] = 0;//

            
            this.STATISTICS.times += 1;
        }
    },
]

export default hanAArr;