        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let u = new Util();
            let value = q.GetAdditionValue(self,36,'',0.18);
            let damageValue = 55;

            let tag = '' + self.belong + self.position;
            let realRange = q.GetTacticsRange(self,this);
            let all = a.SelectTarget(self,realRange,true,false,3,true,true,this);
            let temp = [];
            for(let i in all){
                let arms = u.TransferArms(all[i].upArms[all[i].choseUpArmsIdx]);
                if(arms == 'q') temp.push(all[i]);
            }
            let res = [];
            for(let i=0;i<2 && temp.length > 0;i++){
                let rand = Math.floor(Math.random()*temp.length);
                res.push(temp[rand]);
                temp.splice(rand,1);
            }
            for(let i in res){
                let block = new DamageBlock(self,this,'',true,value,false,false,2,0);
                block.Init(res[i],null,true,3);
            }
            let attack = function(){
                let tgt = this.beExecuted;
                if(tgt[tag] > 0){
                    tgt[tag]--;
                }
                else if(tgt[tag] == 0){
                    //
                    if(tgt[tag] < 0){
                        let arr = [];
                        for(let i in tgt.COUNTER_ATTACK){
                            if(tgt.COUNTER_ATTACK[i].tactics == that && tgt.COUNTER_ATTACK[i].type == ''){
                                let r = new Report();
                                r.InitExecuteReport(3,tgt,``);
                            }
                            else arr.push(tgt.COUNTER_ATTACK[i]);
                        }
                        tgt.COUNTER_ATTACK = arr;
                    }
                }
            }
            for(let i in res){
                let block = new CounterAttackBlock(self,this,'',damageValue,100,2,0);
                let work = block.Init(res[i],false,3);
                if(work){
                    let block = new AttackExecuteBlock(self,this,attack,2,0);
                    block.Init(res[i],false);
                    res[i][tag] = 2;
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
            }
            this.STATISTICS.times += 1;
        }
