    {
        id: 150,
        name: "",
        type: "",
        odd: 100,
        range: 5,
        isDamageMaker: false,
        damageMakeType: [],
        isControlMaker: false,
        isCureMaker: true,
        selectFri: true,
        selectSelf: true,
        selectEnemy: false,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {
            let q = new Query();
            let a = new Attack();
            let c = new Count()
            let maxTime = 9;
            let value = 5;
            let cureRate = 50 + Math.floor((q.GetAttr(self, '') - 80) * 0.11);
            if (cureRate > 100) cureRate = 100;

            let tag = '' + self.belong + self.position;
            self[tag] = 0;

            let that = this;
            let moralDown = function () {
                if (self[tag] < maxTime && this.damageToWho.alive) {
                    self[tag] += 1;
                    this.damageToWho.morale -= value;
                    let r = new Report();
                    r.InitTwoTargetReport(3, self, this.damageToWho, ``, `${value}(${this.damageToWho.morale})(${9 - self[tag]})`);
                }
            }
            let makeCure = function () {
                if (this.damageType == '') {
                    let cure = c.CountCure(self, this.beExecuted, 0, 0, that, Math.floor(this.damageValue * cureRate / 100));
                    a.GetCure(self, this.beExecuted, cure, that, false, null);
                    let report = new Report();
                    report.InitTwoTargetReport(3, self, this.beExecuted, ``, `${cure}(${cureRate}%)(${this.beExecuted.hp})`);
                }
            }
            for (let i in self.ally) {
                let block1 = new MakeDamageBlock(self, this, moralDown, 99);
                block1.Init(self.ally[i], this.name);
                let block2 = new MakeDamageBlock(self, this, makeCure, 99);
                block2.Init(self.ally[i], this.name);
            }

            let r = new Report();
            r.InitExecuteReport(2, self, ``);

            this.STATISTICS.times += 1;
        }
    },
    {
        id: 151,
        name: "",
        type: "",
        odd: 100,
        range: 5,
        isDamageMaker: true,
        damageMakeType: [""],
        isControlMaker: false,
        isCureMaker: true,
        selectFri: true,
        selectSelf: true,
        selectEnemy: false,
        targetNumber: 3,
        arms: [''],
        wait: 0,
        execute(self) {
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let maxTime = 15; //
            let upValue = 10 + Math.floor((q.GetAttr(self, '') - 80) * 0.05);;
            let damageValue = q.GetAdditionValue(self, 100, '', 1.2);//
            let value = 10 + Math.floor((q.GetAttr(self, '') - 80) * 0.06);

            let tag = '' + self.belong + self.position;

            let that = this;
            let makeDamage = function () {
                if (self.driver.turn == 6 || self.driver.turn == 8) {
                    let r = new Report();
                    r.InitExecuteReport(2, this.beExecuted, ``);
                    let realRange = q.GetTacticsRange(this.beExecuted, that);
                    let realTargetNumber = q.GetRealTargetNumber(1, this.beExecuted, that);
                    let res = a.SelectTarget(this.beExecuted, realRange, false, true, realTargetNumber, false, true, that,"");
                    for (let i in res) {
                        let finalDamageValue = this.beExecuted[tag] * upValue / 100 * damageValue;
                        let damage = c.CountDamage(this.beExecuted, res[i], finalDamageValue, "", false, that, null);
                        a.GetHurt(this.beExecuted, res[i], damage, false, '', that, true);
                    }
                }
            }
            let getCure = function () {
                if (this.beExecuted[tag] < maxTime) {
                    this.beExecuted[tag] += 1;
                    let r = new Report();
                    r.InitExecuteReport(3, this.beExecuted, `${this.beExecuted[tag] * upValue}%`)
                }
            }
            for (let i in self.ally) {
                let block = new CureVarBlock(self, this, value, false, false, 99, 0, null);
                let work = block.Init(self.ally[i]);
                if (work) {
                    let r = new Report();
                    r.InitExecuteReport(2, self.ally[i], `${value}%`);
                }
            }
            for (let i in self.ally) {
                let block = new ExecuteBlock(self, this, "", makeDamage, 99);
                let work = block.Init(self.ally[i], true);
                if (work) {
                    let block = new GetCureExecuteBlock(self, this, getCure, 99);
                    block.Init(self.ally[i]);
                    self.ally[i][tag] = 0;
                    let r = new Report();
                    r.InitExecuteReport(2, self.ally[i], ``);
                }
            }


            this.STATISTICS.times += 1;
        }
    }
