import CureStatusBlock from "../block/cureStatus";
import DamageBlock from "../block/damage";
import DamageStatus from "../block/damageStatus";
import DodgeBlock from "../block/dodge";
import ExecuteBlock from "../block/execute";
import AfterDamageBlock from "../block/execute1";
import ActiveTacticsBlock from "../block/execute3";
import FallIntoContinueDamageExecuteBlock from "../block/fallIntoCtnDmgExec";
import GetContinueDamageExecuteBlock from "../block/getCtnDmgExec";
import StatusBlock from "../block/status";
import Attack from "../js/attack";
import Count from "../js/count";
import Judge from "../js/judge";
import Query from "../js/query";
import Report from "../js/report";
import Util from "../js/util";

let wuSArr = [
    //
    {
        id : 3,
        name : "",
        type : "",
        odd : 35,
        range : 3,
        isDamageMaker : false,
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let u = new Util();
            let q = new Query();
            let a = new Attack();
            let realRange = q.GetRealTacticsOdd(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,true,false,realTargetNumber,true,true,this);
            for(let i in res){
                let count = 0
                count += u.ClearWhichTypeTacticsInitStatus(self,res[i],'',false,this,true,1,3);
                count += u.ClearWhichTypeTacticsInitStatus(self,res[i],'',false,this,true,1,3);
                if(count==0){
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }
                
            }
            for(let i in res){
                for(let j=0;j<2;j++){
                    let block = new DodgeBlock(self,this,99);
                    block.Init(res[i]);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 9,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : true,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let damageIndex = q.GetAdditionValue(self,215,'',2.25);

            let realRange = q.GetRealTacticsOdd(self,this);
            //
            let realTgtNum = q.GetRealTargetNumber(2,self,this);
            let res1 = a.SelectTarget(self,realRange,false,true,realTgtNum,false,true,this);
            for(let i in res1){
                let block = new StatusBlock(self,this,"",2);
                let work = block.Init(res1[i],false);
                if(work){
                    let report = new Report();
                    report.InitExecuteReport(2,res1[i],`()`)
                }
            }
            //
            let realTargetNumber2 = q.GetRealTargetNumber(3,self,this);
            let res2 = a.SelectTarget(self,realRange,false,true,realTargetNumber2,false,true,this);
            let block = new StatusBlock(self,this,'',1,0);
            block.Init(self,false);

            for(let i in res2){
                let damage = c.CountDamage(self,res2[i],damageIndex,"",false,this,null);
                let action = function(){
                    if(self.driver.turn == 3 && this.beExecuted.alive){
                        let block = new StatusBlock(self,this.tactics,"",0);
                        block.Init(self,false);
                        let realDamage = damage
                        if(this.beExecuted.hp < damage){
                            realDamage = this.beExecuted.hp
                        }
                        let report = new Report().InitTwoTargetReport(2,self,res2[i],``,`${realDamage}(${res2[i].hp - realDamage})`,false)
                        a.GetHurt(self,res2[i],realDamage,false,"",this.tactics,false,report);
                        let arr = [];
                        for(let i in self.STATUS){
                            if(self.STATUS[i].tactics != this.tactics) arr.push(self.STATUS[i]);
                        }
                        self.STATUS = arr;
                    }
                }
                let execute = new ExecuteBlock(self,this,"",action,3);
                execute.Init(res2[i],true);
                let report = new Report();
                report.InitExecuteReport(2,res2[i],`()`)
            }
            let arr = [];
            for(let i in self.STATUS){
                if(self.STATUS[i].tactics != this.tactics) arr.push(self.STATUS[i]);
            }
            self.STATUS = arr;
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 46,
        name : "",
        type : "",
        odd : 40,
        range : 3,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 1,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let value = 111 + Math.floor((q.GetAttr(self,"")-80)* 0.95);
            let value2 = 221 + Math.floor((q.GetAttr(self,"")-80)* 2.45);
            let realRange = q.GetRealTacticsOdd(self,this);
            let realTargetNumber = q.GetRealTargetNumber(this.targetNumber,self,this);
            let res = a.SelectTarget(self,realRange,false,true,realTargetNumber,false,true,this);
            let action = function(){
                if(that == this.damageFromTactics){
                    return;
                }
                let target = this.beExecuted;
                //
                let isWork = false;
                for(let i in target.SPECIAL_STATUS){
                    if(target.SPECIAL_STATUS[i].type == '' && target.SPECIAL_STATUS[i].tactics == that) isWork = true;
                }
                if(isWork){
                    let damage = c.CountDamage(self,target,value2,"",false,that,"");
                    let report = new Report().InitTwoTargetReport(3,self,target,``,`${damage}(${target.hp-damage})`)
                    a.GetHurt(self,target,damage,false,"",that,false,report);


                    //
                    let temp = [];
                    for(let i in target.AFTER_DAMAGE){
                        if(target.AFTER_DAMAGE[i].tactics != that) temp.push(target.AFTER_DAMAGE[i]);
                        else{
                            let r = new Report();
                            r.InitExecuteReport(3,target,``)
                        }
                    }
                    target.AFTER_DAMAGE = temp;
                    let arr = [];
                    for(let i in target.SPECIAL_STATUS){
                        if(target.SPECIAL_STATUS[i].tactics != that) arr.push(target.SPECIAL_STATUS[i]);
                    }
                    target.SPECIAL_STATUS = arr;
                }
                
            }
            for(let i in res){
                let damage = c.CountDamage(self,res[i],value,"",false,this,null);
                a.GetHurt(self,res[i],damage,false,"",this,true,null);
                //
                let target = res[i];
                let temp = [];
                for(let j in target.AFTER_DAMAGE){
                    if(target.AFTER_DAMAGE[j].tactics != that) temp.push(target.AFTER_DAMAGE[j]);
                    else{
                        //
                        let isWork = false;
                        let arr = [];
                        for(let k in target.SPECIAL_STATUS){
                            if(target.SPECIAL_STATUS[k].type == '' && target.SPECIAL_STATUS[k].tactics == that){
                                isWork = true;
                            }
                            else arr.push(target.SPECIAL_STATUS[k])
                        }
                        target.SPECIAL_STATUS = arr;
                        if(isWork){ 
                            let damage = c.CountDamage(self,target,value2,"",false,that,"");
                            let report = new Report().InitTwoTargetReport(3,self,target,``,`${damage}(${target.hp-damage})`)
                            a.GetHurt(self,target,damage,false,"",that,false,report)
                            let r = new Report();
                            r.InitExecuteReport(3,target,``)
                        }

                    }
                }
                target.AFTER_DAMAGE = temp;
                
            }
            for(let i in res){
                let block = new DamageStatus(self,this,'',value2,'',99,0,false);
                let work =  block.Init(res[i],false);
                if(work){
                    let block = new AfterDamageBlock(self,this,action,99,true,true,true,true,true,true,true);
                    block.Init(res[i],"");
                    let r = new Report();
                    r.InitExecuteReport(3,res[i],``);
                }

            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 64,
        name : "",
        type : "",
        odd : 100,
        range : 4,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let q = new Query();
            let a = new Attack();
            let c = new Count();
            let odd = 50;
            let damageValue1 = 150;//
            let damageValue2 = 150;//
            let cureValue = 175;
            let cureIndex = 0;

            
            
            let action = function(){
                //
                let highestAd = self;
                let highestTact = self;
                let maxAd = q.GetAttr(self,'');
                let maxTact = q.GetAttr(self,'');
                for(let i=0;i<3;i++){
                    if(self.ally[i].alive && q.GetAttr(self.ally[i],'') > maxAd){
                        highestAd = self.ally[i];
                        maxAd = q.GetAttr(self.ally[i],'');
                    }
                    if(self.ally[i].alive && q.GetAttr(self.ally[i],'') > maxTact){
                        highestTact = self.ally[i];
                        maxTact = q.GetAttr(self.ally[i],'');
                    }
                }
                let action1 = function(){
                    let realOdd = q.GetMoraleOdd(highestAd,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let r = new Report();
                        r.InitExecuteReport(2,highestAd,``);
                        //
                        let res = a.SelectTarget(highestAd,4,false,true,1,false,true,that);
                        for(let i in res){
                            let damage = c.CountDamage(highestAd,res[i],damageValue1,'',false,that,null);
                            a.GetHurt(highestAd,res[i],damage,false,'',that,true,null);
                        }
                        //
                        let cure = c.CountCure(highestAd,highestAd,cureValue,cureIndex,that);
                        a.GetCure(highestAd,highestAd,cure,that,true);
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(2,highestAd,`${realOdd}%`);
                    }
                }
                let block1 = new ExecuteBlock(self,that,'',action1,1);
                block1.Init(highestAd);
                let action2 = function(){
                    let realOdd = q.GetMoraleOdd(highestTact,odd);
                    let rand = Math.floor(Math.random()*100);
                    if(rand < realOdd){
                        let r = new Report();
                        r.InitExecuteReport(2,highestTact,``);
                        //
                        let res = a.SelectTarget(highestTact,4,false,true,1,false,true,that);
                        let realDamageValue = damageValue2 + Math.floor((q.GetAttr(highestTact,'')-80)*1.25);
                        for(let i in res){
                            let damage = c.CountDamage(highestTact,res[i],realDamageValue,'',false,that,null);
                            a.GetHurt(highestTact,res[i],damage,false,'',that,true,null);
                        }
                        //
                        let cure = c.CountCure(highestTact,highestTact,cureValue,cureIndex,that);
                        a.GetCure(highestTact,highestTact,cure,that,true);
                    }
                    else{
                        let r = new Report();
                        r.InitExecuteReport(2,highestTact,`${realOdd}%`);
                    }
                }
                let block2 = new ExecuteBlock(self,that,'',action2,1);
                block2.Init(highestTact);
                let r1 = new Report();
                r1.InitExecuteReport(1,highestAd,``);
                let r2 = new Report();
                r2.InitExecuteReport(1,highestTact,``);

            }
            let block = new ExecuteBlock(self,this,'',action,99);
            block.Init(self,true);
            let r = new Report();
            r.InitExecuteReport(2,self,``);
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 94,
        name : "",
        type : "",
        odd : 120,
        range : 1,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 1,
        arms : [''],
        wait : 0,
        execute(self,target){
            let a = new Attack();
            let c = new Count();
            let q = new Query();
            let damageValue = q.GetAdditionValue(self,150,'',1.7);//
            let damageValue2 = q.GetAdditionValue(self,270,'',2.1);//
            let value = 10;//
            
            //
            let time = 0;
            let temp  = [];
            let tag = true;
            let isHigher = false;
            for(let i in target.SPECIAL_STATUS){
                if(target.SPECIAL_STATUS[i].tactics == this && target.SPECIAL_STATUS[i].duration > 0){
                    time = target.SPECIAL_STATUS[i].duration;
                    let r = new Report();
                    r.InitExecuteReport(3,target,``);
                    tag = false;
                    isHigher = target.SPECIAL_STATUS[i]['higher'];
                }
                else if(target.SPECIAL_STATUS[i].tactics != this) temp.push(target.SPECIAL_STATUS[i]);
            }
            //
            for(let i=0;i<time;i++){
                if(isHigher){
                    let damage = c.CountDamage(self,target,damageValue2,'',false,this,'');
                    a.GetHurt(self,target,damage,false,'',this,true,null);
                }
                else{
                    let damage = c.CountDamage(self,target,damageValue,'',false,this,'');
                    a.GetHurt(self,target,damage,false,'',this,true,null);
                }
            }
            target.SPECIAL_STATUS = temp;
            //
            if(tag){
                let block = new DamageStatus(self,this,'',damageValue,'',2,0,true);
                block['higher'] = false;
                block.Init(target,true);
            }
            else{
                let block = new DamageStatus(self,this,'',damageValue2,'',1,0,true);
                block['higher'] = true;
                let work =  block.Init(target,false);
                if(work){
                    let r = new Report();
                    r.InitExecuteReport(3,target,``);
                }
            }
            //
            if(!tag){
                let res = a.GetDivideForceTarget(target);
                res.push(target);
                for(let i in res){
                    if(res[i].morale > 0){
                        res[i].morale -= value;
                        let r = new Report();
                        r.InitExecuteReport(3,res[i],`${value}(${res[i].morale})`);
                    }
                }   
            }
            
            
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 96,
        name : "",
        type : "",
        odd : 35,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : true,
        selectFri : true,
        selectSelf : true,
        selectEnemy : true,
        targetNumber : 2,
        arms : [''],
        wait : 0,
        execute(self){
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            let damageValue1 = q.GetAdditionValue(self,176,'',1.95);//
            let damageValue2 = q.GetAdditionValue(self,86,'',0.825);//
            let cureValue1 = 161;//
            let cureIndex1 = 1.325;
            let cureValue2 = 82;//
            let cureIndex2 = 0.65;
            
            let realRange = q.GetTacticsRange(self,this);
            let res = a.SelectTarget(self,realRange,false,true,2,false,true,this);
            let fri = a.SelectTarget(self,realRange,true,false,2,true,true,this);
            for(let i in res){
                let damage = c.CountDamage(self,res[i],damageValue1,'',false,this,null);
                a.GetHurt(self,res[i],damage,false,'',this,true,null);
            }
            //
            for(let i in res){
                let maxHp = q.GetMaxHp(res[i]);
                if(res[i].hp > maxHp/2){
                    let block = new DamageStatus(self,this,'',damageValue2,'',1,0,true);
                    block.Init(res[i],true);
                }
            }
            //
            for(let i in fri){
                let cure = c.CountCure(self,fri[i],cureValue1,cureIndex1,this);
                a.GetCure(self,fri[i],cure,this,true);
            }
            //
            for(let i in fri){
                let maxHp = q.GetMaxHp(fri[i]);
                if(fri[i].hp < maxHp/2){
                    let block = new CureStatusBlock(self,this,'',cureValue2,cureIndex2,1,0,100);
                    block.Init(fri[i],true);
                }
            }
            this.STATISTICS.times += 1;
        }
    },
    
    //
    {
        id : 131,
        name : "",
        type : "",
        odd : 100,
        range : 3,
        isDamageMaker : false,
        damageMakeType : [],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : true,
        selectSelf : true,
        selectEnemy : false,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            //
            let value1 = Number(((q.GetAttr(self,'')-80)*0.01 + 2).toFixed(1));
            //
            let value2 = Number(((q.GetAttr(self,'')-80)*0.01 + 2).toFixed(1));
            //
            let maxTime = 8;

            let tag1 = '' + self.belong + self.position + 1;
            let tag2 = '' + self.belong + self.position + 2;
            let res = a.SelectTarget(self,3,true,false,3,true,false,this);
            let getHurt = function(){
                let target = this.beExecuted;
                let tgtArr = a.GetDivideForceTarget(target);
                for(let i in tgtArr){
                    //
                    if(tgtArr[i][tag1] < maxTime){
                        for(let j in tgtArr[i].DAMAGE_VAR){
                            if(tgtArr[i].DAMAGE_VAR[j].tactics == that && !tgtArr[i].DAMAGE_VAR[j].isMakeDamage){
                                tgtArr[i].DAMAGE_VAR[j].value -= value1;
                                tgtArr[i].DAMAGE_VAR[j].value = Number(tgtArr[i].DAMAGE_VAR[j].value.toFixed(1));
                                let r = new Report();
                                r.InitTwoTargetReport(3,self,tgtArr[i],``,`${tgtArr[i].DAMAGE_VAR[j].damageType}${Math.abs(tgtArr[i].DAMAGE_VAR[j].value)}%`);
                            }
                        }
                        tgtArr[i][tag1]+=1;
                    }
                    //
                    if(tgtArr[i][tag2] < maxTime){
                        for(let j in tgtArr[i].DAMAGE_VAR){
                            if(tgtArr[i].DAMAGE_VAR[j].tactics == that && tgtArr[i].DAMAGE_VAR[j].isMakeDamage){
                                tgtArr[i].DAMAGE_VAR[j].value += value2;
                                tgtArr[i].DAMAGE_VAR[j].value = Number(tgtArr[i].DAMAGE_VAR[j].value.toFixed(1));
                                let r = new Report();
                                r.InitTwoTargetReport(3,self,tgtArr[i],``,`${tgtArr[i].DAMAGE_VAR[j].value}%`);
                            }
                        }
                        tgtArr[i][tag2]+=1;
                    }
                }
            }
            for(let i in res){
                let block = new AfterDamageBlock(self,this,getHurt,99,true,true,true,true,true,true,true);
                block.Init(res[i],'');
                res[i][tag1] = 0;
                res[i][tag2] = 0;
                let block1 = new DamageBlock(self,that,'',false,0,false,false,99,0);
                block1.isBenefit = true;
                block1.Init(res[i],null,true);
                let block2 = new DamageBlock(self,that,'',true,0,false,false,99,0);
                block2.isBenefit = true;
                block2.Init(res[i],null,false);
                let r = new Report();
                r.InitExecuteReport(2,res[i],``);
            }
            this.STATISTICS.times += 1;
        }
    },
    //
    {
        id : 134,
        name : "",
        type : "",
        odd : 100,
        range : 5,
        isDamageMaker : true,
        damageMakeType : [""],
        isControlMaker : false,
        isCureMaker : false,
        selectFri : false,
        selectSelf : false,
        selectEnemy : true,
        targetNumber : 3,
        arms : [''],
        wait : 0,
        execute(self){
            let that = this;
            let a = new Attack();
            let q = new Query();
            let c = new Count();
            //
            let odd = 50;
            //
            let damageValue = q.GetAdditionValue(self,140,'',1.25);
            
            for(let i in self.enemy){
                let tag = '' + self.enemy[i].belong + self.enemy[i].position;
                let action = function(){
                    let tgt = this.beExecuted;
                    if(!tgt[tag]){
                        let realOdd = q.GetMoraleOdd(self,odd);
                        let rand = Math.floor(Math.random()*100);
                        if(rand < realOdd){
                            let damage = c.CountDamage(self,tgt,damageValue,'',false,that,null);
                            let r = new Report().InitTwoTargetReport(3,self,tgt,'',`${damage}(${tgt.hp-damage})`)
                            a.GetHurt(self,tgt,damage,false,'',that,false,r,false);
                        }
                        else{
                            let r = new Report();
                            r.InitExecuteReport(3,self,`${realOdd}%`);
                        }
                        tgt[tag] = 1;
                    }
                }
                let fallInto = function(){
                    if(self.driver.turn >= 3){
                        let tgt = this.beExecuted;
                        let newDmgValue = this.damageValue;//
                        let newDmgType = this.damageType;//
                        let newType = this.type;
                        let dmgOrigin = this.originWarrior;//;
                        let damage = c.CountDamage(dmgOrigin,tgt,newDmgValue,newDmgType,false,that,newType);
                        let r = new Report().InitTwoTargetReport(3,self,tgt,'',`${damage}(${tgt.hp-damage})`)
                        a.GetHurt(self,tgt,damage,false,newDmgType,that,false,r,false);
                    }
                }
                let clearTag = function(){
                    self.enemy[i][tag] = 0;
                }
                let block = new GetContinueDamageExecuteBlock(self,this,action,99,true,true,true,true,true,true);
                block.Init(self.enemy[i]);
                let block1 = new ExecuteBlock(self,this,'',clearTag,99);
                block1.Init(self.enemy[i],false);
                let block2 = new FallIntoContinueDamageExecuteBlock(self,this,fallInto,99);
                block2.Init(self.enemy[i]);
                let r = new Report();
                r.InitExecuteReport(2,self.enemy[i],``);
            }
            this.STATISTICS.times += 1;
        }
    },
]

export default wuSArr;