import Report from "../js/report";
import Attack from "../js/attack";
import Manage from "../js/manage";
import Judge from "../js/judge";
//
class Warrior{
    constructor(data,position,belong,global,driver){
        this.driver = driver;
        this.global = global;
        this.belong = belong;
        this.position = position;
        this.red = data.red;
        this.sign = data.sign;
        //id
        this.id = data.id;
        //
        this.alive = true;
        //
        this.ally = null;
        //
        this.enemy = null;
        //
        this.action = true;
        //
        this.level = data.level;
        //
        this.gender = data.gender;
        //
        this.name = data.name;
        //
        this.camp = data.camp;
        //
        this.arms = data.arms;
        //
        this.unitType = "";
        //
        this.cost = data.cost;
        //
        this.ackRange = data.range;
        //
        this.hp = data.level * 100;
        this.hpMax = data.level * 100;
        this.wound = 0;
        //
        this.artifact = data.artifact;
        //
        this.morale = 100;
        //
        this.armsSpec = data.armsSpec
        this.armsCommonSpec = data.armsCommonSpec
        this.upArms = data.upArms;
        this.choseUpArmsIdx = data.choseUpArmsIdx;
        this.choseArmsCharacterIdx = data.choseArmsCharacterIdx
        //
        this.attr = {
            ad : Math.floor(data.attr.ad + data.grow.ad * (data.level - 1) + data.plus.ad),
            de : Math.floor(data.attr.de + data.grow.de * (data.level - 1) + data.plus.de),
            tact : Math.floor(data.attr.tact + data.grow.tact * (data.level - 1) + data.plus.tact),
            speed : Math.floor(data.attr.speed + data.grow.speed * (data.level - 1) + data.plus.speed),
        }
        //
        this.img = {
            body : data.img.body,
            preview : data.img.preview,
            head : data.img.head,
            banner : data.img.banner,
        }
        //
        this.TacticsArr = [];
        //
        this.WaitTacticsArr = [];
        //
        this.IGNORE = [];
        //()
        this.STATUS = [];
        //
        this.DAMAGE_VAR = [];
        //
        this.CURE_VAR = [];
        //
        this.STATUS_DAMAGE_VAR = []
        //
        this.RANGE = [];
        //
        this.ODD_VAR = [];
        //
        this.SPECIAL_STATUS = [];
        //
        this.CURE_STATUS = [];
        //
        this.ATTR = [];
        //
        this.DODGE = [];
        //
        this.EXECUTE = [];
        //
        this.BEFORE_MAKE_DAMAGE = [];
        //
        this.MAKE_DAMAGE = [];
        //
        this.BEFORE_GET_HURT = [];
        //
        this.AFTER_DAMAGE = [];
        //
        this.AFTER_CONTINUE_DAMAGE = [];
        //
        this.GET_CURE = [];
        //
        this.ACTIVE_TACTICS_EXECUTE = [];
        //
        this.LAUNCH_ATTACK_EXECUTE = [];
        
        //
        this.COUNTER_ATTACK = [];
        //*
        this.DISCOUNT = [];

        //
        this.FALL_INTO_CONTINUE_DAMAGE = [];
        //
        this.BEFORE_FALL_INTO_STATUS = [];
        this.FALL_INTO_STATUS = [];

        //
        this.BEFORE_APPLIED_ATTR_EXECUTE = [];
        //
        this.AFTER_APPLIED_ATTR_EXECUTE = [];
        //
        this.STATUS_RESIST = [];

        //
        this.BEFORE_TARGET_SELECT_EXECUTE = []
        this.AFTER_TARGET_SELECT_EXECUTE = [];

        //
        this.STATISTICS = {
            atk : 0,//
            tact : 0,//
            cure : 0,//
        }
        //  app  
        for (const key in data) {
            if (!this.hasOwnProperty(key)) {
                this[key] = data[key];
            }
        }
    }
    //   
    Execute(){
        let a = new Attack();
        let m = new Manage();
        let j = new Judge();

        let report = new Report();
        report.InitExecuteReport(1,this,` ${this.hp}`,true,'');
        // report.InitSysMsg(this.global.ReportHeap,"",` (${this.hp})`)
        //------------------------------------------------------------------
        m.ManageExecuteBlock(this,"");//
        m.ManageAttrAppliedBlock(this);
        m.ManageResistBlock(this);//
        m.ManageFallIntoStatusExecuteBlock(this);//
        m.ManageFallIntoDamageStatusExecuteBlock(this);//
        m.ManageRangBlock(this);//
        m.ManageOddBlock(this);//
        m.ManageDamageStatusBlock(this); //
        m.ManageCureStatusBlock(this); //
        m.ManageStatusBlock(this);//
        m.ManageAttrBlock(this);//
        m.ManageDamageVarBlock(this); //
        m.ManageDamageStatusVarBlock(this);
        m.ManageCureVarBlock(this);
        
        m.ManageIgnoreBlock(this);
        m.ManageCounterAttackBlock(this);//

        
        m.ManageBeforeMakeDamageBlock(this);//
        m.ManageAfterGetDamagedBlock(this);//
        m.ManageGetCureExecuteBlock(this);//
        m.ManageAttackExecuteBlock(this);//
        m.ManageTacticsExecutedBlock(this);//
        m.ManageExecuteBlock(this,"");//
        if(!this.alive){
            //
            this.action = false;
            return;
        }
        //
        let insight = j.IsGetStatus(this,"");
        //
        if(j.IsGetStatus(this,"") && !insight){
            let r = new Report();
            r.InitExecuteReport(2,this,``);
        }
        else if(j.IsGetStatus(this,"") && insight){
            let r = new Report();
            r.InitExecuteReport(2,this,``);
        }
        if(j.IsGetStatus(this,"") && !insight){
            let report = new Report();
            report.InitExecuteReport(2,this,"");
        }
        else{
            if(j.IsGetStatus(this,"") && insight){
                let r = new Report();
                r.InitExecuteReport(2,this,``);
            }
            //
            if(j.IsGetStatus(this,"") && !insight){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else if(j.IsGetStatus(this,"")){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else{
                if(j.IsGetStatus(this,"") && insight){
                    let r = new Report();
                    r.InitExecuteReport(2,this,``);
                }
                a.LaunchActiveTactics(this);
            } 
            //
            if(j.IsGetStatus(this,"")){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else if(j.IsGetStatus(this,"") && !insight && !j.IsGetStatus(this,"")){
                let report = new Report();
                report.InitExecuteReport(2,this,"");
            }
            else{
                if(j.IsGetStatus(this,"")){
                    if(insight){
                        let r = new Report();
                        r.InitExecuteReport(2,this,``);
                    }
                    else if(j.IsGetStatus(this,"")){
                        let r = new Report();
                        r.InitExecuteReport(2,this,``);
                    }
                }
                a.LaunchAttack(this);
                if(j.IsGetStatus(this,"")){
                    let report = new Report();
                    report.InitExecuteReport(2,this,"");
                    a.LaunchAttack(this);
                }
            }
        }
        
        //
        m.ManageExecuteBlock(this,"");//
        this.action = false;
        //
        this.wound = Math.floor(this.wound * 0.87);
    }
}

export default Warrior;