import { defineStore } from 'pinia'
// import { ref } from 'vue'

export const store = defineStore('app', {
    // 
    state: () => ({
        //appId
        DATABASE_NAME: 'AssistingBorder App',
        //
        device : "phone",
        //
        App: null,
        //
        user: null,
        //
        armsMap : {
            "":"b_j",
            "":"b_z",
            "":"b_c",
            "":"b_t",
            "":"b_m",
            "":'g_s',
            "":'g_n',
            "":'g_c',
            "":"q_z",
            "":"q_q",
            "":"q_t",
            "":"q_x",
            "":"q_g",
        },
        //
        warriors: [],
        //,json
        warriorOriginDataList: [],
        //
        warriorIdxTable: {},
        //
        tactics: {
            command: [],
            active: [],
            combo: [],
            passive: [],
        },
        //
        userArtifacts: [],
        //
        artifacts: [],
        //
        allWarriors:[],
        allTactics:{
            command: [],
            active: [],
            combo: [],
            passive: [],
        },
        //
        allArtifacts:[],
        //
        artifactOriginDataList: [],
        //
        teams: [],
        //
        buffer: {
            curWarrior: null,
            curTactics: null,
            curArtifact: null,
            curConfigArt:null,
            curTeam: null,
            curBattleTeam:null,

            editTactIdx: -1,
            editTeamIdx: -1,
            editTeamWarriorIdx: -1,

        },
        dirty: {
            warrior: false,
            tactics: false,

            reportHistory: false,
            battleLineup: false
        },
        //
        prompt: {
            title: "",
            type: '',
            msg: "",
            CallBack: function () { },//
            status: false
        },
        //
        promptOptional:{
            title: "",
            msg : "",
            optionBtnNames:[],
            //
            optionCallBack :[
            ],
            status:false
        },
        alertQueue: [],
        battleStorage: null,
        battle: {
            lineupList: [],//(battleStorage)
            visibleLineups : [],//
            currentGroup:null,//
            lineupGroups:[],//
            teams: [],
            setting:null,
            version: "",

            //
            battleResult: [
                [],
                []
            ],
            ReportHeap: [],//
            //
            Statistics: {
                allTurn: 0,
                //
                dataMatrices: [],
            },
            ReportContainer: [],//

        }
    }),

    //  Vuex  getters 
    getters: {
        doubleCount: (state) => state.count * 2
    },

    //  Vuex  actions  mutations
    actions: {
        OptionalPrompt(params){
            this.promptOptional.title = params.title || " "
            this.promptOptional.msg = params.msg || '';
            this.promptOptional.optionBtnNames = params.optionBtnNames || [];
            this.promptOptional.optionCallBack = params.optionCallBack || [];
            this.promptOptional.status = true;
        },
        Prompt(params) {
            this.prompt.title = params.title || ' ';
            this.prompt.msg = params.msg || '';
            this.prompt.type = params.type || 'info';
            this.prompt.status = true;
            this.prompt.CallBack = params.CallBack;
        },
        Alert(msg,duration=1500) {
            let alert_item = {
                id: Math.floor(Math.random() * 9999),
                msg: msg || "",
            }
            this.alertQueue.push(alert_item)
            setTimeout(() => {
                if (this.alertQueue.length > 0) {
                    this.alertQueue.shift();
                }
            }, duration)
        },
        AutoStyle(dom){
            dom.classList.add(this.device);
        },
        // WatchWarriors() {
        //     //  warriors 
        //     watch(
        //         () => this.warriors,
        //         (newWarriors) => {
        //             console.log(':', newWarriors)
        //             // 
        //             // localStorage.setItem('warriors', JSON.stringify(newWarriors))
        //         },
        //         { deep: true } // 
        //     )
        // },
        async fetchData() {
            const response = await fetch('/api/data')
            this.count = await response.json()
        }
    }
})

